import React, { useState, useEffect } from 'react';

// Custom Components
import Loader from '../../Components/Loader';
import TopNav from '../../Components/AppNav';
import Alert from '../../Components/Alert';
import Footer from '../../Components/Footer/index';
import Cognitofunctions from '../Cognito/CognitoFunctions';
import AddProduct from './AddProduct'
import ProductAPIs from './ProductAPIs';
import DataList from '../../Utils/DataList';
import apidomain from '../../Utils/ApiDomain';
import apipath from '../../Utils/Apipath';
import GitAPIs from '../Git/GitAPIs';

// Styles
import ContainerStyles from '../../assets/styles/ContainerStyles'
import TypographyStyles from '../../assets/styles/TypographySyles'

// Material UI
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

// Material Icons
import AddIcon from '@material-ui/icons/Add';
import RefreshIcon from '@material-ui/icons/Refresh';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';

// React Helmet
import Helmet from "react-helmet/es/Helmet";




const ServicesList = () => {
    const [productList, setProductList] = useState([])
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loadTimeout, setLoadTimeout] = useState(false)
    const [newProd,setNewProd] = useState(false)

    const containerClasses = ContainerStyles()
    const typographyClasses = TypographyStyles()


    useEffect(() => {
        // if (!window.gapi) {
        //     GoogleScript.CheckGoogleAPI()
        // }
        sessionStorage.removeItem('ServiceId')
        checkrole()
    }, [])

    const checkrole = () => {
        Cognitofunctions.CurrentUser(function (success, data) {
            if (success) {
                let gitapidata = {
                    path: apipath['datastore'] + "/git",
                    method: 'GET',
                    body: {},
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    ref: "GetGitCredentials"
                }
                manageAPIs(gitapidata)
            }
            else {
                // window.location.origin = window.location.origin + "/login"
                // window.location.origin = window.location.origin
                setMessage('You are not authorized to access this page.')
                setSeverrity('error')
                setOpenAlert(true)
                setLoadTimeout(true)
                setTimeout(function () {
                    window.location.href = window.location.origin + "/#/app/login"
                    window.location.reload()
                }, 1000)
            }
        })
    }

    const manageAPIs = (apidata) => {
        if(apidata.ref === "GetGitCredentials"){
                GitAPIs.GetGitDetails(apidata, function (success, response) {
                    if (success) {
                        if (Object.keys(response.data).length > 0) {
                            let nextapidata = {
                                method: "GET",
                                path: apipath['apipath']+"/product",
                                headers: {
                                    headers: true,
                                    apidomain: apidomain.vmurl
                                },
                                body: {},
                                ref: "GetAllProducts"
                            }
                            manageAPIs(nextapidata)
                        }
                        else {
                            window.location.href = window.location.origin + "/#/app/gitsetup"
                        }
                    }
                })
        }
        else if(apidata.ref === "GetAllProducts"){
            ProductAPIs.GetAllProducts(apidata, function (success, response) {
                if (success) {
                    if (response.hasOwnProperty('message')) {
                        setMessage(response.message)
                        if (response.error) {
                            setSeverrity('error')
                        }
                        else {
                            setSeverrity('success')
                        }
                        setOpenAlert(true)
                        setLoadTimeout(true)
                    }
                    if (response.data.length === 0) {
                        setLoadTimeout(true)
                        setNewProd(true)
                        setLoadTimeout(true)
                    }
                    else{
                        sessionStorage.setItem('Products',window.btoa(JSON.stringify(response.data)))
                        DataList.ProductList = response.data
                        setProductList(response.data)
                        if (response.data.length === 1) {
                            DataList.ActiveProduct = response.data[0]
                            sessionStorage.setItem('CurrentProduct',window.btoa(JSON.stringify(response.data[0])))
                            window.location.href = window.location.origin + "/#/app/products/" + response.data[0].productid+"/services"
                        }
                        else {
                            setLoadTimeout(true)
                        }
                    }
                }
                else {
                    setMessage(response.message)
                    setSeverrity('error')
                    setOpenAlert(true)
                    setLoadTimeout(true)
                }
            })
        }
    }

    const handleActions = (actionData, e) => {
        setProductList([])
        setLoadTimeout(false)
        e.preventDefault()
        if (actionData.Action === "refresh") {
            let apidata = {
                method: "GET",
                path: apipath['apipath']+"/product",
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                body: {},
                ref: "GetAllProducts"
            }
            manageAPIs(apidata)
        }
    }

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const CancelAddProduct = () =>{
        setNewProd(false)
    }

    return (
        <div>
            <Helmet>
                <title>Products</title>
            </Helmet>
            <TopNav />
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Container className={containerClasses.root}>
                <Box style={{ width: '100%' }} p={1} pl={2} pr={2}>
                    <Box display="flex" justifyContent="space-between" alignItems="center" style={{ width: '100%' }} mb={1}>
                        <Typography className={`${typographyClasses.h5} ${'text-design'} ${typographyClasses.bold}`}>Products</Typography>
                        <Box display="flex" alignItems="center">
                            <Box mr={1} display="flex">
                                <Button
                                    variant="text"
                                    color="primary"
                                    onClick={handleActions.bind(this, { "Action": "refresh" })}
                                >
                                    <RefreshIcon className={typographyClasses.text}/>
                                </Button>
                            </Box>
                            <Box display="flex">
                                <Box>
                                    <Button
                                        className="trigger-btn"
                                        variant="contained"
                                        color="primary"
                                        onClick={() => setNewProd(true)}
                                    >
                                        <AddIcon className={typographyClasses.text}/>&nbsp;&nbsp;Create Product
                                    </Button>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                    <Box display="flex" flexWrap="wrap" justifyContent="flexStart"  >
                        {
                            productList.length > 0 ?
                                <Grid container>
                                    {
                                        productList.map((prod, index) => {
                                            let des = prod.hasOwnProperty('description') ? (prod.description.length > 50 ? prod.description.slice(0, 50) : prod.description) :''
                                            return (
                                                <Grid item xs={12} sm={6} md={3} lg={2} style={{ padding: '3px' }} key={index}>
                                                    <Box boxShadow={2} display="flex" flexDirection="column" alignItems="flex-start" p={2} bgcolor="white" style={{ height: '100%' }} minHeight={150}>
                                                        <Box display="flex" flexDirection="column" justifyContent="space-between" style={{ width: '100%', height: '100%' }}>
                                                            <Box>
                                                                <Box mb={1} color="#525151">
                                                                    <Typography className={`${typographyClasses.bold} ${typographyClasses.h6}`} align="left">{prod.productname}</Typography>
                                                                </Box>
                                                                <Box mb={1} color="#525151">
                                                                    <Typography className={`${typographyClasses.text}`} align="left">{des}{prod.hasOwnProperty('description') ? (prod.description.length > 50 ? <span>&nbsp;...<span className={typographyClasses.smallText} onClick={() => {
                                                                        window.location.href = window.location.origin + "/#/app/products/" + prod.productid + "/services"
                                                                    }} style={{ color: 'blue', cursor: 'pointer' }}>Show More</span></span> : ""):""}</Typography>
                                                                </Box>
                                                                <Box mb={1} color="#525151">
                                                                    <Typography className={`${typographyClasses.smallText}`} align="left">{prod.productid}</Typography>
                                                                </Box>
                                                            </Box>
                                                            <Box>
                                                                <Box display="flex" justifyContent="flex-end" style={{ width: '100%' }}>
                                                                    <Button
                                                                        className="trigger-btn"
                                                                        variant="contained"
                                                                        color="primary"
                                                                        onClick={() => {
																			DataList.ActiveProduct = prod
                                                                            sessionStorage.setItem('CurrentProduct',window.btoa(JSON.stringify(prod)))
                                                                            window.location.href = window.location.origin + "/#/app/products/" + prod.productid + "/service_templates"
                                                                        }}
                                                                    >
                                                                        <ArrowForwardIcon className={typographyClasses.text}/>
                                                                    </Button>
                                                                </Box>
                                                            </Box>
                                                        </Box>
                                                    </Box>
                                                </Grid>
                                            )
                                        })
                                    }
                                </Grid>
                                :
                                <Box>
                                    {
                                        loadTimeout === false ?
                                            <Loader type="block" />
                                            :
                                            <Typography className={`${typographyClasses.bold} ${typographyClasses.text}`} align="center">No Products</Typography>

                                    }
                                </Box>
                        }
                    </Box>
                </Box>
            </Container>
            <Footer page="app" />
            {
                newProd === true ?
                    <AddProduct Handler={CancelAddProduct}/>
                    :
                    null
            }
        </div>
    );
}

export default ServicesList;