import React, { useState } from 'react'
import PropTypes from 'prop-types';

// Material UI
import Box from '@material-ui/core/Box';
import IconButton from '@material-ui/core/IconButton';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';

// Styles
import TypographyStyles from '../../assets/styles/TypographySyles'

// Material Icons
import FileCopyIcon from '@material-ui/icons/FileCopy';

//Ace Editor
import AceEditor from 'react-ace'
import "ace-builds/src-noconflict/theme-dreamweaver";
import "ace-builds/src-noconflict/theme-monokai";
import "ace-builds/src-noconflict/theme-clouds";
import "ace-builds/src-noconflict/theme-github";
import "ace-builds/src-noconflict/theme-eclipse";
import "ace-builds/src-noconflict/mode-sh";

const tabStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        backgroundColor: 'white !important'
    },
    header: {
        boxShadow: 'none',
        background: 'white',
        border: 'none',
    }
}));

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-auto-tabpanel-${index}`}
            aria-labelledby={`scrollable-auto-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box >
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function TabProps(name, index) {
    return {
        id: `${name}-auto-tab-${index}`,
        'aria-controls': `${name}-auto-tabpanel-${index}`,
    };
}

const Actions = ({ actions }) => {
    const [tab, setTab] = useState(0)

    const tabClasses = tabStyles();
    const typographyClasses = TypographyStyles()

    function copyText(key) {
        let cptext = key.split("").join('\n')
        var tempIp = document.createElement('textarea')
        tempIp.value = cptext
        tempIp.style.position = 'fixed'
        tempIp.setAttribute('id', 'temp-copy')
        var root = document.getElementById('root')
        root.appendChild(tempIp)
        let copytext = document.getElementById('temp-copy')
        copytext.select()
        document.execCommand("copy");
        tempIp.remove()
    }

    const handleActionsTab = (event, newValue) => {
        setTab(newValue)
    }

    return (
        <Box style={{ width: '100%' }}>
            {
                actions.hasOwnProperty('initscript') === true ?
                    <Box display="flex">
                        <Box style={{ borderColor: '#272822', width: '100%' }} border={4} bgcolor="#272822" borderRadius={0} p={1} pt={0}>
                            <Box display="flex" justifyContent="space-between" alignItems="center">
                                <Box display="flex" alignItems="center">
                                    <Box display="flex" width={10} boxShadow={6} height={10} style={{ borderRadius: '50%' }} bgcolor="green" mr={1}>
                                    </Box>
                                    <Box display="flex" width={10} boxShadow={6} height={10} style={{ borderRadius: '50%' }} bgcolor="yellow" mr={1}>
                                    </Box>
                                    <Box display="flex" width={10} boxShadow={6} height={10} style={{ borderRadius: '50%' }} bgcolor="red">
                                    </Box>
                                </Box>
                                <Box display="flex" color="white" style={{ cursor: 'pointer' }}>
                                    <IconButton
                                        color="inherit"
                                        onClick={() => {
                                            copyText(actions['initscript'])
                                        }}
                                    >
                                        <FileCopyIcon className={typographyClasses.text} />
                                    </IconButton>
                                </Box>
                            </Box>
                            <Box display="flex" color="white" overflow="auto" maxHeight={250}>
                                <AceEditor
                                    style={{ height: '100%', width: '100%' }}
                                    mode="sh"
                                    theme="monokai"
                                    setOptions={{
                                        showLineNumbers: false,
                                        showGutter: false,
                                        highlightActiveLine: false,
                                        highlightGutterLine: false,
                                        highlightSelectedWord: false,
                                        fontSize: 12,
                                        readOnly: true,
                                        useSoftTabs: true,
                                        tabSize: 4,
                                        showPrintMargin: false,
                                        wrap: true,
                                        maxLines: Infinity,
                                        dragEnabled: false,
                                    }}
                                    editorProps={{ $blockScrolling: true, $blockSelectEnabled: false, $enableMultiselect: false }}
                                    value={actions['initscript']}
                                />
                            </Box>
                        </Box>
                    </Box>
                    :
                    <div className={tabClasses.root}>
                        <AppBar position="static" color="default" className={tabClasses.header}>
                            <Tabs
                                value={tab}
                                onChange={handleActionsTab}
                                variant="scrollable"
                                scrollButtons="auto"
                                aria-label="Actions auto tabs example"
                            >
                                {
                                    Object.keys(actions).map((act, index) => {
                                        return (
                                            <Tab disableFocusRipple disableRipple disableTouchRipple key={index} label={<Typography className={typographyClasses.text}>{act}</Typography>} {...TabProps("Actions", index)} />
                                        )
                                    })
                                }
                            </Tabs>
                        </AppBar>
                        {
                            Object.keys(actions).map((act, index) => {
                                return (
                                    <TabPanel value={tab} index={index}>
                                        <Box>
                                            {
                                                actions[act].hasOwnProperty('script') === true ?
                                                    <Box display="flex">
                                                        <Box style={{ borderColor: '#272822', width: '100%' }} border={4} bgcolor="#272822" borderRadius={0} p={1} pt={0}>
                                                            <Box display="flex" justifyContent="space-between" alignItems="center">
                                                                <Box display="flex" alignItems="center">
                                                                    <Box display="flex" width={10} boxShadow={6} height={10} style={{ borderRadius: '50%' }} bgcolor="green" mr={1}>
                                                                    </Box>
                                                                    <Box display="flex" width={10} boxShadow={6} height={10} style={{ borderRadius: '50%' }} bgcolor="yellow" mr={1}>
                                                                    </Box>
                                                                    <Box display="flex" width={10} boxShadow={6} height={10} style={{ borderRadius: '50%' }} bgcolor="red">
                                                                    </Box>
                                                                </Box>
                                                                <Box display="flex" color="white" style={{ cursor: 'pointer' }}>
                                                                    <IconButton
                                                                        color="inherit"
                                                                        onClick={() => {
                                                                            copyText(actions[act]['script'])
                                                                        }}
                                                                    >
                                                                        <FileCopyIcon className={typographyClasses.text} />
                                                                    </IconButton>
                                                                </Box>
                                                            </Box>
                                                            <Box display="flex" color="white" overflow="auto" maxHeight={250}>
                                                                <AceEditor
                                                                    style={{ height: '100%', width: '100%' }}
                                                                    mode="sh"
                                                                    theme="monokai"
                                                                    setOptions={{
                                                                        showLineNumbers: false,
                                                                        showGutter: false,
                                                                        highlightActiveLine: false,
                                                                        highlightGutterLine: false,
                                                                        highlightSelectedWord: false,
                                                                        fontSize: 12,
                                                                        readOnly: true,
                                                                        useSoftTabs: true,
                                                                        tabSize: 4,
                                                                        showPrintMargin: false,
                                                                        wrap: true,
                                                                        maxLines: Infinity,
                                                                        dragEnabled: false,
                                                                    }}
                                                                    editorProps={{ $blockScrolling: true, $blockSelectEnabled: false, $enableMultiselect: false }}
                                                                    value={actions[act]['script']}
                                                                />
                                                            </Box>
                                                        </Box>
                                                    </Box>
                                                    :
                                                    null
                                            }
                                        </Box>
                                    </TabPanel>
                                )
                            })
                        }
                    </div>
            }
        </Box>
    )
}

export default Actions