import React, { useState } from 'react'

//Material UI
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl'

//Custom Component
import Loader from '../../Components/Loader';
import Alert from '../../Components/Alert'
import DataList from '../../Utils/DataList';
import ProductAPIs from './ProductAPIs';
import apidomain from '../../Utils/ApiDomain';
import apipath from '../../Utils/Apipath';


const AddProduct = ({ Handler }) => {
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loader, setLoader] = useState(false)
    const [dialog, setDialog] = useState(true)
    const [formData, setFormData] = useState({})
    const [prodname, setprodname] = useState('')

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const handleFormDataChage = (field, event) => {
        let formFields = formData;
        if (field === "productname") {
            const re = /^[a-zA-Z][a-zA-Z0-9_]*[a-zA-Z0-9]*$/;
            if (re.test(event.target.value)) {
                formFields[field] = event.target.value.toLowerCase();
                setprodname(event.target.value.toLowerCase())
            }
            else if (event.target.value === "") {
                formFields[field] = event.target.value;
                setprodname(event.target.value)
            }
        }
        else {
            formFields[field] = event.target.value;
        }
        setFormData(formFields);
    }

    const SubmitHandler = (key) => {
        if (!key) {
            Handler()
        }
        else {
            setLoader(true)
            let canSubmit = true, requireAttrs = ['productname']
            for (let i = 0; i < requireAttrs.length; i++) {
                if (formData[requireAttrs[i]] === undefined || formData[requireAttrs[i]] === "" || formData[requireAttrs[i]] === []) {
                    canSubmit = false
                    setLoader(false)
                    setMessage("Please fill the required details.")
                    setSeverrity('error')
                    setOpenAlert(true)
                }
            }
            if (canSubmit) {
                let apidata = {
                    method: "POST",
                    path: apipath['apipath'] + "/product",
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    body: formData,
                }
                ProductAPIs.AddProduct(apidata, function (success, response) {
                    if (success) {
                        if (response.hasOwnProperty('message') && response.message !== "") {
                            setMessage(response.message)
                            setSeverrity('info')
                            setOpenAlert(true)
                        }
                        let prods = DataList.ProductList.length > 0 ? DataList.ProductList : (sessionStorage.getItem('Products') ? JSON.parse(window.atob(sessionStorage.getItem('Products'))) : [])
                        prods.push(response.data)
                        DataList.ProductList = prods
                        sessionStorage.setItem('Products', window.btoa(JSON.stringify(prods)))
                        sessionStorage.setItem('CurrentProduct', window.btoa(JSON.stringify(response.data)))
                        DataList.ActiveProduct = response.data
                        sessionStorage.removeItem('Services')
                        sessionStorage.removeItem('CurrentService')
                        DataList.ServiceList = []
                        DataList.ActiveService = {}
                        setLoader(false)
                        setDialog(false)
                        window.setTimeout(function () {
                            Handler()
                            window.location.href = window.location.origin + "/#/app/products/" + response.data.productid + "/service_templates"
                            // window.location.reload()
                        }, 1000)
                    }
                })
            }
        }
    }

    return (
        <div>
            <Loader type="page" open={loader} />
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Dialog open={dialog} onClose={() => SubmitHandler(false)} aria-labelledby="form-dialog-title" maxWidth="sm" fullWidth>
                <DialogTitle id="form-dialog-title">Create Product</DialogTitle>
                <DialogContent>
                    <Box>
                        <form style={{}} autoComplete="off">
                            <Box mb={1}>
                                <FormControl fullWidth>
                                    <label htmlFor="ProductName" required>Product Name</label>
                                    <TextField
                                        id="ProductName"
                                        required
                                        fullWidth
                                        value={prodname}
                                        placeholder="Enter Product Name"
                                        onChange={handleFormDataChage.bind(this, "productname")}
                                        variant="outlined"
                                        helperText="No capital letters,special characters except underscore is allowed."
                                    >
                                    </TextField>
                                </FormControl>
                            </Box>
                            <Box>
                                <FormControl fullWidth>
                                    <label htmlFor="Description">Description</label>
                                    <TextField
                                        id="Description"
                                        required
                                        fullWidth
                                        placeholder="Enter Description"
                                        onChange={handleFormDataChage.bind(this, "description")}
                                        variant="outlined"
                                        multiline
                                        rows={3}
                                    >
                                    </TextField>
                                </FormControl>
                            </Box>
                        </form>
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => SubmitHandler(false)} color="secondary" variant="outlined">
                        Cancel
                    </Button>
                    <Button onClick={() => SubmitHandler(true)} color="primary" className="api-btn" variant="contained">
                        Create
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default AddProduct;