import React, { useState } from 'react';

//Material UI
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Popover from '@material-ui/core/Popover';
import Divider from '@material-ui/core/Divider';

//Custom Component
import DataList from '../../Utils/DataList';
import Loader from '../../Components/Loader';
import Alert from '../../Components/Alert'
import AddService from './AddService'
import Confirm from '../../Components/Confirm';
import ServiceAPIs from './ServiceAPIs';
import apipath from '../../Utils/Apipath';
import apidomain from '../../Utils/ApiDomain';
import GitAPIs from '../Git/GitAPIs';

//Material Icons
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';

// Styles
import TypographyStyles from '../../assets/styles/TypographySyles'


const ServiceIndex = () => {
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loader, setLoader] = useState(false)
    const [confirmation, setConfirmation] = useState('')
    const [confApiData, setConfApiData] = useState({})
    const [serviceList, setServiceList] = useState(DataList.ServiceList.length > 0 ? DataList.ServiceList : (sessionStorage.getItem('Services') ? JSON.parse(window.atob(sessionStorage.getItem('Services'))) : []))
    const [newServ, setNewServ] = useState(false)
    const [curServ, setCurServ] = useState(Object.keys(DataList.ActiveService).length > 0 ? DataList.ActiveService : (sessionStorage.getItem('CurrentService') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentService'))) : {}))
    const [anchorEl, setAnchorEl] = useState(null);
    const openPopover = Boolean(anchorEl);
    const id = openPopover ? 'simple-popover' : undefined;
    const [curProd, setCurProd] = useState(Object.keys(DataList.ActiveProduct).length > 0 ? DataList.ActiveProduct : (sessionStorage.getItem('CurrentProduct') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentProduct'))) : {}))

    const typographyClasses = TypographyStyles()

    const manageAPIs = (apidata) => {
        if (apidata.ref === "GetGitCredentials") {
            GitAPIs.GetGitDetails(apidata, function (success, response) {
                if (success) {
                    if (Object.keys(response.data).length > 0) {
                        window.location.href = window.location.origin + "/#/app/products/" + curProd.productid + "/service_templates"
                    }
                    else {
                        setMessage("Configure Git create a product")
                        setSeverrity('error')
                        setOpenAlert(true)
                    }
                }
            })
        }
        else if (apidata.ref === "DeleteService") {
            ServiceAPIs.DeleteService(apidata, function (success, response) {
                if (response.hasOwnProperty('message') && response.message !== "") {
                    setMessage(response.message)
                    setSeverrity('info')
                    setOpenAlert(true)
                }
                DataList.ActiveService = {}
                DataList.ServiceList = []
                sessionStorage.removeItem('Services')
                sessionStorage.removeItem('CurrentService')
                window.setTimeout(function () {
                    window.location.href = window.location.origin + "/#/app/products/" + curProd.productid + "/services"
                    window.location.reload()
                }, 1000)
            })
        }
    }

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const handleActions = (apidata) => {
        if (apidata.Action === 'redirect') {
            window.open(apidata.url, '_blank')
        }
        else if (apidata.Action === 'refresh') {
        }
        else if (apidata.Action === "confirmation") {
            setConfApiData(apidata['apidata'])
            setConfirmation(apidata.confirmText)
        }
        else {
            if (apidata.method === "DELETE") {
                setAnchorEl(null)
                setLoader(true)
            }
            manageAPIs(apidata)
        }
    }

    const handleUserClick = (event) => {
        setServiceList(DataList.ServiceList.length > 0 ? DataList.ServiceList : (sessionStorage.getItem('Services') ? JSON.parse(window.atob(sessionStorage.getItem('Services'))) : []))

        setCurServ(Object.keys(DataList.ActiveService).length > 0 ? DataList.ActiveService : (sessionStorage.getItem('CurrentService') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentService'))) : {}))

        setCurProd(Object.keys(DataList.ActiveProduct).length > 0 ? DataList.ActiveProduct : (sessionStorage.getItem('CurrentProduct') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentProduct'))) : {}))

        setAnchorEl(event.currentTarget);
    };

    const handlePopoverClose = () => {
        setAnchorEl(null);
    };

    const ChangeService = (service) => {
        setAnchorEl(null)
        if (curServ.serviceid !== service.serviceid) {
            setLoader(true)
            sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(service)))
            DataList.ActiveService = {}
        }
        window.location.href = window.location.origin + "/#/app/products/" + curProd.productid + "/services/" + service.serviceid
        window.location.reload()
    }

    const CancelAddService = () => {
        setNewServ(false)
    }

    const handleConfirmation = (key) => {
        setConfirmation('')
        if (key) {
            handleActions(confApiData)
            // console.log(confApiData)
            setConfApiData({})
        }
        else {
            setConfApiData({})
        }
    }

    return (
        <div>
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Loader open={loader} type="page" />
            <Box mr={2} display="flex" alignItems="center" style={{ height: '100%' }} className={`${"top-nav-link"}`} position="relative" onClick={handleUserClick}>
                <Box display="flex" alignItems="center" p={0.5} pl={1} pr={1} style={{ cursor: 'pointer' }} className="links">
                    <Box display="flex" alignItems="center" color="#6f7071" mr={0.5}>
                        <Typography className={typographyClasses.text} style={{ fontWeight: 'bold' }}>{Object.keys(curServ).length > 0 ? <span><span>Service&nbsp;-&nbsp;</span><span style={{ color: '#3F51B5' }}>{curServ.servicename}</span></span> : "Service"}</Typography>
                    </Box>
                    <Box color="gray" display="flex">
                        {
                            anchorEl === null ?
                                <ExpandMoreIcon fontSize="small" />
                                :
                                <ExpandLessIcon fontSize="small" />
                        }
                    </Box>
                </Box>
            </Box>
            <Popover
                id={id}
                open={openPopover}
                anchorEl={anchorEl}
                onClose={handlePopoverClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                <Box bgcolor="white" boxShadow={2} height={240} width={260}>
                    <Box height={200}>
                        {
                            serviceList.length > 0 ?
                                <Box style={{ width: '100%' }} height={200} overflow="auto">
                                    {
                                        serviceList.map((serv, index) => {
                                            return (
                                                <Box style={{ width: '100%', cursor: 'pointer' }} p={1} key={index} bgcolor={curServ.serviceid === serv.serviceid ? "#f5f5f5" : "white"} onClick={() => ChangeService(serv)}>
                                                    <Box color={curServ.serviceid === serv.serviceid ? "#3F51B5" : "gray"} display="flex" alignItems="center" justifyContent="space-between">
                                                        <Box display="flex" flexDirection="column" alignItems="flex-start">
                                                            <Typography className={typographyClasses.text} align="left" color="inherit">{serv.servicename}</Typography>
                                                            <Typography className={typographyClasses.smallText} align="left" color="inherit">{serv.serviceid}</Typography>
                                                        </Box>
                                                        <Box display="flex"
                                                            onClick={(event) => {
                                                                event.stopPropagation()
                                                                handleActions({
                                                                    Action: "confirmation",
                                                                    confirmText: "Are you sure you want to delete this service(" + serv.servicename + ")?",
                                                                    apidata: {
                                                                        method: "DELETE",
                                                                        path: apipath['apipath'] + "/service/" + curProd.productid + "/" + serv.serviceid,
                                                                        headers: {
                                                                            headers: true,
                                                                            apidomain: apidomain.vmurl
                                                                        },
                                                                        body: {},
                                                                        ref: "DeleteService"
                                                                    }
                                                                })
                                                            }}
                                                        >
                                                            <DeleteIcon className={typographyClasses.text} color="secondary" />
                                                        </Box>
                                                    </Box>
                                                </Box>
                                            )
                                        })
                                    }
                                </Box>
                                :
                                <Box p={1}>
                                    <Typography className={typographyClasses.smallText}>No Services</Typography>
                                </Box>
                        }
                    </Box>
                    <Divider />
                    <Box display="flex" alignItems="center" height={39}>
                        <Button
                            variant="text"
                            color="primary"
                            onClick={() => {
                                if (Object.keys(curProd).length > 0) {
                                    let gitapidata = {
                                        path: apipath['datastore'] + "/git",
                                        method: 'GET',
                                        body: {},
                                        headers: {
                                            headers: true,
                                            apidomain: apidomain.vmurl
                                        },
                                        ref: "GetGitCredentials"
                                    }
                                    manageAPIs(gitapidata)
                                }
                                else {
                                    setMessage('Choose a product to create a service.')
                                    setSeverrity('info')
                                    setOpenAlert(true)
                                }
                            }}
                        >
                            <AddIcon className={typographyClasses.smallText} />&nbsp;&nbsp;Create Service
                        </Button>
                    </Box>
                </Box>
            </Popover>
            {
                newServ === true ?
                    <AddService Handler={CancelAddService} />
                    :
                    null
            }
            {
                confirmation !== '' ?
                    <Confirm confirmText={confirmation} clickHandler={handleConfirmation} />
                    :
                    null
            }
        </div>
    )
}

export default ServiceIndex;