import HandleAPI from '../../Utils/HandleApi';


function GetGitDetails(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                asyncfunction(true, { data: result.body, message: result.hasOwnProperty('message') ? result.message : "" })
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

