function CheckGoogleAPI(key) {
    const ga = window.gapi;
    if (ga) {
        initGapi(ga, key)
    }
    else {
        createScript(key)
    }
}

function initGapi(g, key) {
    g.load('auth2', function () {
        g.auth2.init({
            client_id: '38263492319-j1p2qgsvt2cr412jjfb6hstdjkslg81e.apps.googleusercontent.com',
            // authorized scopes
            scope: 'profile email openid'
        });
        if (key === "signout") {
            window.gapi.auth2.getAuthInstance().signOut()
        }
    });
}

function createScript(key) {
    // load the Google SDK
    const script = document.createElement('script');
    script.src = 'https://apis.google.com/js/platform.js';
    script.async = true;
    script.onload = getGapi();
    document.body.appendChild(script);
}

function getGapi(key) {
    // init the Google SDK client
    let ginterval = setInterval(function () {
        const g = window.gapi
        if (g !== undefined) {
            initGapi(g, key)
            clearInterval(ginterval)
        }
    }, 1000)
}

export default { CheckGoogleAPI }