import React from 'react';

//Material UI
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Box from '@material-ui/core/Box';

//Material Icons
import AddIcon from '@material-ui/icons/Add';
import RemoveIcon from '@material-ui/icons/Remove';

const MinMax = ({ props }) => {
    const handleMinMax = (key) => {
        if (key === 'add') {
            let val = props.value + 1
            props.clickHandler(props.field, val)
        }
        else if (key === 'minus') {
            let val = props.value - 1
            props.clickHandler(props.field, val)
        }
    }

    return (
        <Box display="flex" mb={1}>
            <Box mr={1}>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={() => handleMinMax('minus')}
                    disabled={props.value - 1 < props.minvalue ? true : false}
                >
                    <RemoveIcon />
                </Button>
            </Box>
            <Box mr={1}>
                <Typography variant="h4" color="primary">{props.value}</Typography>
            </Box>
            <Box mr={1}>
                <Button
                    variant="contained"
                    onClick={() => handleMinMax('add')}
                    color="primary"
                    disabled={props.value + 1 > props.maxvalue ? true : false}
                >
                    <AddIcon />
                </Button>
            </Box>
        </Box>
    )
}

export default MinMax;