function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-auto-tabpanel-${index}`}
            aria-labelledby={`scrollable-auto-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={1} pl={0} pr={0}>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function TabProps(name, index) {
    return {
        id: `${name}-auto-tab-${index}`,
        'aria-controls': `${name}-auto-tabpanel-${index}`,
    };
}


const ServiceDetails = ({ match }) => {
    const [loader, setLoader] = useState(false)
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [confirmation, setConfirmation] = useState('')
    const [confApiData, setConfApiData] = useState({})
    const [loadTimeout, setLoadTimeout] = useState(false)
    const [versions, setVersions] = useState([])
    const [curVersion, setCurVersion] = useState(0)
    const [resources, setResources] = useState([])
    const [curRes, setCurRes] = useState(0)
    const [resObj, setResObj] = useState({})
    const [showVersions, setShowVersions] = useState(false)
    const [showRes, setShowRes] = useState(true)
    const [formDialog, setFormDialog] = useState(false)
    const [formName, setFormName] = useState('')
    const [Methods, setMethods] = useState([])
    const [curServ, setCurServ] = useState(Object.keys(DataList.ActiveService).length > 0 ? DataList.ActiveService : (sessionStorage.getItem('CurrentService') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentService'))) : {}))
    const [formData, setFormData] = useState({})
    const [resname, setresname] = useState('')
    const [vername, setvername] = useState('')
    const [methodProps, setMethodProps] = useState({})
    const [btnAction, setBtnAction] = useState(false)
    const [showSidebar, setShowSideBar] = useState(true)
    const [mainBlk, setMainBlk] = useState('actions')
    const [resJSON, setResJSON] = useState({})
    const [jsontohtml, setJsontoHtml] = useState({})
    const [curState, setCurstate] = useState("")
    const [apiJson, setApiJson] = useState({})
    const [servstatus, setServStatus] = useState('')
    const [deployId, setDeployId] = useState('')
    const [methodJSON, setMethodJSON] = useState({})
    const [curMethod, setCurMethod] = useState(0)
    const [liveInterval, setLiveInterval] = useState(false)
    const [resourceStatus, setResourceStatus] = useState("")
    const [delInterval, setDelInterval] = useState(false)
    const [isGitConfigured, setIsGitConfigured] = useState(false)
    const [branches, setBranches] = useState([])
    const [showBranches, setShowBranches] = useState(false)
    const [curBr, setCurBr] = useState({
        "Branchname": "Select Branch",
        "Name": ""
    })

    const containerClasses = ContainerStyles()
    const typographyClasses = TypographyStyles()
    const statusClasses = StatusStyles()
    const tabClasses = tabStyles();
    const sideBarClasses = sidebarStyles()
    const accordionClassess = accordionStyles()

    const statemap = {
        deploying: "Deploying",
        running: "Running",
        failed: "Failed",
        design: "Design",
        published: "Published"
    }

    let userData = sessionStorage.getItem('user') ? JSON.parse(window.atob(sessionStorage.getItem('user'))) : ''
    let interval = '', intervalFlag = false, liveinterval = "", liveintervalflag = false, deltimeout = ""

    useEffect(() => {
        checkrole()
    }, [])

    const checkrole = () => {
        Cognitofunctions.CurrentUser(function (success, data) {
            if (success) {
                let gitapidata = {
                    path: apipath['datastore'] + "/git",
                    method: 'GET',
                    body: {},
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    ref: "GetGitCredentials"
                }
                manageAPIs(gitapidata)

                let params = {}
                if (window.location.href.indexOf('?') !== -1) {
                    let url = window.location.href.split('?')
                    if (url[1].indexOf('&') !== -1) {
                        let eachparam = url[1].split('&'), zz = { dummy: "" }
                        for (let i = 0; i < eachparam.length; i++) {
                            zz[eachparam[i].split('=')[0]] = eachparam[i].split('=')[1]
                        }
                        delete zz['dummy']
                        params = zz
                    }
                    else {
                        let zz = {
                            dummy: 'a'
                        }
                        zz[url[1].split('=')[0]] = url[1].split('=')[1]
                        delete zz['dummy']
                        params = zz
                    }
                }
                let apidata = {
                    method: "GET",
                    path: apipath['designapi'] + "/service/" + match.params.productid + "/" + match.params.serviceid,
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    body: {},
                    ref: "GetService",
                }
                manageAPIs(apidata)
            }
            else {
                // window.location.origin = window.location.origin + "/login"
                // window.location.origin = window.location.origin
                setMessage('You are not authorized to access this page.')
                setSeverrity('error')
                setOpenAlert(true)
                setLoader(false)
                setTimeout(function () {
                    window.location.href = window.location.origin + "/#/app/login"
                    window.location.reload()
                }, 1000)
            }
        })
    }

    const manageAPIs = (apidata) => {
        HandleApi.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
            .then(result => {
                if (result.hasOwnProperty('message') && result.message !== "") {
                    setMessage(result.message)
                    setSeverrity('info')
                    setOpenAlert(true)
                }
                if ((result.hasOwnProperty('statusCode') && result.statusCode === 200) || (result.hasOwnProperty('status') && (result.status === 200 || result.status === "200"))) {
                    if (apidata.ref === "GetAPI") {
                        setApiJson(result.body)
                        if (result.body.versions.length === 0) {
                            setLoadTimeout(true)
                            setLoader(false)
                            setFormName('Add Version')
                            setFormDialog(true)
                        }
                        else {
                            setLoader(false)
                            setVersions(result.body.versions)
                            let params = apidata.params, verid = result.body.versions[0].versionid, vers = result.body.versions
                            if (apidata.hasOwnProperty('status') && (apidata.status === "deploying" || apidata.status === "running" || apidata.status === "failed")) {
                                if (Object.keys(params).length > 0) {
                                    for (let i = 0; i < vers.length; i++) {
                                        if (vers[i].versionid === params.versionid) {
                                            verid = vers[i].versionid
                                            setCurVersion(i)
                                            break;
                                        }
                                    }
                                }
                                else {
                                    window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + result.body.versions[0].versionid
                                }
                                let apidata = {
                                    method: "POST",
                                    path: "/mock/describe",
                                    headers: true,
                                    body: {
                                        serviceid: match.params.serviceid,
                                        productid: match.params.productid,
                                        tenantid: userData.TenantId,
                                        userid: userData.EmailId
                                    },
                                    ref: "GetDeployDetails",
                                    versionid: verid,
                                }
                                manageAPIs(apidata)
                            }
                            else {
                                let ress = vers[0].resources
                                setResources(vers[0].resources)
                                if (!ress.length) {
                                    setLoadTimeout(true)
                                    setLoader(false)
                                    setFormName('Add Resource')
                                    setFormDialog(true)
                                }
                                if (Object.keys(params).length > 0) {
                                    for (let i = 0; i < vers.length; i++) {
                                        if (vers[i].versionid === params.versionid) {
                                            verid = vers[i].versionid
                                            setCurVersion(i)
                                            setResources(vers[i].resources)
                                            ress = vers[i].resources
                                            break;
                                        }
                                    }
                                    if (!ress.length) {
                                        setLoadTimeout(true)
                                        setLoader(false)
                                        setFormName('Add Resource')
                                        setFormDialog(true)
                                    }
                                    if (params.hasOwnProperty('resourceid')) {
                                        window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + params.versionid + "&resourceid=" + params.resourceid

                                        setCurRes(ress.indexOf(params.resourceid))

                                        let nextAPIdata = {
                                            method: "GET",
                                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + params.versionid + "/" + params.resourceid,
                                            ref: "GetSpecificResource",
                                            headers: {
                                                headers: true,
                                                apidomain: apidomain.vmurl
                                            },
                                            index: 0,
                                            resources: ress,
                                            versionid: params.versionid
                                        }
                                        manageAPIs(nextAPIdata)
                                        if (ress.length > 1) {
                                            for (let i = 0; i < ress.length; i++) {
                                                let nextAPIdata = {
                                                    method: "GET",
                                                    path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + params.versionid + "/" + ress[i],
                                                    ref: "GetSpecificResource",
                                                    headers: {
                                                        headers: true,
                                                        apidomain: apidomain.vmurl
                                                    },
                                                    index: i === ress.length - 1 && ress.length > 1 ? -2 : -1,
                                                    resources: ress,
                                                    versionid: params.versionid
                                                }
                                                manageAPIs(nextAPIdata)
                                            }
                                        }
                                    }
                                }
                                else {
                                    if (ress.length) {
                                        window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + result.body.versions[0].versionid + "&resourceid=" + ress[0]

                                        for (let i = 0; i < ress.length; i++) {
                                            let nextAPIdata = {
                                                method: "GET",
                                                path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + result.body.versions[0].versionid + "/" + ress[i],
                                                ref: "GetSpecificResource",
                                                headers: {
                                                    headers: true,
                                                    apidomain: apidomain.vmurl
                                                },
                                                index: i === ress.length - 1 && ress.length > 1 ? -2 : i,
                                                resources: ress,
                                                versionid: result.body.versions[0].versionid
                                            }
                                            manageAPIs(nextAPIdata)
                                        }
                                    }
                                    else {
                                        window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + result.body.versions[0].versionid
                                    }
                                }
                            }
                        }
                    }
                    else if (apidata.ref === "GetSpecificResource") {
                        let resjson = resJSON
                        resjson[result.body.resourceid] = result.body
                        setResJSON(resjson)
                        if (apidata.index === 0 || apidata.resources.length === 1) {
                            setResObj(result.body)
                            let nextAPidata = {
                                method: "GET",
                                path: apipath['designapi'] + "/api/" + match.params.productid + "/" + match.params.serviceid + "/" + curServ.apiid + "/" + apidata.versionid + "/" + result.body.resourceid + "/method",
                                ref: "GetAllMethods",
                                headers: {
                                    headers: true,
                                    apidomain: apidomain.vmurl
                                },
                                body: {}
                            }
                            manageAPIs(nextAPidata)
                            let nextAPIdata1 = {
                                method: "GET",
                                headers: {
                                    headers: true,
                                    apidomain: apidomain.vmurl
                                },
                                path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + apidata.versionid + "/" + result.body.resourceid + "/output",
                                ref: "GetLiveResOP",
                                body: {},
                                show: false,
                                resjson: result.body,
                                key: "Resource",
                                reslist: apidata.resources,
                                versionid: apidata.versionid
                            }
                            manageAPIs(nextAPIdata1)
                        }
                        else if (apidata.index === -2) {
                            let meths = JSON.parse(window.atob(sessionStorage.getItem('rffd')))
                            setMethods(meths)
                        }
                    }
                    else if (apidata.ref === "NewResource") {
                        setResObj({})

                        window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[curVersion].versionid + "&resourceid=" + result.body.resourceid

                        let res = [...resources]
                        res.push(result.body.resourceid)
                        setResources(res)

                        let resjson = resJSON
                        resjson[result.body.resourceid] = result.body
                        setResJSON(resjson)

                        setCurRes(res.length - 1)
                        setFormDialog(false)
                        setFormData({})
                        setMethods(result.body.methods)
                        setResObj(result.body)
                        setLoader(false)
                        setresname('')
                    }
                    else if (apidata.ref === "NewVersion") {
                        let vers = result.body.versions
                        window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + result.body.versions[vers.length - 1].versionid
                        setResources([])
                        setCurRes(0)
                        setResObj({})
                        setResJSON({})
                        setMethods([])
                        setVersions(vers)
                        setCurVersion(vers.length - 1)
                        setFormDialog(false)
                        setFormData({})
                        setvername('')
                        if (!vers[vers.length - 1].resources.length) {
                            setLoadTimeout(true)
                            setLoader(false)
                            setFormName('Add Resource')
                            setFormDialog(true)
                        }
                        // let nextAPIdata = {
                        //     method: "GET",
                        //     path: '/api/' + curServ.apiid + "/" + result.body.versions[vers.length - 1].versionid + "/resource",
                        //     ref: "GetResources",
                        //     headers: true,
                        //     params: {}
                        // }
                        // manageAPIs(nextAPIdata)
                    }
                    else if (apidata.ref === "DeleteVersion") {
                        setResourceStatus('')
                        setDelInterval(false)
                        liveintervalflag = false
                        clearInterval(liveinterval)
                        clearTimeout(deltimeout)
                        setVersions([])
                        setCurVersion(0)
                        setResources([])
                        setCurRes(0)
                        setResObj({})
                        setMethods([])
                        setMethodProps({})
                        setShowVersions(false)
                        let nextAPIdata = {
                            method: "GET",
                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid,
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                            body: {},
                            ref: "GetAPI",
                            params: {},
                            status: servstatus
                        }
                        manageAPIs(nextAPIdata)
                    }
                    else if (apidata.ref === "DeleteResource") {
                        setResourceStatus('')
                        setDelInterval(false)
                        liveintervalflag = false
                        clearInterval(liveinterval)
                        clearTimeout(deltimeout)
                        let ress = [...resources]
                        ress.splice(ress.indexOf(apidata.resourceid), 1)
                        setResources(ress)

                        setResObj({})
                        setMethods([])
                        setMethodProps({})
                        setCurRes(0)
                        setLoader(false)

                        let resjson = resJSON
                        delete resjson[apidata.resourceid]
                        setResJSON(resjson)

                        if (!ress.length) {
                            window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[curVersion].versionid

                            setLoadTimeout(true)
                            setLoader(false)
                            setFormName('Add Resource')
                            setFormDialog(true)
                        }
                        else {
                            window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[curVersion].versionid + "&resourceid=" + ress[0]

                            setResObj(resJSON[ress[0]])
                            sessionStorage.setItem("rffd", window.btoa(JSON.stringify(resJSON[ress[0]].methods)))
                            if (resJSON[ress[0]].methods > 0) {
                                let meths = resJSON[ress[0]].methods
                                setMethods(meths)
                                setLoader(false)
                            }
                            else {
                                setLoadTimeout(true)
                                setLoader(false)
                            }
                        }
                    }
                    else if (apidata.ref === "DeleteService") {
                        ServiceAPIs.DeleteService(apidata, function (success, response) {
                            if (response.hasOwnProperty('message') && response.message !== "") {

                            }
                            sessionStorage.removeItem('CurrentService')
                            DataList.ActiveService = {}
                            window.setTimeout(function () {
                                window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services"
                            }, 500)
                        })
                    }
                    else if (apidata.ref === "NewMethod") {
                        setFormDialog(false)
                        setMethods(result.body)
                        setLoader(false)

                        let resjson = resJSON
                        resjson[resources[curRes]]['methods'] = result.body
                        setResJSON(resjson)

                        let meths = [...Methods]
                        meths.push(result.body)
                        setMethods(meths)
                    }
                    else if (apidata.ref === "DeleteMethod") {
                        // setMethods(result.body)
                        // setLoader(false)

                        setMethods([])
                        setLoadTimeout(false)

                        let resjson = resJSON
                        resjson[resources[curRes]]['methods'] = result.body
                        setResJSON(resjson)

                        let nextAPidata = {
                            method: "GET",
                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + match.params.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resources[curRes] + "/method",
                            ref: "GetAllMethods",
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                            body: {}
                        }
                        manageAPIs(nextAPidata)
                    }
                    else if (apidata.ref === "GetSpecificMethod") {
                        let metjson = methodJSON
                        metjson[result.body.methodid] = result.body
                        setMethodJSON(metjson)
                        let meths = [...Methods]
                        setMethods([])
                        setMethods(meths)
                    }
                    else if (apidata.ref === "GetAllMethods") {
                        if (result.body.length > 0) {
                            let meths = result.body
                            sessionStorage.setItem("rffd", window.btoa(JSON.stringify(result.body)))
                            setMethods(meths)
                            setLoader(false)
                            setLoadTimeout(true)
                        }
                        else {
                            setLoadTimeout(true)
                            setLoader(false)
                        }
                    }
                    else if (apidata.ref === "PublishDesign") {
                        // sessionStorage.setItem('servicestatus', result.body.status)
                        // setServStatus(result.body.status)
                        // setCurServ(result.body)
                        // DataList.ActiveService = result.body
                        // sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(result.body)))
                        // if (result.body.status !== "design") {
                        //     setBtnAction(true)
                        // }
                        // else {
                        //     setBtnAction(false)
                        // }
                        setLoader(false)
                        setTimeout(function () {
                            window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "/deploy"
                        }, 1000)
                    }
                    else if (apidata.ref === "DeployCode") {
                        sessionStorage.setItem('servicestatus', result.body.status)
                        setServStatus(result.body.status)
                        setCurServ(result.body)
                        DataList.ActiveService = result.body
                        sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(result.body)))
                        setLoader(false)
                        let apidata = {
                            method: "POST",
                            path: "/mock/describe",
                            headers: true,
                            body: {
                                serviceid: match.params.serviceid,
                                productid: match.params.productid,
                                tenantid: userData.TenantId,
                                userid: userData.EmailId
                            },
                            ref: "GetDeployDetails",
                            versionid: versions[curVersion].versionid
                        }
                        manageAPIs(apidata)
                    }
                    else if (apidata.ref === "GetDeployDetails") {
                        sessionStorage.setItem('servicestatus', result.body.status)
                        setServStatus(result.body.status)
                        if (result.body.hasOwnProperty('api')) {
                            setApiJson(result.body.api)
                        }
                        if (result.body.hasOwnProperty('deployid')) {
                            setDeployId(result.body.deployid)
                        }
                        if (result.body.hasOwnProperty('status') && result.body.status === "deploying") {
                            if (!intervalFlag) {
                                intervalFlag = true
                                handleAPIInterval(apidata.versionid)
                            }
                        }
                        else {
                            clearInterval(interval)
                        }
                        let ress = [], resjson = { "dummy": "a" }, serv = curServ, meths = []
                        serv['status'] = result.body.status
                        setCurServ(serv)
                        for (let i = 0; i < result.body.resources.length; i++) {
                            ress.push(result.body.resources[i].resourceid)
                            resjson[result.body.resources[i].resourceid] = result.body.resources[i]
                            if (i === 0) {
                                setResObj(result.body.resources[i])
                                meths = result.body.resources[i].methods
                                window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + apidata.versionid + "&resourceid=" + result.body.resources[i].resourceid
                            }
                        }
                        delete resjson['dummy']
                        setResJSON(resjson)
                        setResources(ress)
                        setMethods(meths)
                        DataList.ActiveService = serv
                        sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(serv)))
                        setLoadTimeout(true)
                    }
                    else if (apidata.ref === "DeleteDeploy") {
                        // setApiJson({})
                        setDeployId('')
                        clearInterval(interval)
                        intervalFlag = false
                        setResObj({})
                        let apidata = {
                            method: "GET",
                            path: "/service/" + match.params.productid + "/" + match.params.serviceid,
                            headers: true,
                            body: {},
                            ref: "GetService",
                        }
                        manageAPIs(apidata)

                    }
                    else if (apidata.ref === "DesignService") {
                        sessionStorage.setItem('servicestatus', result.body.status)
                        setServStatus(result.body.status)
                        setCurServ(result.body)
                        DataList.ActiveService = result.body
                        if (result.body.status !== "design") {
                            if (result.body.status !== "deploying") {
                                clearInterval(interval)
                            }
                            else {
                                intervalFlag = false
                            }
                            setBtnAction(true)
                        }
                        else {
                            setBtnAction(false)
                        }
                        setLoader(false)
                        sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(result.body)))
                    }
                    else if (apidata.ref === "GetService") {
                        sessionStorage.setItem('servicestatus', result.body.status)
                        setServStatus(result.body.status)
                        setCurServ(result.body)
                        DataList.ActiveService = result.body
                        if (result.body.status !== "design") {
                            if (result.body.status !== "deploying") {
                                clearInterval(interval)
                            }
                            else {
                                intervalFlag = false
                            }
                            setBtnAction(true)
                        }
                        else {
                            setBtnAction(false)
                        }
                        sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(result.body)))
                        let apidata = {
                            method: "GET",
                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + result.body.serviceid + "/" + result.body.apiid,
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                            body: {},
                            ref: "GetAPI",
                            params: {},
                            status: result.body.hasOwnProperty('status') === true ? result.body.status : ""

                        }
                        manageAPIs(apidata)
                    }
                    else if (apidata.ref === "GetLogs") {
                        setFormName(apidata.logname)
                        setJsontoHtml(result.body)
                        setFormDialog(true)
                        setLoader(false)
                    }
                    else if (apidata.ref === "TriggerAutopopulateMethods") {
                        let nextAPIdata = {
                            ref: "AutopopulateMethods",
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                            body: {
                                actions: result.body,
                                resourcename: resObj.resourcename,
                                versionname: versions[curVersion].versionname
                            },
                            method: "POST",
                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + match.params.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid,
                        }
                        manageAPIs(nextAPIdata)
                    }
                    else if (apidata.ref === "AutopopulateMethods") {
                        let nextAPidata = {
                            method: "GET",
                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + match.params.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid + "/method",
                            ref: "GetAllMethods",
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                            body: {}
                        }
                        manageAPIs(nextAPidata)
                        setMethods([])
                        setLoadTimeout(false)
                    }
                    else if (apidata.ref === "CreateLiveResource" || apidata.ref === "UpdateLiveResource") {
                        if (apidata.ref === "CreateLiveResource") {
                            if (result.hasOwnProperty('containerstatus')) {
                                if (result.containerstatus === "success") {
                                    setMessage("Resource Deployment Started")
                                    setSeverrity("info")
                                    setOpenAlert(true)
                                }
                                else if (result.containerstatus === "error") {
                                    setMessage("Some internal error, Please try after some time")
                                    setSeverrity("info")
                                    setOpenAlert(true)
                                }
                            }
                        }
                        else if (apidata.ref === "UpdateLiveResource") {
                            if (result.hasOwnProperty('containerstatus')) {
                                if (result.containerstatus === "success") {
                                    setMessage("Resource Deployment Updated")
                                    setSeverrity("info")
                                    setOpenAlert(true)
                                }
                                else if (result.containerstatus === "error") {
                                    setMessage("Some internal error, Please try after some time")
                                    setSeverrity("info")
                                    setOpenAlert(true)
                                }
                            }
                        }
                        setLoader(false)
                        setLiveInterval(true)
                        liveintervalflag = true
                        liveinterval = window.setInterval(function () {
                            let nextAPIdata = {
                                path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid + "/output",
                                showMessage: true,
                                reslist: resources,
                                ref: "GetLiveResOP",
                                method: "GET",
                                headers: {
                                    headers: true,
                                    apidomain: apidomain.vmurl
                                },
                                body: {},
                                formname: "Resource Outputs",
                                show: false,
                                key: "Resource",
                                resjson: resObj,
                                versionid: versions[curVersion].versionid
                            }
                            manageAPIs(nextAPIdata)
                        }, 5000)
                    }
                    else if (apidata.ref === "GetLiveResOP" || apidata.ref === "GetLiveResLogs") {
                        let allresjson = resJSON
                        if (result.hasOwnProperty('resource_output')) {
                            if (apidata.show) {
                                setJsontoHtml(result.resource_output)
                                setFormName(apidata.formname)
                                setLoader(false)
                                setFormDialog(true)
                            }
                            else {
                                if (!allresjson.hasOwnProperty(apidata.resjson.resourceid)) {
                                    allresjson[apidata.resjson.resourceid] = apidata.resjson
                                }
                                allresjson[apidata.resjson.resourceid]['status'] = result.resource_output.status
                                if (apidata.key === "Resource" && (apidata.hasOwnProperty('showMessage') && apidata.showMessage)) {
                                    if (result.hasOwnProperty('resource_output')) {
                                        if (result.resource_output.hasOwnProperty('status')) {
                                            if (result.resource_output.status !== "Initiated") {
                                                setLiveInterval(false)
                                                clearInterval(liveinterval)
                                                liveintervalflag = false
                                            }
                                        }
                                    }
                                }
                            }
                            if (result.resource_output.hasOwnProperty('status')) {
                                setResourceStatus(result.resource_output.status)
                                if (result.resource_output.status !== "Running") {
                                    sessionStorage.removeItem('isResourceLive')
                                    if (result.resource_output.status === "Initiated") {
                                        if (apidata.key === "CRLive") {
                                            setLoader(false)
                                            setMessage('You cannot update in Initiated, Please wait for some time to update')
                                            setSeverrity('info')
                                            setOpenAlert(true)
                                        }
                                        else {
                                            setLiveInterval(true)
                                            if (liveintervalflag === false) {
                                                liveintervalflag = true
                                                liveinterval = window.setInterval(function () {
                                                    let nextAPIdata = {
                                                        path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + apidata.versionid + "/" + apidata.resjson.resourceid + "/output",
                                                        showMessage: true,
                                                        ref: "GetLiveResOP",
                                                        method: "GET",
                                                        headers: {
                                                            headers: true,
                                                            apidomain: apidomain.vmurl
                                                        },
                                                        body: {},
                                                        formname: "Resource Outputs",
                                                        show: false,
                                                        key: "Resource",
                                                        resjson: apidata.resjson,
                                                        reslist: apidata.reslist,
                                                        versionid: apidata.versionid
                                                    }
                                                    manageAPIs(nextAPIdata)
                                                }, 5000)
                                            }
                                        }
                                    }
                                    else if (result.resource_output.status === "Error" || result.resource_output.status === "Degraded") {
                                        setLiveInterval(false)
                                        clearInterval(liveinterval)
                                        liveintervalflag = false
                                        if (apidata.key === "CRLive") {
                                            let nextAPIdata = {
                                                ref: "UpdateLiveResource",
                                                method: "POST",
                                                headers: {
                                                    headers: true,
                                                    apidomain: apidomain.vmurl
                                                },
                                                body: {
                                                    type: "PYTHON_FLASK",
                                                    serviceid: match.params.serviceid,
                                                    versionid: versions[curVersion].versionid,
                                                    versionname: versions[curVersion].versionname,
                                                    resourceid: apidata.resjson.resourceid
                                                },
                                                path: apipath['liveapi'] + "/resource",
                                                resjson: apidata.resjson
                                            }
                                            manageAPIs(nextAPIdata)
                                        }
                                    }
                                }
                                else if (result.resource_output.status === "Running") {
                                    sessionStorage.setItem('isResourceLive', "yes")
                                    setLiveInterval(false)
                                    clearInterval(liveinterval)
                                    liveintervalflag = false
                                    let meths = [...Methods]
                                    if (meths.length > 0) {
                                        setMethods([])
                                        setMethods(meths)
                                    }
                                    if (apidata.key === "CRLive") {
                                        setLiveInterval(true)
                                        setResourceStatus('')
                                        let nextAPIdata = {
                                            ref: "UpdateLiveResource",
                                            method: "POST",
                                            headers: {
                                                headers: true,
                                                apidomain: apidomain.vmurl
                                            },
                                            body: {
                                                type: "PYTHON_FLASK",
                                                serviceid: match.params.serviceid,
                                                versionid: versions[curVersion].versionid,
                                                versionname: versions[curVersion].versionname,
                                                resourceid: apidata.resjson.resourceid
                                            },
                                            path: apipath['liveapi'] + "/resource/update",
                                            resjson: apidata.resjson
                                        }
                                        manageAPIs(nextAPIdata)
                                    }
                                }
                            }
                        }
                        else if (result.hasOwnProperty('job_status')) {
                            sessionStorage.removeItem('isResourceLive')
                            setResourceStatus('')
                            if (!allresjson.hasOwnProperty(apidata.resjson.resourceid)) {
                                allresjson[apidata.resjson.resourceid] = apidata.resjson
                            }
                            if (result.job_status === "Initiated") {
                                if (apidata.key === "CRLive") {
                                    setLoader(false)
                                    setMessage('You cannot update in Initiated, Please wait for some time to update')
                                    setSeverrity('info')
                                    setOpenAlert(true)
                                }
                                else {
                                    setLiveInterval(true)
                                    if (liveintervalflag === false) {
                                        liveintervalflag = true
                                        liveinterval = window.setInterval(function () {
                                            let nextAPIdata = {
                                                path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + apidata.versionid + "/" + apidata.resjson.resourceid + "/output",
                                                showMessage: true,
                                                ref: "GetLiveResOP",
                                                method: "GET",
                                                headers: {
                                                    headers: true,
                                                    apidomain: apidomain.vmurl
                                                },
                                                body: {},
                                                formname: "Resource Outputs",
                                                show: false,
                                                key: "Resource",
                                                resjson: apidata.resjson,
                                                reslist: apidata.reslist,
                                                versionid: apidata.versionid
                                            }
                                            manageAPIs(nextAPIdata)
                                        }, 5000)
                                    }
                                }
                            }
                            else if (result.job_status === "Not Deployed") {
                                if (apidata.key === "CRLive") {
                                    let nextAPIdata = {
                                        ref: "CreateLiveResource",
                                        method: "POST",
                                        headers: {
                                            headers: true,
                                            apidomain: apidomain.vmurl
                                        },
                                        body: {
                                            type: "PYTHON_FLASK",
                                            serviceid: match.params.serviceid,
                                            versionid: versions[curVersion].versionid,
                                            versionname: versions[curVersion].versionname,
                                            resourceid: apidata.resjson.resourceid
                                        },
                                        path: apipath['liveapi'] + "/resource",
                                        resjson: apidata.resjson
                                    }
                                    manageAPIs(nextAPIdata)
                                }
                            }
                        }
                        else {
                            setResourceStatus('')
                            sessionStorage.removeItem('isResourceLive')
                            if (apidata.show) {
                                setMessage('Some internal error, Please try after sometime')
                                setSeverrity('error')
                                setOpenAlert(true)
                            }
                            setLoader(false)
                        }
                    }
                    else if (apidata.ref === "DeleteLiveRes") {
                        setDelInterval(true)
                        setLoader(false)
                        setResourceStatus('Stopping')
                        deltimeout = window.setTimeout(function () {
                            setMessage("Resource Deployment deleted")
                            setResourceStatus('')
                            setSeverrity("info")
                            setOpenAlert(true)
                            setDelInterval(false)
                            let params = { versionid: '', resourceid: resources[curRes] }
                            let apidata1 = {
                                method: "GET",
                                path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resources[curRes],
                                ref: "GetSpecificResource",
                                headers: {
                                    headers: true,
                                    apidomain: apidomain.vmurl
                                },
                                versionid: versions[curVersion].versionid,
                                params: params,
                                index: 0,
                                resources: resources
                            }
                            manageAPIs(apidata1)
                        }, 60000)
                    }
                    else if (apidata.ref === "GetGitCredentials") {
                        GitAPIs.GetGitDetails(apidata, function (success, response) {
                            if (success) {
                                if (Object.keys(response.data).length > 0) {
                                    setIsGitConfigured(true)
                                }
                                else {
                                    window.location.href = window.location.origin + "/#/app/gitsetup"
                                }
                            }
                        })
                    }
                    else if (apidata.ref === "GetBranchesByCategory") {
                        setBranches(result.body)
                        setFormName('Share Service')
                        setFormDialog(true)
                        setLoader(false)
                    }
                    else if(apidata.ref === "Shareservice"){
                        setLoader(false)
                        setFormName('')
                        setFormDialog(false)
                        setCurBr({
                            "Branchname": "Select Branch",
                            "Name": ""
                        })
                    }
                }
                else {
                    if (result.hasOwnProperty('error_message')) {
                        setMessage(result.error_message)
                    }
                    else if (result.hasOwnProperty('errorMessage')) {
                        setMessage(result.errorMessage)
                    }
                    else {
                        setMessage('Some internal error, Please try after sometime')
                    }
                    if (apidata.ref === "GetLiveResOP" || apidata.ref === "GetLiveResLogs") {
                        if (apidata.hasOwnProperty('show')) {
                            if (apidata.show) {
                                setSeverrity('error')
                                setOpenAlert(true)
                            }
                            setLoader(false)
                        }
                    }
                    else {
                        setSeverrity('error')
                        setOpenAlert(true)
                        setLoader(false)
                    }
                }
            })
            .catch(err => {
                setMessage("Some internal error,Please try after some time.")
                setSeverrity('error')
                setOpenAlert(true)
                setLoader(false)
            })
    }

    const handleActions = (apidata) => {
        if (apidata.Action === 'redirect') {
            window.open(apidata.url, '_blank')
        }
        else if (apidata.Action === 'refresh') {
            if (apidata.key === "api") {
                clearInterval(interval)
                intervalFlag = false
                setLoader(true)
                setCurServ({})
                setVersions([])
                setResources([])
                setResObj({})
                setMethodJSON({})
                setMethods([])
                setLoadTimeout(false)
                let apidata = {
                    method: "GET",
                    path: apipath['designapi'] + "/service/" + match.params.productid + "/" + match.params.serviceid,
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    body: {},
                    ref: "GetService",
                }
                manageAPIs(apidata)
            }
            else if (apidata.key === "resource") {
                setResObj({})
                setMethodJSON({})
                setMethods([])
                setLoadTimeout(false)
                if (curServ.hasOwnProperty('status') && (servstatus === "deploying" || servstatus === "running" || servstatus === "failed")) {
                    setResources([])
                    let apidata = {
                        method: "POST",
                        path: "/mock/describe",
                        headers: true,
                        body: {
                            serviceid: match.params.serviceid,
                            productid: match.params.productid,
                            tenantid: userData.TenantId,
                            userid: userData.EmailId
                        },
                        ref: "GetDeployDetails",
                        versionid: versions[curVersion].versionid
                    }
                    manageAPIs(apidata)
                }
                else {
                    let params = { versionid: '', resourceid: resources[curRes] }
                    let apidata = {
                        method: "GET",
                        path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resources[curRes],
                        ref: "GetSpecificResource",
                        headers: {
                            headers: true,
                            apidomain: apidomain.vmurl
                        },
                        versionid: versions[curVersion].versionid,
                        params: params,
                        index: 0,
                        resources: resources
                    }
                    manageAPIs(apidata)
                }
            }
        }
        else if (apidata.Action === "confirmation") {
            setConfApiData(apidata['apidata'])
            setConfirmation(apidata.confirmText)
        }
        else {
            if (apidata.method === "DELETE") {
                setLoader(true)
            }
            manageAPIs(apidata)
        }
    }

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const handleConfirmation = (key) => {
        setConfirmation('')
        if (key) {
            setLoader(true)
            handleActions(confApiData)
            // console.log(confApiData)
            setConfApiData({})
        }
        else {
            setConfApiData({})
        }
    }

    const Sidebar = () => {
        return (
            <Box display="flex" flexDirection="column" alignItems="center" justifyContent="flex-start" position="relative">
                <Box display="flex" style={{ width: '100%' }} mb={1}>
                    <Box display="flex" alignItems="center" justifyContent="space-between" style={{ width: '100%' }} p={1} bgcolor="#e4e7e9" position="relative">
                        <Box onClick={() => { setShowVersions(!showVersions) }} display="flex" alignItems="center" justifyContent="space-between" style={{ width: '100%', cursor: "pointer" }}>
                            <Box mr={1} display="flex" alignItems="center">
                                <Box display="flex" flexDirection="column" alignItems="flex-start">
                                    <Box>
                                        <Typography className={`${typographyClasses.smallText} ${typographyClasses.bold}`}>{versions.length > 0 ? versions[curVersion].versionname : ''}</Typography>
                                    </Box>
                                    <Box>
                                        <Typography className={`${typographyClasses.smallText}`}>versions</Typography>
                                    </Box>
                                </Box>
                            </Box>
                            <Box display="flex">
                                {
                                    showVersions === true ?
                                        <ArrowDropUpIcon fontSize="small" />
                                        :
                                        <ArrowDropDownIcon fontSize="small" />
                                }
                            </Box>
                        </Box>
                        {
                            showVersions === true ?
                                <Box position="absolute" bgcolor="white" height={250} style={{ width: '100%' }} boxShadow={1} top={55} left={0} zIndex={1}>
                                    <Box position="relative" style={{ height: '100%' }}>
                                        <Box overflow="auto" height={210}>
                                            {
                                                versions.length > 0 ?
                                                    versions.map((ver, index) => {
                                                        return (
                                                            <Box key={index} display="flex" alignItems="center" justifyContent="space-between" p={1} bgcolor={index === curVersion ? "#e6e8f3" : "white"} className={`${sideBarClasses.root} ${sideBarClasses.onhoveritem}`} onClick={() => {
                                                                handleSidebarPopover("version", index)
                                                            }} title={ver.description}>
                                                                <Box display="flex" flexDirection="column" alignItems="flex-start">
                                                                    <Typography className={`${typographyClasses.bold} ${typographyClasses.smallText}`} color={index === curVersion ? "primary" : "inherit"}>{ver.versionname}</Typography>
                                                                    <Typography className={typographyClasses.smallText} color={index === curVersion ? "primary" : "inherit"}>{ver.versionid}</Typography>
                                                                </Box>
                                                                {
                                                                    btnAction === false ?
                                                                        <Box display="flex"
                                                                            onClick={(event) => {
                                                                                event.stopPropagation()
                                                                                handleActions({
                                                                                    Action: "confirmation",
                                                                                    confirmText: "Are you sure you want to delete this version(" + ver.versionname + ")?",
                                                                                    apidata: {
                                                                                        method: "DELETE",
                                                                                        path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + ver.versionid,
                                                                                        headers: {
                                                                                            headers: true,
                                                                                            apidomain: apidomain.vmurl
                                                                                        },
                                                                                        body: {},
                                                                                        ref: "DeleteVersion"
                                                                                    }
                                                                                })
                                                                            }}
                                                                        >
                                                                            <DeleteIcon className={typographyClasses.text} color="secondary" />
                                                                        </Box>
                                                                        :
                                                                        null
                                                                }
                                                            </Box>
                                                        )
                                                    })
                                                    :
                                                    <Box p={1}>
                                                        {
                                                            loadTimeout === false ?
                                                                <Loader type="block" />
                                                                :
                                                                <Typography className={typographyClasses.smallText}>No Versions</Typography>
                                                        }
                                                    </Box>
                                            }
                                        </Box>
                                        <Box position="absolute" bottom={0} left={0} borderTop={1} style={{ borderColor: 'lightgray', width: '100%' }} top={210} height={40} display="flex" justifyContent="space-between" alignItems="center" >
                                            <Box style={{ height: '100%', width: '100%' }} display="flex" alignItems="center">
                                                <Button
                                                    variant="text"
                                                    color="primary"
                                                    onClick={() => {
                                                        setFormData({ 'versionname': '' })
                                                        setFormName('Add Version')
                                                        setFormDialog(true)
                                                    }}
                                                    disabled={btnAction}
                                                >
                                                    <AddIcon className={typographyClasses.smallText} />&nbsp;Version
                                                </Button>
                                            </Box>
                                        </Box>
                                    </Box>
                                </Box>
                                :
                                null
                        }
                    </Box>
                </Box>
                <Box display="flex" style={{ width: '100%' }} mb={1}>
                    <Box display="flex" alignItems="center" justifyContent="space-between" style={{ width: '100%' }} p={1} bgcolor="#e4e7e9" position="relative">
                        <Box onClick={() => { setShowRes(!showRes) }} display="flex" alignItems="center" justifyContent="space-between" style={{ width: '100%', cursor: "pointer" }}>
                            <Box mr={1} display="flex" alignItems="center">
                                <Box display="flex" flexDirection="column" alignItems="flex-start">
                                    <Box>
                                        <Typography className={`${typographyClasses.smallText} ${typographyClasses.bold}`}>{resObj.hasOwnProperty('resourcename') === true ? resObj['resourcename'] : ""}</Typography>
                                    </Box>
                                    <Box>
                                        <Typography className={`${typographyClasses.smallText}`}>Resources</Typography>
                                    </Box>
                                </Box>
                            </Box>
                            <Box display="flex">
                                {
                                    showRes === true ?
                                        <ArrowDropUpIcon fontSize="small" />
                                        :
                                        <ArrowDropDownIcon fontSize="small" />
                                }
                            </Box>
                        </Box>
                        {
                            showRes === true ?
                                <Box position="absolute" bgcolor="white" height={250} style={{ width: '100%' }} boxShadow={1} top={55} left={0}>
                                    <Box position="relative" style={{ height: '100%' }}>
                                        <Box overflow="auto" height={210}>
                                            {
                                                resources.length > 0 && Object.keys(resJSON).length > 0 ?
                                                    resources.map((res, index) => {
                                                        return (
                                                            <Box key={index} display="flex" alignItems="center" justifyContent="space-between" p={1} bgcolor={curRes === index ? "#e6e8f3" : "white"} className={`${sideBarClasses.root} ${sideBarClasses.onhoveritem}`} onClick={() => {
                                                                if (curRes !== index) {
                                                                    handleSidebarPopover("resource", index)
                                                                }
                                                            }}>
                                                                <Box display="flex" flexDirection="column" alignItems="flex-start">
                                                                    <Typography className={` ${typographyClasses.bold} ${typographyClasses.smallText}`} color={curRes === index ? "primary" : "inherit"}>{resJSON.hasOwnProperty(res) === true ? resJSON[res]['resourcename'] : ""}</Typography>
                                                                    <Typography className={typographyClasses.smallText} color={index === curRes ? "primary" : "inherit"}>{res}</Typography>
                                                                </Box>
                                                                {
                                                                    btnAction === false ?
                                                                        <Box display="flex"
                                                                            disabled={resourceStatus === "Running" ? true : false}
                                                                            onClick={(event) => {
                                                                                event.stopPropagation()
                                                                                handleActions({
                                                                                    Action: "confirmation",
                                                                                    confirmText: "Are you sure you want to delete this resource(" + res + ")?",
                                                                                    apidata: {
                                                                                        method: "DELETE",
                                                                                        path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + res,
                                                                                        headers: {
                                                                                            headers: true,
                                                                                            apidomain: apidomain.vmurl
                                                                                        },
                                                                                        body: {},
                                                                                        ref: "DeleteResource",
                                                                                        resourceid: res
                                                                                    }
                                                                                })
                                                                            }}
                                                                        >
                                                                            <DeleteIcon className={typographyClasses.text} color="secondary" />
                                                                        </Box>
                                                                        :
                                                                        null
                                                                }
                                                            </Box>
                                                        )
                                                    })
                                                    :
                                                    Object.keys(resObj).length > 0 ?
                                                        <Box display="flex" alignItems="center" justifyContent="space-between" p={1} bgcolor="#e6e8f3" className={`${sideBarClasses.root} ${sideBarClasses.onhoveritem}`}>
                                                            <Box display="flex" flexDirection="column" alignItems="flex-start">
                                                                <Typography className={` ${typographyClasses.bold} ${typographyClasses.smallText}`} color={"primary"}>{resObj['resourcename']}</Typography>
                                                                <Typography className={typographyClasses.smallText} color={"primary"}>{resObj['resourceid']}</Typography>
                                                            </Box>
                                                            {
                                                                btnAction === false ?
                                                                    <Box display="flex"
                                                                        onClick={(event) => {
                                                                            event.stopPropagation()
                                                                            handleActions({
                                                                                Action: "confirmation",
                                                                                confirmText: "Are you sure you want to delete this resource(" + resObj['resourcename'] + ")?",
                                                                                apidata: {
                                                                                    method: "DELETE",
                                                                                    path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resObj['resourceid'],
                                                                                    headers: {
                                                                                        headers: true,
                                                                                        apidomain: apidomain.vmurl
                                                                                    },
                                                                                    body: {},
                                                                                    ref: "DeleteResource",
                                                                                    resourceid: resObj['resourceid']
                                                                                }
                                                                            })
                                                                        }}
                                                                    >
                                                                        <DeleteIcon className={typographyClasses.text} color="secondary" />
                                                                    </Box>
                                                                    :
                                                                    null
                                                            }
                                                        </Box>
                                                        :
                                                        <Box p={1}>
                                                            {
                                                                loadTimeout === false ?
                                                                    <Loader type="block" />
                                                                    :
                                                                    <Typography className={typographyClasses.smallText}>No Resources</Typography>
                                                            }
                                                        </Box>
                                            }
                                        </Box>
                                        <Box position="absolute" bottom={0} left={0} borderTop={1} style={{ borderColor: 'lightgray', width: '100%' }} top={210} height={40} display="flex" justifyContent="space-between" alignItems="center">
                                            <Box style={{ height: '100%', width: '100%' }} display="flex" alignItems="center">
                                                <Button
                                                    variant="text"
                                                    color="primary"
                                                    onClick={() => {
                                                        setFormData({ 'resourcename': '' })
                                                        setFormName('Add Resource')
                                                        setFormDialog(true)
                                                    }}
                                                    disabled={btnAction}
                                                >
                                                    <AddIcon className={typographyClasses.smallText} />&nbsp;Resource
                                                </Button>
                                            </Box>
                                        </Box>
                                    </Box>
                                </Box>
                                :
                                null
                        }
                    </Box>
                </Box>
            </Box >
        )
    }

    const handleSidebarPopover = (key, value) => {
        if (key === "version") {
            if (value === curVersion) {
                setShowVersions(false)
            }
            else {
                setLoadTimeout(false)
                window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[value].versionid

                setResources([])
                let ress = versions[value].resources
                if (!ress.length) {
                    setLoadTimeout(true)
                    setLoader(false)
                    setFormName('Add Resource')
                    setFormDialog(true)
                }
                // setLoader(true)
                setMethodProps({})
                setCurVersion(value)
                setResObj({})
                setResources(ress)
                setMethods([])
                setCurRes(0)
                setShowVersions(false)
                if (ress.length) {
                    window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[value].versionid + "&resourceid=" + ress[0]

                    for (let i = 0; i < ress.length; i++) {
                        let nextAPIdata = {
                            method: "GET",
                            path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[value].versionid + "/" + ress[i],
                            ref: "GetSpecificResource",
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                            index: i === ress.length - 1 && ress.length > 1 ? -2 : i,
                            resources: ress,
                            versionid: versions[value].versionid
                        }
                        manageAPIs(nextAPIdata)
                    }
                }
                else {
                    window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[value].versionid
                }
            }
        }
        else {
            if (value === curRes) {
                setShowRes(false)
            }
            else {
                setLoadTimeout(false)
                window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "?versionid=" + versions[curVersion].versionid + "&resourceid=" + resources[value]
                setResObj({})
                setMainBlk('actions')
                setMethodProps({})
                setCurRes(value)
                let apidata = {
                    method: "GET",
                    path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resources[value],
                    ref: "GetSpecificResource",
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    versionid: versions[curVersion].versionid,
                    params: {},
                    index: 0
                }
                manageAPIs(apidata)
            }
        }
    }

    const handlePanelChange = (method, index) => {
        let methprops = methodProps
        methprops[method.methodid]['expanded'] = !methprops[method.methodid]['expanded']
        setMethodProps(methprops)
        let meths = [...Methods]
        setMethods([])
        setMethods(meths)
        if (methprops[method.methodid]['expanded']) {
            setShowSideBar(false)
            var elmnt = document.getElementById(method.methodid);
            elmnt.scrollIntoView();
            setCurMethod(index)
            let nextAPidata = {
                method: "GET",
                path: apipath['designapi'] + "/api/" + match.params.productid + "/" + match.params.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid + "/" + method.methodid,
                ref: "GetSpecificMethod",
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                body: {}
            }
            manageAPIs(nextAPidata)
        }
    };

    const PopulateMethods = () => {
        return (
            <Box>
                {
                    Methods.map((method, metindex) => {
                        if (!methodProps.hasOwnProperty(method.methodid)) {
                            methodProps[method.methodid] = {
                                expanded: false,
                                ipeditor: {
                                    code: method.inputschema,
                                    readonly: true
                                },
                                opeditor: {
                                    code: method.outputschema,
                                    readonly: true
                                },
                                req: {
                                    code: method.requirements,
                                    readonly: true
                                },
                                tab: 0,
                                index: metindex,
                                method: method.methodtype,
                                apiurl: method.apiurl,
                                headers: method.headers,
                            }
                        }
                        return (
                            <Accordion key={method.methodid} square TransitionProps={{ unmountOnExit: true }} style={{ marginBottom: '4px' }} defaultExpanded expanded={methodProps[method.methodid]['expanded']} onChange={() => handlePanelChange(method, metindex)} id={method.methodid}>
                                <AccordionSummary {...expansionProps("method-type", metindex)} expandIcon={<ExpandMoreIcon fontSize="small" />} style={{ minHeight: 35, height: 40, margin: '0 !important', padding: 5, border: '1px solid' }} className={`${accordionClassess[method.methodtype]} ${accordionClassess['bd' + method.methodtype]}`}>
                                    <Box display="flex" alignItems="center" style={{ width: '100%' }} justifyContent="space-between">
                                        <Box display="flex" alignItems="center">
                                            <Box display="flex" alignItems="center" justifyContent="center" p={1} pt={0.5} pb={0.5} minWidth={75} className={accordionClassess['bg' + method.methodtype]} color="white" mr={3}>
                                                <Box display="flex">
                                                    <Typography color="inherit" className={`${typographyClasses.smallText}`}>{method.methodtype}</Typography>
                                                </Box>
                                            </Box>
                                            {
                                                resourceStatus === "Running" ?
                                                    <Box display="flex" alignItems="center">
                                                        <Box>
                                                            <Box display="flex" alignItems="center">
                                                                <Typography className={`${typographyClasses.smallText}`} align="left">{window.location.origin}/liveapp/{match.params.serviceid}/{versions[curVersion].versionname}/{resObj.resourcename}{method.hasOwnProperty('methodpath') === true ? method.methodpath : ""}</Typography>
                                                                <IconButton
                                                                    color="inherit"
                                                                    title="Copy"
                                                                    onClick={(e) => {
                                                                        e.stopPropagation()
                                                                        let strtocp = window.location.origin + "/liveapp/" + match.params.serviceid + "/" + versions[curVersion].versionname + "/" + resObj.resourcename
                                                                        let metpth = method.hasOwnProperty('methodpath') === true ? method.methodpath : ""
                                                                        strtocp = strtocp + metpth

                                                                        copyText(strtocp)
                                                                    }}
                                                                >
                                                                    <FileCopyIcon fontSize="small" style={{ fontSize: '0.8rem' }} />
                                                                </IconButton>
                                                            </Box>
                                                        </Box>
                                                    </Box>
                                                    :
                                                    <Box>
                                                        <Typography color="inherit" className={`${typographyClasses.smallText} ${typographyClasses.bold}`}>/{versions[curVersion].versionname}/{resObj.resourcename}{method.methodpath}</Typography>
                                                    </Box>
                                            }
                                        </Box>
                                        <Box display="flex" alignItems="center">
                                            <Box mr={1}>
                                                <Button
                                                    variant="text"
                                                    color="secondary"
                                                    onClick={(e) => {
                                                        e.stopPropagation()
                                                        handleActions({
                                                            Action: "confirmation",
                                                            confirmText: "Are you sure you want to delete this method?",
                                                            apidata: {
                                                                method: "DELETE",
                                                                path: apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resources[curRes] + "/" + method.methodid,
                                                                headers: {
                                                                    headers: true,
                                                                    apidomain: apidomain.vmurl
                                                                },
                                                                ref: "DeleteMethod"
                                                            }
                                                        })
                                                    }}
                                                    disabled={btnAction}
                                                >
                                                    <DeleteIcon className={typographyClasses.text} />
                                                </Button>
                                            </Box>
                                            {
                                                method.hasOwnProperty('state') === true ?
                                                    method.state.hasOwnProperty('status') === true ?
                                                        method.state.hasOwnProperty(method.state.status) === true ?
                                                            <Box mr={1} style={{ cursor: 'pointer' }} borderRadius={16} height={28} p={0.5} pl={2} pr={2} className={statusClasses[method.state.status]} onClick={(e) => {
                                                                e.stopPropagation()
                                                                setCurstate(method.state.status)
                                                                setJsontoHtml(method.state[method.state.status])
                                                                setFormName('Status')
                                                                setFormDialog(true)
                                                            }}>
                                                                <Typography className={`${typographyClasses.root} ${typographyClasses.smallText}`}>Method Status - {method.state.status}</Typography>
                                                            </Box>
                                                            :
                                                            null
                                                        :
                                                        null
                                                    :
                                                    null
                                            }
                                            {
                                                method.hasOwnProperty('output') === true ?
                                                    <Box mr={1} height={28} style={{ cursor: 'pointer', borderColor: 'gray' }} border={1} p={0.5} pl={2} pr={2} onClick={(e) => {
                                                        e.stopPropagation()
                                                        setJsontoHtml(method.output)
                                                        setFormName('Outputs')
                                                        setFormDialog(true)
                                                    }}>
                                                        <Typography className={typographyClasses.smallText}>Output</Typography>
                                                    </Box>
                                                    :
                                                    null

                                            }
                                            {
                                                method.hasOwnProperty('logpath') === true ?
                                                    <Box style={{ cursor: 'pointer', borderColor: 'gray' }} border={1} mr={1} height={28} p={0.5} pl={2} pr={2} onClick={(e) => {
                                                        e.stopPropagation()
                                                        setLoader(true)
                                                        let apidata = {
                                                            path: "/mock/log",
                                                            headers: true,
                                                            method: "POST",
                                                            body: {
                                                                logpath: method.logpath
                                                            },
                                                            ref: "GetLogs",
                                                            logname: "Method Logs"
                                                        }
                                                        manageAPIs(apidata)
                                                    }}>
                                                        <Typography className={typographyClasses.smallText}>Logs</Typography>
                                                    </Box>
                                                    :
                                                    null

                                            }
                                        </Box>
                                    </Box>
                                </AccordionSummary>
                                <AccordionDetails style={{ padding: 0 }}>
                                    <Box style={{ width: '100%' }} className={accordionClassess['bd' + method.methodtype]} border={1} borderTop={0}>
                                        {
                                            methodJSON.hasOwnProperty(method.methodid) === true ?
                                                <MethodTabs methodid={method.methodid} methodtype={method.methodtype} codetype={method.language} methodjson={Methods[curMethod].methodid === method.methodid ? methodJSON[method.methodid] : method} />
                                                :
                                                null
                                        }
                                    </Box>
                                </AccordionDetails>
                            </Accordion>
                        )
                    })
                }
            </Box >
        )
    }

    function copyText(key) {
        var tempIp = document.createElement('input')
        tempIp.value = key
        tempIp.style.position = 'fixed'
        tempIp.setAttribute('id', 'temp-copy')
        var root = document.getElementById('root')
        root.appendChild(tempIp)
        let copytext = document.getElementById('temp-copy')
        copytext.select()
        document.execCommand("copy");
        setMessage("Text copied")
        setSeverrity('success')
        setOpenAlert(true)
        tempIp.remove()
    }

    const handleMethoTab = (methodid, event, newValue) => {
        let methprops = methodProps
        methprops[methodid]['tab'] = newValue
        setMethodProps(methprops);
        let meths = [...Methods]
        setMethods([])
        setMethods(meths)
    };

    const MethodTabs = ({ methodid, ...props }) => {
        const { codetype, methodtype, methodjson } = props
        return (
            <div className={tabClasses.root}>
                <AppBar position="static" color="default" className={tabClasses.header}>
                    <Tabs
                        value={methodProps[methodid]['tab']}
                        onChange={handleMethoTab.bind(this, methodid)}
                        variant="scrollable"
                        scrollButtons="auto"
                        aria-label="Main auto tabs example"
                    >
                        <Tab label={<Typography className={typographyClasses.smallText}>Functions</Typography>} {...TabProps("Main", 0)} />
                        {/* <Tab label={<Typography className={typographyClasses.smallText}>Test Case</Typography>} {...TabProps("Main", 1)} /> */}
                    </Tabs>
                </AppBar>
                <TabPanel value={methodProps[methodid]['tab']} index={0}>
                    {
                        Methods.length > 0 ?
                            <FunctionEditor
                                methodid={methodid}
                                btnAction={btnAction}
                                codetype={codetype}
                                methodtype={methodtype}
                                methodJson={methodjson}
                                serviceid={match.params.serviceid}
                                productid={match.params.productid}
                                apiid={curServ.apiid}
                                versionid={versions[curVersion].versionid}
                                resourceid={resObj['resourceid']}
                                versionname={versions[curVersion].versionname}
                                datasource={methodjson['datasource']}
                            />
                            :
                            <Box></Box>
                    }
                </TabPanel>
                {/* <TabPanel value={methodProps[functionid]['tab']} index={1}>
                    <Box p={1} pt={0} pb={0}>
                        <TestCaseComp functionid={functionid} btnAction={btnAction} />
                    </Box>
                </TabPanel> */}
            </div>
        )
    }

    const handleFormDataChage = (field, event) => {
        let formFields = formData;
        if (field === "resourcename") {
            const re = /^[a-z][a-z0-9_]*[a-z0-9]*$/;
            if (re.test(event.target.value)) {
                formFields[field] = event.target.value.toLowerCase();
                setresname(event.target.value.toLowerCase())
            }
            else if (event.target.value === "") {
                formFields[field] = event.target.value.toLowerCase();
                setresname(event.target.value.toLowerCase())
            }
        }
        else if (field === "versionname") {
            const re = /^[a-z][a-z0-9_]*[a-z0-9]*$/;
            if (re.test(event.target.value)) {
                formFields[field] = event.target.value.toLowerCase();
                setvername(event.target.value.toLowerCase())
            }
            else if (event.target.value === "") {
                formFields[field] = event.target.value.toLowerCase();
                setvername(event.target.value.toLowerCase())
            }
        }
        else {
            formFields[field] = event.target.value;
            setFormData(formFields);
        }
    }

    const FormDialogSubmit = () => {
        setLoader(true)
        let canSubmit = true, requireAttrs = [], path = '', ref = ''
        if (formName === "Add Resource") {
            requireAttrs.push('resourcename')
            // requireAttrs.push('databasetype')
            path = apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/resource"
            ref = "NewResource"
        }
        else if (formName === "Add Version") {
            requireAttrs.push('versionname')
            path = apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/version"
            ref = "NewVersion"
        }
        else if (formName === "Add Action") {
            if (formData.hasOwnProperty('path') && formData.path !== "") {
                let fmdata = formData
                fmdata['path'] = "/" + fmdata['path']
                setFormData(fmdata)
            }
            requireAttrs.push('methodtype')
            path = apipath['designapi'] + "/api/" + match.params.productid + "/" + curServ.serviceid + "/" + curServ.apiid + "/" + versions[curVersion].versionid + "/" + resources[curRes] + "/method"
            ref = "NewMethod"
        }
        else if (formName === "Share Service") {
            requireAttrs.push('commitmessage')
            path = apipath['datastore'] + "/git/designpush/" + match.params.productid + "/" + match.params.serviceid
            ref = "Shareservice"
            if (curBr['Branchname'] === "") {
                setMessage('Please select branch to publish')
                setSeverrity('error')
                setOpenAlert(true)
                setLoader(false)
            }
            formData['Branchname'] = curBr['Branchname']
            formData['Name'] = curBr['Name']
        }
        for (let i = 0; i < requireAttrs.length; i++) {
            if (formData[requireAttrs[i]] === undefined || formData[requireAttrs[i]] === "" || formData[requireAttrs[i]] === []) {
                canSubmit = false
                setLoader(false)
                setMessage("Please fill the required details.")
                setSeverrity('error')
                setOpenAlert(true)
            }
        }
        if (canSubmit) {
            let apidata = {
                method: "POST",
                path: path,
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                body: formData,
                ref: ref
            }
            manageAPIs(apidata)
        }
    }

    const handleAPIInterval = (verid) => {
        interval = setInterval(function () {
            if (sessionStorage.getItem('servicestatus') !== "design" && sessionStorage.getItem('servicestatus') !== "published" && intervalFlag) {
                setResObj({})
                let apidata = {
                    method: "POST",
                    path: "/mock/describe",
                    headers: true,
                    body: {
                        serviceid: match.params.serviceid,
                        productid: match.params.productid,
                        tenantid: userData.TenantId,
                        userid: userData.EmailId
                    },
                    ref: "GetDeployDetails",
                    versionid: verid
                }
                manageAPIs(apidata)
            }
            else {
                clearInterval(interval)
            }
        }, 60000)
    }

    const handleAddMethodTrigger = (tablejson) => {
        setLoadTimeout(false)
        let apidata = {
            ref: "TriggerAutopopulateMethods",
            headers: {
                headers: true,
                apidomain: apidomain.vmurl
            },
            body: {
                servicetype: "dynamodb_eks_flask",
                tablename: tablejson.tablename,
                indexes: tablejson.indexes,
                schema: tablejson.schema,
                dbtype: "dynamodb",
                tableid: tablejson.tableid,
                dbid: tablejson.dbid
            },
            method: "POST",
            path: apipath['designapi'] + "/api/snippetmethods"
        }
        manageAPIs(apidata)
    }

    const handleMainBlock = (key) => {
        setMainBlk(key)
    }

    const ResourceOpStatus = () => {
        return (
            <Box>
                {
                    Object.keys(resObj).length > 0 ?
                        <Box display="flex" alignItems="center">
                            {
                                resourceStatus !== "" && resourceStatus !== "Stopping" ?
                                    <Box display="flex" alignItems="center">
                                        <Box mr={1} style={{ cursor: 'pointer' }} borderRadius={16} height={28} p={0.5} pl={2} pr={2} className={statusClasses[resourceStatus]}>
                                            <Typography className={`${typographyClasses.root} ${typographyClasses.smallText}`}>Resource Status - {resourceStatus}</Typography>
                                        </Box>
                                        <Box style={{ cursor: 'pointer', borderColor: 'gray' }} border={1} mr={1} height={28} p={0.5} pl={2} pr={2} onClick={(e) => {
                                            e.stopPropagation()
                                            setLoader(true)
                                            let apidata = {
                                                path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid + "/output",
                                                headers: {
                                                    headers: true,
                                                    apidomain: apidomain.vmurl
                                                },
                                                method: "GET",
                                                body: {},
                                                ref: "GetLiveResOP",
                                                formname: "Resource Outputs",
                                                show: true,
                                                key: "Resource",
                                                resjson: resObj,
                                                reslist: resources,
                                                versionid: versions[curVersion].versionid
                                            }
                                            manageAPIs(apidata)
                                        }}>
                                            <Typography className={typographyClasses.smallText}>Output</Typography>
                                        </Box>
                                        <Box style={{ cursor: 'pointer', borderColor: 'gray' }} border={1} mr={1} height={28} p={0.5} pl={2} pr={2} onClick={(e) => {
                                            e.stopPropagation()
                                            setLoader(true)
                                            setLoader(true)
                                            let apidata = {
                                                path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid + "/logs",
                                                headers: {
                                                    headers: true,
                                                    apidomain: apidomain.vmurl
                                                },
                                                method: "GET",
                                                body: {},
                                                ref: "GetLiveResLogs",
                                                formname: "Resource Logs",
                                                show: true,
                                                key: "Resource",
                                                resjson: resObj,
                                                reslist: resources
                                            }
                                            manageAPIs(apidata)
                                        }}>
                                            <Typography className={typographyClasses.smallText}>Logs</Typography>
                                        </Box>
                                    </Box>
                                    :
                                    null
                            }
                        </Box>
                        :
                        null
                }
            </Box>
        )
    }

    const CheckResourceForLive = () => {
        if (sessionStorage.getItem('isDBLive') && sessionStorage.getItem('isDBLive') === "yes") {
            if (sessionStorage.getItem('isTableLive') && (sessionStorage.getItem('isTableLive') === "yes" || sessionStorage.getItem('isTableLive') !== "Stopping")) {
                if (Methods.length > 0) {
                    let apidata = {
                        ref: "GetLiveResOP",
                        path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid + "/output",
                        method: "GET",
                        headers: {
                            headers: true,
                            apidomain: apidomain.vmurl
                        },
                        body: {},
                        formname: "Resource Outputs",
                        show: false,
                        key: "CRLive",
                        resjson: resObj,
                        reslist: resources,
                        versionid: versions[curVersion].versionid
                    }
                    manageAPIs(apidata)
                }
                else {
                    setMessage('No actions are there, please add atleast one action.')
                    setSeverrity('info')
                    setOpenAlert(true)
                }
            }
            else {
                setMessage('One or more Tables are not in running state')
                setSeverrity('info')
                setOpenAlert(true)
            }
        }
        else {
            setMessage('No DB are in running state.')
            setSeverrity('info')
            setOpenAlert(true)
        }
    }

    const handleBranchesPopover = (br) => {
        setCurBr(br)
        setShowBranches(false)
    }

    const PopulateBranches = () => {
        return (
            <Box display="flex" style={{ width: '100%' }} mt={1}>
                <Box display="flex" flexDirection="column" alignItems="center" style={{ width: '100%', border: '1px solid gray' }} position="relative">
                    <Box onClick={() => { setShowBranches(!showBranches) }} display="flex" alignItems="center" justifyContent="space-between" style={{ width: '100%', cursor: "pointer" }} p={1}>
                        <Box mr={1} display="flex" alignItems="center">
                            <Box display="flex" flexDirection="column" alignItems="flex-start" >
                                <Box>
                                    <Typography className={`${typographyClasses.smallText} ${typographyClasses.bold}`}>{curBr['Branchname']}</Typography>
                                </Box>
                                <Box>
                                    <Typography color="inherit" className={`${typographyClasses.smallText} ${typographyClasses.bold}`}>{curBr['Name']}</Typography>
                                </Box>
                            </Box>
                        </Box>
                        <Box display="flex">
                            {
                                showBranches === true ?
                                    <ArrowDropUpIcon fontSize="small" />
                                    :
                                    <ArrowDropDownIcon fontSize="small" />
                            }
                        </Box>
                    </Box>
                    {
                        showBranches === true ?
                            <Box height={250} style={{ width: '100%', borderTop: '1px solid gray' }}>
                                <Box position="relative" style={{ height: '100%' }}>
                                    <Box overflow="auto" height={210}>
                                        {
                                            branches.length > 0 ?
                                                branches.map((br, index) => {
                                                    return (
                                                        <Box key={index} display="flex" alignItems="center" borderBottom={1} style={{ borderColor: 'lightgray' }} justifyContent="space-between" p={1} bgcolor={curBr.Branchname === br.Branchname ? "#e6e8f3" : ""} className={`${sideBarClasses.root} ${sideBarClasses.onhoveritem}`} onClick={() => {
                                                            handleBranchesPopover(br)
                                                        }}>
                                                            <Box display="flex" flexDirection="column" alignItems="flex-start">
                                                                <Typography className={`${typographyClasses.smallText}`} color={curBr.Branchname === br.Branchname ? "primary" : "inherit"}>{br['Branchname']}</Typography>
                                                                <Typography className={typographyClasses.smallText} color={curBr.Branchname === br.Branchname ? "primary" : "inherit"}>{br['Name']}</Typography>
                                                            </Box>
                                                        </Box>
                                                    )
                                                })
                                                :
                                                null
                                        }
                                    </Box>
                                </Box>
                            </Box>
                            :
                            null
                    }
                </Box>
            </Box>
        )
    }

    return (
        <div>
            {
                loader === true ?
                    <Loader type="page" open={true} />
                    :
                    null
            }
            {
                Object.keys(curServ).length > 0 ?
                    <Helmet>
                        <title>Services - {curServ.servicename}</title>
                    </Helmet>
                    :
                    null
            }
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <TopNav />
            <Container className={containerClasses.root}>
                <Box style={{ width: '100%' }}>
                    <Box display="flex" justifyContent="space-between" alignItems="center" borderBottom={1} style={{ borderColor: 'lightgray' }} p={1}>
                        <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
                            <Box
                                style={{ cursor: "pointer" }}
                                onClick={() => {
                                    sessionStorage.removeItem('CurrentProduct')
                                    sessionStorage.removeItem('Services')
                                    sessionStorage.removeItem('CurrentService')
                                    DataList.ActiveService = {}
                                    DataList.ActiveProduct = {}
                                    DataList.ServiceList = []
                                    window.location.href = window.location.origin + "/#/app/products"
                                }}
                            >
                                <Typography className={`${typographyClasses.text} ${'text-design'} ${typographyClasses.bold}`}>Products</Typography>
                            </Box>
                            <Typography className={typographyClasses.smallText}>{match.params.productid}</Typography>
                            <Box
                                style={{ cursor: "pointer" }}
                                onClick={() => {
                                    sessionStorage.removeItem('Services')
                                    sessionStorage.removeItem('CurrentService')
                                    DataList.ActiveService = {}
                                    DataList.ServiceList = []
                                    window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services"
                                }}
                            >
                                <Typography className={`${typographyClasses.smallText} ${'text-design'} ${typographyClasses.bold}`}>Services</Typography>
                            </Box>
                            <Typography className={typographyClasses.smallText}>{match.params.serviceid}</Typography>
                            <Typography className={typographyClasses.smallText}>API</Typography>
                            {
                                versions.length > 0 ?
                                    <Typography className={typographyClasses.smallText}>{versions[curVersion].versionname}</Typography>
                                    :
                                    null
                            }
                            {
                                resources.length > 0 ?
                                    <Typography className={typographyClasses.smallText}>{resources[curRes]}</Typography>
                                    :
                                    null
                            }
                        </Breadcrumbs>
                        <Box display="flex">
                            <Box mr={1}>
                                <Button
                                    variant="text"
                                    color="primary"
                                    onClick={handleActions.bind(this, { "Action": "refresh", key: "api" })}
                                    disabled={versions.length > 0 ? false : true}
                                >
                                    <RefreshIcon className={typographyClasses.text} />
                                </Button>
                            </Box>
                            <Box mr={1}>
                                <Button
                                    variant="contained"
                                    className="trigger-btn"
                                    color="primary"
                                    onClick={() => {
                                        setLoader(true)
                                        let apidata1 = {
                                            ref: "GetBranchesByCategory",
                                            cat: "shared",
                                            body: {},
                                            path: apipath['datastore'] + "/git/getbranch/sharedbranch",
                                            headers: {
                                                headers: true,
                                                apidomain: apidomain.vmurl
                                            }
                                        }
                                        manageAPIs(apidata1)
                                    }}
                                >
                                    Share Service
                                </Button>
                            </Box>
                            {/* {
                                servstatus !== "deploying" ?
                                    <Box p={0.5} pl={2} pr={2} height={28} mr={1} className={statusClasses[servstatus]} borderRadius={16}>
                                        <Typography className={`${typographyClasses.root} ${typographyClasses.smallText}`}>Service Status - {statemap[servstatus]}</Typography>
                                    </Box>
                                    :
                                    <Box>
                                        {
                                            servstatus === "deploying" ?
                                                <Box p={0.5} pl={2} pr={2} height={28} mr={1} className={statusClasses[servstatus]} borderRadius={16} display="flex" alignItems="center">
                                                    <Box display="flex" mr={0.5}>
                                                        <Typography className={`${typographyClasses.root} ${typographyClasses.smallText}`}>Service Status - {statemap[servstatus]}</Typography>
                                                    </Box>
                                                    <Box display="flex">
                                                        <CircularProgress size={12} />
                                                    </Box>
                                                </Box>
                                                :
                                                null
                                        }
                                    </Box>
                            } */}
                            {
                                servstatus === "design" ?
                                    Methods.length > 0 ?
                                        <Box display="flex" alignItems="center">
                                            <Box>
                                                <Button
                                                    title={isGitConfigured === true ? "Publish Design" : "Add Code Repo to publish"}
                                                    variant="contained"
                                                    color="primary"
                                                    className="trigger-btn"
                                                    onClick={() => {
                                                        setLoader(true)
                                                        manageAPIs({
                                                            path: apipath['publish'] + "/createdeploy/" + match.params.productid + "/" + match.params.serviceid,
                                                            ref: "PublishDesign",
                                                            method: "POST",
                                                            headers: {
                                                                headers: true,
                                                                apidomain: apidomain.vmurl
                                                            },
                                                            body: {
                                                                servicetype: curServ.servicetype
                                                            }
                                                        })
                                                    }}
                                                    disabled={!isGitConfigured}
                                                >
                                                    <PublishIcon className={typographyClasses.text} />&nbsp;&nbsp;Publish Design
                                                </Button>
                                            </Box>
                                        </Box>
                                        :
                                        null
                                    :
                                    null
                            }
                            {/* {
                                resourceStatus === "Running" ?
                                    <Box>
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            className="trigger-btn"
                                            onClick={() => {
                                                window.location.href = window.location.origin + "/#/app/products/" + match.params.productid + "/services/" + match.params.serviceid + "/deploy"
                                            }}
                                        >
                                            <DeviceHubIcon className={typographyClasses.text} />&nbsp;&nbsp;Deploy
                                        </Button>
                                    </Box>
                                    :
                                    null
                            } */}
                            {/* {
                                servstatus === "published" ?
                                    <Box>
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            className="trigger-btn"
                                            onClick={() => {
                                                handleActions({
                                                    Action: "confirmation",
                                                    confirmText: "Are you sure you want to start this deployment process?",
                                                    apidata: {
                                                        path: "/mock/deploy",
                                                        method: "POST",
                                                        ref: "DeployCode",
                                                        headers: true,
                                                        body: {
                                                            serviceid: match.params.serviceid,
                                                            productid: match.params.productid,
                                                            tenantid: userData.TenantId,
                                                            userid: userData.EmailId
                                                        }
                                                    }
                                                })
                                            }}
                                        >
                                            <DeviceHubIcon className={typographyClasses.text} />&nbsp;&nbsp;Deploy
                                        </Button>
                                    </Box>
                                    :
                                    null
                            } */}
                            {
                                servstatus === "published" ?
                                    <Box ml={1}>
                                        <Button
                                            variant="contained"
                                            color="primary"
                                            className="trigger-btn"
                                            onClick={() => {
                                                setLoader(true)
                                                manageAPIs({
                                                    path: apipath['designapi'] + "/service/" + match.params.productid + "/" + match.params.serviceid + "/updatestatus",
                                                    ref: "DesignService",
                                                    method: "POST",
                                                    headers: {
                                                        headers: true,
                                                        apidomain: apidomain.vmurl
                                                    },
                                                    body: {
                                                        servicetype: curServ.servicetype
                                                    }
                                                })
                                            }}
                                        >
                                            <MemoryIcon className={typographyClasses.text} />&nbsp;&nbsp;Design
                                        </Button>
                                    </Box>
                                    :
                                    null
                            }
                            {
                                servstatus === "running" || servstatus === "failed" || servstatus === "deploying" ?
                                    <Box ml={1}>
                                        <Button
                                            variant="outlined"
                                            color="secondary"
                                            onClick={() => {
                                                handleActions({
                                                    Action: "confirmation",
                                                    confirmText: "Are you sure you want to delete this deployment process?",
                                                    apidata: {
                                                        method: "POST",
                                                        path: "/mock/delete",
                                                        headers: true,
                                                        body: {
                                                            serviceid: match.params.serviceid,
                                                            productid: match.params.productid,
                                                            tenantid: userData.TenantId,
                                                            userid: userData.EmailId
                                                        },
                                                        ref: "DeleteDeploy"
                                                    }
                                                })
                                            }}
                                        >
                                            <DeleteIcon className={typographyClasses.text} />&nbsp;&nbsp;Delete Mock
                                        </Button>
                                    </Box>
                                    :
                                    null
                            }
                        </Box>
                    </Box>
                    <Box style={{ width: '100%' }}>
                        <Box display="flex" alignItems="center" style={{ width: '100%' }}>
                            {
                                showSidebar === false ?
                                    <Box style={{ height: 'calc(100vh - 120px)' }} bgcolor="#FAFBFC" width={40}>
                                        <Box display="flex" alignItems="center" style={{ width: '100%', cursor: 'pointer' }} onClick={() => setShowSideBar(true)} p={1} justifyContent="center">
                                            <MenuIcon className={`${typographyClasses.h5}`} />
                                        </Box>
                                    </Box>
                                    :
                                    null
                            }
                            <Box style={{ width: '100%' }}>
                                <Grid container>
                                    {
                                        showSidebar === true ?
                                            <Grid item xs={2}>
                                                <Box bgcolor="#FAFBFC">
                                                    <Box p={1} borderBottom={1} style={{ borderColor: 'lightgray' }}>
                                                        <Box display="flex" alignItems="center" justifyContent="space-between">
                                                            <Box display='flex'>
                                                                <Typography className={`${typographyClasses.text} ${typographyClasses.bold}`}>API</Typography>
                                                            </Box>
                                                            <Box display='flex' style={{ cursor: 'pointer' }} onClick={(e) => {
                                                                e.stopPropagation()
                                                                setShowSideBar(false)
                                                            }}>
                                                                <Clear className={`${typographyClasses.h5}`} />
                                                            </Box>
                                                        </Box>
                                                    </Box>
                                                    <Box style={{ height: 'calc(100vh - 180px)' }} position="relative" pl={1} pr={1} pt={1} pb={1}  >
                                                        <Box >
                                                            <Sidebar />
                                                        </Box>
                                                    </Box>
                                                </Box>
                                            </Grid>
                                            :
                                            null
                                    }
                                    <Grid item xs={showSidebar === true ? 10 : 12}>
                                        <Box style={{ height: 'calc(100vh - 120px)' }} boxShadow={2}>
                                            <Box p={0.5} pl={1} pr={1} borderBottom={1} style={{ borderColor: 'lightgray' }} height={41}>
                                                <Box display="flex" alignItems="center" justifyContent="space-between" style={{ height: '100%' }}>
                                                    <ResourceOpStatus />
                                                    {
                                                        deployId !== "" ?
                                                            <Box display="flex" p={0.5} pl={2} pr={2} bgcolor="#fafafa" alignItems="center">
                                                                <Typography className={typographyClasses.smallText}>Deploy Id - {deployId}</Typography>
                                                                <Box ml={0.5} display="flex" style={{ cursor: 'pointer' }} onClick={(e) => {
                                                                    e.stopPropagation()
                                                                    copyText(deployId)
                                                                }}>
                                                                    <FileCopyIcon fontSize="small" style={{ fontSize: '0.8rem' }} />
                                                                </Box>
                                                            </Box>
                                                            :
                                                            null
                                                    }
                                                    <Box display="flex" alignItems="center">
                                                        {
                                                            mainBlk === "actions" ?
                                                                <Box mr={1}>
                                                                    <Button
                                                                        variant="text"
                                                                        color="primary"
                                                                        onClick={handleActions.bind(this, { "Action": "refresh", key: "resource" })}
                                                                        disabled={resources.length > 0 ? false : true}
                                                                    >
                                                                        <RefreshIcon className={typographyClasses.text} />
                                                                    </Button>
                                                                </Box>
                                                                :
                                                                null
                                                        }
                                                        {
                                                            liveInterval === false && resources.length > 0 && resourceStatus === "" && delInterval === false ?
                                                                <Box mr={1} p={1} height={28} color="white" display="flex" alignItems="center" justifyContent="center" style={{ backgroundImage: 'linear-gradient(45deg, #05877b, #4caf50)', cursor: 'pointer' }} pr={2} pl={2} onClick={CheckResourceForLive}>
                                                                    <Box mr={0.5} display="flex">
                                                                        <PlayArrowIcon className={typographyClasses.text} />
                                                                    </Box>
                                                                    <Box display="flex">
                                                                        <Typography className={typographyClasses.smallText}>Live Resource</Typography>
                                                                    </Box>
                                                                </Box>
                                                                :
                                                                null
                                                        }
                                                        {
                                                            liveInterval === false && resources.length > 0 && delInterval === false && resourceStatus !== "Initiated" && resourceStatus !== "" && btnAction === false ?
                                                                <Box mr={1} p={1} height={28} color="white" display="flex" alignItems="center" justifyContent="center" style={{ backgroundImage: 'linear-gradient(45deg, #05877b, #4caf50)', cursor: 'pointer' }} pr={2} pl={2} onClick={CheckResourceForLive}>
                                                                    <Box mr={0.5} display="flex">
                                                                        <PlayArrowIcon className={typographyClasses.text} />
                                                                    </Box>
                                                                    <Box display="flex">
                                                                        <Typography className={typographyClasses.smallText}>Update Resource</Typography>
                                                                    </Box>
                                                                </Box>
                                                                :
                                                                null
                                                        }
                                                        {
                                                            Object.keys(resObj).length > 0 ?
                                                                resourceStatus !== "" && delInterval === false && resourceStatus !== "Initiated" ?
                                                                    <Box mr={1} p={1} height={28} color="white" display="flex" alignItems="center" justifyContent="center" style={{ backgroundImage: 'linear-gradient(45deg, #f44336, #d10147)', cursor: 'pointer' }} pr={2} pl={2} onClick={() => {
                                                                        handleActions({
                                                                            Action: "confirmation",
                                                                            confirmText: "Are you sure you want to stop this process?",
                                                                            apidata: {
                                                                                path: apipath['liveapi'] + "/resource/" + match.params.serviceid + "/" + versions[curVersion].versionid + "/" + resObj.resourceid,
                                                                                headers: {
                                                                                    headers: true,
                                                                                    apidomain: apidomain.vmurl
                                                                                },
                                                                                method: "DELETE",
                                                                                body: {},
                                                                                ref: "DeleteLiveRes",
                                                                            }
                                                                        })
                                                                    }}>
                                                                        <Box mr={0.5} display="flex">
                                                                            <StopIcon className={typographyClasses.text} />
                                                                        </Box>
                                                                        <Box display="flex">
                                                                            <Typography className={typographyClasses.smallText}>Stop Resource</Typography>
                                                                        </Box>
                                                                    </Box>
                                                                    :
                                                                    null
                                                                :
                                                                null
                                                        }
                                                        {
                                                            liveInterval === true && (resourceStatus === "" || resourceStatus === "Initiated") ?
                                                                <Box p={0.5} pl={2} pr={2} height={28} mr={1} className={statusClasses["deploying"]} borderRadius={16} display="flex" alignItems="center">
                                                                    <Box display="flex" mr={0.5}>
                                                                        <Typography className={`${typographyClasses.root} ${typographyClasses.smallText}`}>Deploying</Typography>
                                                                    </Box>
                                                                    <Box display="flex">
                                                                        <CircularProgress size={12} />
                                                                    </Box>
                                                                </Box>
                                                                :
                                                                null
                                                        }
                                                        {
                                                            resourceStatus === "Stopping" && delInterval === true ?
                                                                <Box p={0.5} pl={2} pr={2} height={28} mr={1} className={statusClasses['failed']} borderRadius={16} display="flex" alignItems="center">
                                                                    <Box display="flex" mr={0.5}>
                                                                        <Typography className={`${typographyClasses.root} ${typographyClasses.smallText}`}>Stopping</Typography>
                                                                    </Box>
                                                                    <Box display="flex">
                                                                        <CircularProgress size={12} />
                                                                    </Box>
                                                                </Box>
                                                                :
                                                                null
                                                        }
                                                        <Box>
                                                            <Button
                                                                variant="outlined"
                                                                color="primary"
                                                                onClick={() => {
                                                                    if (mainBlk === "actions") {
                                                                        setMainBlk('testcase')
                                                                    }
                                                                    else if (mainBlk === "testcase") {
                                                                        setMainBlk('actions')
                                                                    }
                                                                }}
                                                                disabled={resources.length > 0 ? false : true}
                                                            >
                                                                {mainBlk === "actions" ? "Test Cases" : "Methods"}
                                                            </Button>
                                                        </Box>
                                                    </Box>
                                                </Box>
                                            </Box>
                                            {
                                                mainBlk === "actions" ?
                                                    <Box style={{ height: 'calc(100vh - 161px)' }} overflow="auto">
                                                        <Box mb={2}>
                                                            <Box p={1} pb={0} display="flex" justifyContent="space-between" alignItems="center">
                                                                <Box display="flex">
                                                                    <Typography className={`${typographyClasses.text} ${typographyClasses.bold}`}>Actions</Typography>
                                                                </Box>
                                                                <Box>
                                                                    <Box display="flex" style={{ height: '100%' }}>
                                                                        <Box>
                                                                            <Button
                                                                                variant="contained"
                                                                                color="primary"
                                                                                className="trigger-btn"
                                                                                onClick={() => {
                                                                                    setFormName('Add Action')
                                                                                    setFormDialog(true)
                                                                                }}
                                                                                disabled={btnAction === false ? (resources.length > 0 ? false : true) : true}
                                                                            >
                                                                                <AddIcon className={typographyClasses.smallText} />&nbsp;Action
                                                                        </Button>
                                                                        </Box>
                                                                    </Box>
                                                                </Box>
                                                            </Box>
                                                            <Box style={{ height: 'calc(100% - 41px)' }} p={1} overflow="auto">
                                                                {
                                                                    Methods.length > 0 ?
                                                                        <PopulateMethods />
                                                                        :
                                                                        <Box p={1}>
                                                                            {
                                                                                loadTimeout === false ?
                                                                                    <Loader type="block" />
                                                                                    :
                                                                                    <Typography className={typographyClasses.smallText}>No Actions</Typography>
                                                                            }
                                                                        </Box>
                                                                }
                                                            </Box>
                                                        </Box>
                                                        <Box borderTop={1} style={{ borderColor: 'lightgray' }}>
                                                            <Box p={1} display="flex" pb={0}>
                                                                <Typography className={`${typographyClasses.text} ${typographyClasses.bold}`}>Databases</Typography>
                                                            </Box>
                                                            <Box p={1} pb={0}>
                                                                <Box style={{ width: '100%', height: '100%', overflow: 'auto', borderColor: 'lightgray' }} p={1} border={1}>
                                                                    {
                                                                        resources.length > 0 ?
                                                                            Object.keys(resObj).length > 0 ?
                                                                                curServ.hasOwnProperty('status') === false || servstatus === "design" || servstatus === "published" ?
                                                                                    <TableDetails resourceid={resObj['resourceid']} btnAction={btnAction} callapi={true} serviceid={match.params.serviceid} versionid={versions[curVersion].versionid} trigger={handleAddMethodTrigger} />
                                                                                    :
                                                                                    null
                                                                                :
                                                                                <Box p={1}>
                                                                                    {
                                                                                        loadTimeout === false ?
                                                                                            <Loader type="block" />
                                                                                            :
                                                                                            <Typography className={typographyClasses.smallText}>No Database</Typography>
                                                                                    }
                                                                                </Box>
                                                                            :
                                                                            <Box p={1}>
                                                                                {
                                                                                    loadTimeout === false ?
                                                                                        <Loader type="block" />
                                                                                        :
                                                                                        <Typography className={typographyClasses.smallText}>No Resources</Typography>
                                                                                }
                                                                            </Box>
                                                                    }
                                                                </Box>
                                                            </Box>
                                                        </Box>
                                                    </Box>
                                                    :
                                                    null
                                            }
                                            {
                                                mainBlk === "testcase" ?
                                                    <Box style={{ height: 'calc(100vh - 161px)' }} overflow="auto" boxShadow={2} >
                                                        {
                                                            resources.length > 0 && Methods.length > 0 ?
                                                                Object.keys(resObj).length > 0 ?
                                                                    <TestCaseComp
                                                                        btnAction={btnAction}
                                                                        methods={Methods}
                                                                        resourceid={resources[curRes]}
                                                                        mainBlkTrigger={handleMainBlock}
                                                                        resourceStatus={resourceStatus}
                                                                        resourcename={resObj.resourcename}
                                                                        versionname={versions[curVersion].versionname}
                                                                        serviceid={match.params.serviceid}
                                                                        apiid={curServ.apiid}
                                                                        productid={match.params.productid}
                                                                        versionid={versions[curVersion].versionid}
                                                                    />
                                                                    :
                                                                    null
                                                                :
                                                                <Box p={1}>
                                                                    <Typography className={typographyClasses.smallText}>No Resources</Typography>
                                                                </Box>
                                                        }
                                                    </Box>
                                                    :
                                                    null
                                            }
                                        </Box>
                                    </Grid>
                                </Grid>
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Container>
            <Dialog
                open={formDialog}
                onClose={() => setFormDialog(false)}
                aria-labelledby="acc-dialog-title"
                fullWidth={true}
                maxWidth={formName.indexOf('Outputs') !== -1 || formName === "Status" || formName.indexOf('Logs') !== -1 ? "lg" : "sm"}
            >
                <DialogTitle id="acc-dialog-title" style={{ borderBottom: '1px solid lightgrey' }}>{formName === "Status" ? "Status - " + curState : formName}</DialogTitle>
                <DialogContent>
                    <Box style={{ width: '100%' }}>
                        {
                            formName.indexOf('Outputs') !== -1 || formName === "Status" || formName.indexOf('Logs') !== -1 ?
                                <Box>
                                    {
                                        formName.indexOf('Logs') !== -1 ?
                                            Object.keys(jsontohtml).length > 0 ?
                                                <Box className="json-to-html">
                                                    <JSONToHTMLTable data={jsontohtml} tableClassName="table table-sm" />
                                                </Box>
                                                :
                                                <Box>
                                                    <Typography>No Logs</Typography>
                                                </Box>
                                            :
                                            null
                                    }
                                    {
                                        formName.indexOf('Outputs') !== -1 ?
                                            Object.keys(jsontohtml).length > 0 ?
                                                <Box className="json-to-html">
                                                    <JSONToHTMLTable data={jsontohtml} tableClassName="table table-sm" />
                                                </Box>
                                                :
                                                <Box>
                                                    <Typography>No Outputs</Typography>
                                                </Box>
                                            :
                                            null
                                    }
                                    {
                                        formName === "Status" ?
                                            Object.keys(jsontohtml).length > 0 ?
                                                <Box className="json-to-html">
                                                    <JSONToHTMLTable data={jsontohtml} tableClassName="table table-sm" />
                                                </Box>
                                                :
                                                <Box>
                                                    <Typography>No Status</Typography>
                                                </Box>
                                            :
                                            null
                                    }
                                </Box>
                                :
                                <form autoComplete="off">
                                    {
                                        formName === "Add Resource" ?
                                            <Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="ResourceName" required>Resource Name</label>
                                                        <TextField
                                                            id="ResourceName"
                                                            required
                                                            value={resname}
                                                            fullWidth
                                                            placeholder="Enter Resource Name"
                                                            onChange={handleFormDataChage.bind(this, "resourcename")}
                                                            variant="outlined"
                                                            helperText="No capital letters,special characters except underscore is allowed."
                                                        >
                                                        </TextField>
                                                    </FormControl>
                                                </Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="Description">Description</label>
                                                        <TextField
                                                            id="Description"
                                                            required
                                                            fullWidth
                                                            placeholder="Enter Description"
                                                            onChange={handleFormDataChage.bind(this, "description")}
                                                            variant="outlined"
                                                            multiline
                                                            rows={3}
                                                        >
                                                        </TextField>
                                                    </FormControl>
                                                </Box>
                                                {/* <Box>
                                                    <FormControl fullWidth>
                                                        <label required>Database Type</label>
                                                        <Select
                                                            native
                                                            variant="outlined"
                                                            fullWidth
                                                            onChange={handleFormDataChage.bind(this, "databasetype")}
                                                        >
                                                            <option value="">Select Type</option>
                                                            <option value="dynamodb">Dynamo DB</option>
                                                        </Select>
                                                    </FormControl>
                                                </Box> */}
                                            </Box>
                                            :
                                            null
                                    }
                                    {
                                        formName === "Add Version" ?
                                            <Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="VersionName" required>Version Name</label>
                                                        <TextField
                                                            id="VersionName"
                                                            required
                                                            value={vername}
                                                            fullWidth
                                                            placeholder="Enter Version Name"
                                                            onChange={handleFormDataChage.bind(this, "versionname")}
                                                            variant="outlined"
                                                            helperText="No capital letters,special characters except underscore is allowed."
                                                        >
                                                        </TextField>
                                                    </FormControl>
                                                </Box>
                                                <Box>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="Description">Description</label>
                                                        <TextField
                                                            id="Description"
                                                            required
                                                            fullWidth
                                                            placeholder="Enter Description"
                                                            onChange={handleFormDataChage.bind(this, "description")}
                                                            variant="outlined"
                                                            multiline
                                                            rows={3}
                                                        >
                                                        </TextField>
                                                    </FormControl>
                                                </Box>
                                            </Box>
                                            :
                                            null
                                    }
                                    {
                                        formName === "Add Action" ?
                                            <Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label required>Method Type</label>
                                                        <Select
                                                            native
                                                            variant="outlined"
                                                            fullWidth
                                                            onChange={handleFormDataChage.bind(this, "methodtype")}
                                                        >
                                                            <option value="">Select Type</option>
                                                            <option value="POST">POST</option>
                                                            <option value="GET">GET</option>
                                                            <option value="PUT">PUT</option>
                                                            <option value="DELETE">DELETE</option>
                                                            <option value="UPDATE">UPDATE</option>
                                                        </Select>
                                                    </FormControl>
                                                </Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="Path">Path</label>
                                                        <OutlinedInput
                                                            id="Path"
                                                            required
                                                            fullWidth
                                                            placeholder="Enter Path"
                                                            onChange={handleFormDataChage.bind(this, "path")}
                                                            variant="outlined"
                                                            startAdornment={<InputAdornment position="start">/</InputAdornment>}
                                                        >
                                                        </OutlinedInput>
                                                    </FormControl>
                                                </Box>
                                            </Box>
                                            :
                                            null
                                    }
                                    {
                                        formName === "Share Service" ?
                                            <Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="branch" required>Branch to Share</label>
                                                        <PopulateBranches />
                                                    </FormControl>
                                                </Box>
                                                <Box mb={1}>
                                                    <FormControl fullWidth>
                                                        <label htmlFor="Commit" required>Commit Message</label>
                                                        <TextField
                                                            id="Commit"
                                                            required
                                                            fullWidth
                                                            placeholder="Enter Commit Message"
                                                            onChange={handleFormDataChage.bind(this, "commitmessage")}
                                                            variant="outlined"
                                                            multiline
                                                            rows={2}
                                                        >
                                                        </TextField>
                                                    </FormControl>
                                                </Box>
                                            </Box>
                                            :
                                            null
                                    }
                                </form>
                        }
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => {
                        setFormDialog(false)
                        setCurstate("")
                        setJsontoHtml({})
                    }} color="secondary" variant="contained">
                        {formName.indexOf('Outputs') !== -1 || formName === "Status" || formName.indexOf('Logs') !== -1 ? "Close" : "Cancel"}
                    </Button>
                    {
                        formName.indexOf('Outputs') === -1 && formName !== "Status" && formName.indexOf('Logs') === -1 ?
                            <Button onClick={FormDialogSubmit} color="primary" variant="contained" className="api-btn">
                                Add
                            </Button>
                            :
                            null
                    }
                </DialogActions>
            </Dialog>
            <Footer page="app" />
            {
                confirmation !== '' ?
                    <Confirm confirmText={confirmation} clickHandler={handleConfirmation} />
                    :
                    null
            }
        </div >
    );
}

export default ServiceDetails;

