import HandleAPI from '../../Utils/HandleApi';
import DataList from '../../Utils/DataList';


function GetAllProducts(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                if (result.body.length > 0) {
                    DataList.ProductList = result.body
                    sessionStorage.setItem('Products', window.btoa(JSON.stringify(result.body)))
                    asyncfunction(true, { data: result.body })
                }
                else {
                    asyncfunction(true, {data: [],error:true })
                }
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

function GetSpecificProduct(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                DataList.ActiveProduct = result.body
                sessionStorage.setItem('CurrentProduct', window.btoa(JSON.stringify(result.body)))
                asyncfunction(true, { data: result.body })
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

function AddProduct(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
    .then(result => {
        if (result === null) {
            asyncfunction(false, { message: "Some internal error,plese try again" })
        }
        else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
            DataList.ActiveProduct = result.body
            sessionStorage.setItem('CurrentProduct', window.btoa(JSON.stringify(result.body)))
            asyncfunction(true, { data: result.body,message:result.hasOwnProperty('message') === true ? result.message : "" })
        }
        else {
            asyncfunction(false, { message: result.errorMessage })
        }
    })
    .catch(err => {
        asyncfunction(false, { message: "Some internal error, please try after some time" })
    })
}

function DeleteProduct(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                asyncfunction(true, { data: result.body, message: result.hasOwnProperty('message') ? result.message : "" })
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

export default { GetAllProducts, GetSpecificProduct, AddProduct,DeleteProduct } 