import HandleAPI from '../../Utils/HandleApi';
import DataList from '../../Utils/DataList';


function GetAllServices(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                if (result.body.length > 0) {
                    DataList.ServiceList = result.body
                    sessionStorage.setItem('Services', window.btoa(JSON.stringify(result.body)))
                    asyncfunction(true, { data: result.body })
                }
                else {
                    asyncfunction(true, { data: [], error: true })
                }
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

function GetSpecificService(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                DataList.ActiveService = result.body
                sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(result.body)))
                window.location.href = window.location.origin + "/#/app/services"
                asyncfunction(true, { data: result.body })
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

function AddService(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                DataList.ActiveService = result.body
                sessionStorage.setItem('CurrentService', window.btoa(JSON.stringify(result.body)))
                asyncfunction(true, { data: result.body, message: result.hasOwnProperty('message') ? result.message : ""  })
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

function DeleteService(apidata, asyncfunction) {
    HandleAPI.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
        .then(result => {
            if (result === null) {
                asyncfunction(false, { message: "Some internal error,plese try again" })
            }
            else if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                asyncfunction(true, { data: result.body, message: result.hasOwnProperty('message') ? result.message : "" })
            }
            else {
                asyncfunction(false, { message: result.errorMessage })
            }
        })
        .catch(err => {
            asyncfunction(false, { message: "Some internal error, please try after some time" })
        })
}

export default { GetAllServices, GetSpecificService, AddService, DeleteService }