import React, { useState } from 'react';

//Material UI
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Popover from '@material-ui/core/Popover';
import Divider from '@material-ui/core/Divider';

//Custom Component
import DataList from '../../Utils/DataList';
import Loader from '../../Components/Loader';
import Alert from '../../Components/Alert'
import ProductAPIs from './ProductAPIs';
import AddProduct from './AddProduct';
import Confirm from '../../Components/Confirm';
import apidomain from '../../Utils/ApiDomain';
import apipath from '../../Utils/Apipath';
import GitAPIs from '../Git/GitAPIs';

//Material Icons
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';

// Styles
import TypographyStyles from '../../assets/styles/TypographySyles';

const ProductIndex = () => {
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loader, setLoader] = useState(false)
    const [confirmation, setConfirmation] = useState('')
    const [confApiData, setConfApiData] = useState({})
    const [productList, setProductList] = useState(DataList.ProductList.length > 0 ? DataList.ProductList : (sessionStorage.getItem('Products') ? JSON.parse(window.atob(sessionStorage.getItem('Products'))) : []))
    const [newProd, setNewProd] = useState(false)
    const [curProd, setCurProd] = useState(Object.keys(DataList.ActiveProduct).length > 0 ? DataList.ActiveProduct : (sessionStorage.getItem('CurrentProduct') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentProduct'))) : {}))
    const [anchorEl, setAnchorEl] = useState(null);
    const openPopover = Boolean(anchorEl);
    const id = openPopover ? 'simple-popover' : undefined;

    const typographyClasses = TypographyStyles()


    const handleAlert = () => {
        setOpenAlert(false)
    }

    const manageAPIs = (apidata) => {
        if (apidata.ref === "GetGitCredentials") {
            GitAPIs.GetGitDetails(apidata, function (success, response) {
                if (success) {
                    if (Object.keys(response.data).length > 0) {
                        setAnchorEl(null)
                        setNewProd(true)
                    }
                    else {
                        setMessage("Configure Git create a product")
                        setSeverrity('error')
                        setOpenAlert(true)
                    }
                }
            })
        }
        else if (apidata.ref === "DeleteProduct") {
            ProductAPIs.DeleteProduct(apidata, function (success, response) {
                if (success) {
                    if (response.hasOwnProperty('message') && response.message !== "") {
                        setMessage(response.message)
                        setSeverrity('info')
                        setOpenAlert(true)
                    }
                    DataList.ActiveProduct = {}
                    DataList.ServiceList = []
                    DataList.ProductList = []
                    DataList.ActiveService = {}
                    sessionStorage.removeItem('CurrentProduct')
                    sessionStorage.removeItem('Services')
                    sessionStorage.removeItem('Products')
                    sessionStorage.removeItem('CurrentService')
                    window.setTimeout(function () {
                        window.location.href = window.location.origin + "/#/app/products/"
                        window.location.reload()
                    }, 1000)
                }
            })
        }
    }

    const handleActions = (apidata) => {
        if (apidata.Action === 'redirect') {
            window.open(apidata.url, '_blank')
        }
        else if (apidata.Action === 'refresh') {
        }
        else if (apidata.Action === "confirmation") {
            setConfApiData(apidata['apidata'])
            setConfirmation(apidata.confirmText)
        }
        else {
            if (apidata.method === "DELETE") {
                setAnchorEl(null)
                setLoader(true)
            }
            manageAPIs(apidata)
        }
    }

    const handleUserClick = (event) => {
        setProductList(DataList.ProductList.length > 0 ? DataList.ProductList : (sessionStorage.getItem('Products') ? JSON.parse(window.atob(sessionStorage.getItem('Products'))) : []))
        setCurProd(Object.keys(DataList.ActiveProduct).length > 0 ? DataList.ActiveProduct : (sessionStorage.getItem('CurrentProduct') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentProduct'))) : {}))
        setAnchorEl(event.currentTarget);
    };

    const handlePopoverClose = () => {
        setAnchorEl(null);
    };

    const ChangeProduct = (product) => {
        setAnchorEl(null)
        if (curProd.productid !== product.productid) {
            setLoader(true)
            sessionStorage.setItem('CurrentProduct', window.btoa(JSON.stringify(product)))
            DataList.ActiveProduct = product
            sessionStorage.removeItem('Services')
            sessionStorage.removeItem('CurrentService')
            DataList.ServiceList = []
            DataList.ActiveService = {}
        }
        window.location.href = window.location.origin + "/#/app/products/" + product.productid + "/services"
        // window.location.reload()
    }

    const CancelAddProduct = () => {
        setNewProd(false)
    }

    const handleConfirmation = (key) => {
        setConfirmation('')
        if (key) {
            handleActions(confApiData)
            // console.log(confApiData)
            setConfApiData({})
        }
        else {
            setConfApiData({})
        }
    }

    return (
        <div>
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Loader open={loader} type="page" />
            <Box mr={2} display="flex" alignItems="center" style={{ height: '100%' }} className={`${"top-nav-link"}`} position="relative" onClick={handleUserClick}>
                <Box display="flex" alignItems="center" p={0.5} pl={1} pr={1} style={{ cursor: 'pointer' }} className="links">
                    <Box display="flex" alignItems="center" color="#6f7071" mr={0.5}>
                        <Typography className={typographyClasses.text} style={{ fontWeight: 'bold' }}>{Object.keys(curProd).length > 0 ? <span><span>Product&nbsp;-&nbsp;</span><span style={{ color: '#3F51B5' }}>{curProd.productname}</span></span> : "Product"}</Typography>
                    </Box>
                    <Box color="gray" display="flex">
                        {
                            anchorEl === null ?
                                <ExpandMoreIcon fontSize="small" />
                                :
                                <ExpandLessIcon fontSize="small" />
                        }
                    </Box>
                </Box>
            </Box>
            <Popover
                id={id}
                open={openPopover}
                anchorEl={anchorEl}
                onClose={handlePopoverClose}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
            >
                <Box bgcolor="white" boxShadow={2} height={240} width={260}>
                    <Box height={200} overflow="auto">
                        {
                            productList.length > 0 ?
                                <Box style={{ width: '100%' }}>
                                    {
                                        productList.map((prod, index) => {
                                            return (
                                                <Box style={{ width: '100%', cursor: 'pointer' }} p={1} key={index} bgcolor={curProd.productid === prod.productid ? "#f5f5f5" : "white"} onClick={() => ChangeProduct(prod)}>
                                                    <Box color={curProd.productid === prod.productid ? "#3F51B5" : "gray"} display="flex" alignItems="center" justifyContent="space-between">
                                                        <Box display="flex" flexDirection="column" alignItems="flex-start">
                                                            <Typography className={typographyClasses.text} align="left" color="inherit">{prod.productname}</Typography>
                                                            <Typography className={typographyClasses.smallText} align="left" color="inherit">{prod.productid}</Typography>
                                                        </Box>
                                                        <Box display="flex"
                                                            onClick={(event) => {
                                                                event.stopPropagation()
                                                                handleActions({
                                                                    Action: "confirmation",
                                                                    confirmText: "Are you sure you want to delete this product(" + prod.productname + ")?",
                                                                    apidata: {
                                                                        method: "DELETE",
                                                                        path: apipath['apipath'] + "/product/" + prod.productid,
                                                                        headers: {
                                                                            headers: true,
                                                                            apidomain: apidomain.vmurl
                                                                        },
                                                                        body: {},
                                                                        ref: "DeleteProduct"
                                                                    }
                                                                })
                                                            }}
                                                        >
                                                            <DeleteIcon className={typographyClasses.text} color="secondary" />
                                                        </Box>
                                                    </Box>
                                                </Box>
                                            )
                                        })
                                    }
                                </Box>
                                :
                                <Box p={1}>
                                    <Typography className={typographyClasses.smallText}>No Products</Typography>
                                </Box>
                        }
                    </Box>
                    <Divider />
                    <Box display="flex" alignItems="center" height={39}>
                        <Button
                            variant="text"
                            color="primary"
                            onClick={() => {
                                let gitapidata = {
                                    path: apipath['datastore'] + "/git",
                                    method: 'GET',
                                    body: {},
                                    headers: {
                                        headers: true,
                                        apidomain: apidomain.vmurl
                                    },
                                    ref: "GetGitCredentials"
                                }
                                manageAPIs(gitapidata)
                            }}
                        >
                            <AddIcon className={typographyClasses.smallText} />&nbsp;&nbsp;Create Product
                        </Button>
                    </Box>
                </Box>
            </Popover>
            {
                newProd === true ?
                    <AddProduct Handler={CancelAddProduct} />
                    :
                    null
            }
            {
                confirmation !== '' ?
                    <Confirm confirmText={confirmation} clickHandler={handleConfirmation} />
                    :
                    null
            }
        </div>
    )
}

export default ProductIndex;