import React, { useState } from 'react'

//Material UI
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl'

//Custom Component
import Loader from '../../Components/Loader';
import Alert from '../../Components/Alert'
import DataList from '../../Utils/DataList';
import ServiceAPIs from './ServiceAPIs';
import apidomain from '../../Utils/ApiDomain'
import apipath from '../../Utils/Apipath'
import { Typography } from '@material-ui/core';

// Styles
import TypographyStyles from '../../assets/styles/TypographySyles'


const AddProduct = ({ Handler }) => {
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loader, setLoader] = useState(false)
    const [dialog, setDialog] = useState(true)
    const [formData, setFormData] = useState({})
    const curProd = Object.keys(DataList.ActiveProduct).length > 0 ? DataList.ActiveProduct : (sessionStorage.getItem('CurrentProduct') ? JSON.parse(window.atob(sessionStorage.getItem('CurrentProduct'))) : {})
    const [servName, setServName] = useState('')

    const typographyClasses = TypographyStyles()

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const handleFormDataChage = (field, event) => {
        let formFields = formData;
        if (field === "servicename") {
            const re = /^[a-z][a-z0-9_]*[a-z0-9]*$/;
            if (re.test(event.target.value)) {
                formFields[field] = event.target.value.toLowerCase();
                setServName(event.target.value.toLowerCase())
            }
            else if (event.target.value === "") {
                formFields[field] = event.target.value;
                setServName(event.target.value)
            }
        }
        else {
            formFields[field] = event.target.value;
        }
        setFormData(formFields);
    }

    const SubmitHandler = (key) => {
        let formFields = formData
        formFields['servicetype'] = "dynamodb_eks_flask"
        setFormData(formFields)
        if (!key) {
            Handler()
        }
        else {
            setLoader(true)
            let canSubmit = true, requireAttrs = ['servicename']
            for (let i = 0; i < requireAttrs.length; i++) {
                if (formData[requireAttrs[i]] === undefined || formData[requireAttrs[i]] === "" || formData[requireAttrs[i]] === []) {
                    canSubmit = false
                    setLoader(false)
                    setMessage("Please fill the required details.")
                    setSeverrity('error')
                    setOpenAlert(true)
                }
            }
            if (canSubmit) {
                let servjson = JSON.parse(window.atob(sessionStorage.getItem('selectedtemplate')))
                formData['optionname'] = servjson['optionname']
                let apidata = {
                    method: "POST",
                    path: apipath['apipath'] + "/service/" + curProd.productid,
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    body: formData,
                }
                ServiceAPIs.AddService(apidata, function (success, response) {
                    if (success) {
                        if (response.hasOwnProperty('message') && response.message !== "") {
                            setMessage(response.message)
                            setSeverrity('info')
                            setOpenAlert(true)
                        }
                        let servs = DataList.ServiceList.length > 0 ? DataList.ServiceList : (sessionStorage.getItem('Services') ? JSON.parse(window.atob(sessionStorage.getItem('Services'))) : [])
                        servs.push(response.data)
                        DataList.ServiceList = servs
                        sessionStorage.setItem('Services', window.btoa(JSON.stringify(servs)))
                        window.setTimeout(function () {
                            Handler()
                            window.location.href = window.location.origin + "/#/app/products/" + curProd.productid + "/services/" + response.data.serviceid
                            // window.location.reload()
                        }, 1000)
                    }
                    else {
                        setLoader(false)
                        setMessage(response.message)
                        setSeverrity('error')
                        setOpenAlert(true)
                    }
                })
            }
        }
    }

    return (
        <div>
            <Loader type="page" open={loader} />
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Dialog open={dialog} onClose={() => SubmitHandler(false)} aria-labelledby="form-dialog-title" maxWidth="sm" fullWidth>
                <DialogTitle id="form-dialog-title" style={{padding:'8px 24px'}}>Create service</DialogTitle>
                <DialogContent>
                    <Box>
                        <form autoComplete="off">
                            <Box mb={1}>
                                <label>Service Template</label>
                                <Box display="flex" flexWrap="wrap" alignItems="center" justifyContent="flex-start" mt={1}>
                                    <Box mr={1} mb={1} p={1} pl={2} pr={2} display="flex" alignItems="center" bgcolor="#3f51b5" style={{cursor:'pointer'}}>
                                        <Box display="flex" alignItems="center" color="white">
                                            <Typography className={`${typographyClasses.text}`}>AWS-PYTHON-APIGW-FLASK-EKS-DYNAMODB-COGNITO</Typography>
                                        </Box>
                                    </Box>
                                    {/* <Box mr={1} mb={1} p={1} pl={2} pr={2} display="flex" alignItems="center" bgcolor="#a1a1a1" style={{cursor:'pointer'}}>
                                        <Box display="flex" alignItems="center" color="white">
                                            <Typography className={`${typographyClasses.text}`}>mysql_eks_flask(coming soon)</Typography>
                                        </Box>
                                    </Box> */}
                                </Box>
                            </Box>
                            <Box mb={1}>
                                <FormControl fullWidth>
                                    <label htmlFor="ServiceName" required>Service Name</label>
                                    <TextField
                                        id="ServiceName"
                                        required
                                        value={servName}
                                        fullWidth
                                        placeholder="Enter Service Name"
                                        onChange={handleFormDataChage.bind(this, "servicename")}
                                        variant="outlined"
                                        helperText="No capital letters,special characters except underscore is allowed."
                                    >
                                    </TextField>
                                </FormControl>
                            </Box>
                            <Box>
                                <FormControl fullWidth>
                                    <label htmlFor="Description">Description</label>
                                    <TextField
                                        id="Description"
                                        required
                                        fullWidth
                                        placeholder="Enter Description"
                                        onChange={handleFormDataChage.bind(this, "description")}
                                        variant="outlined"
                                        multiline
                                        rows={3}
                                    >
                                    </TextField>
                                </FormControl>
                            </Box>
                        </form>
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => SubmitHandler(false)} color="secondary" variant="outlined">
                        Cancel
                    </Button>
                    <Button onClick={() => SubmitHandler(true)} color="primary" className="api-btn" variant="contained">
                        Create
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default AddProduct;