import React, { useEffect, useState } from 'react'

//Material UI
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl'
import Box from '@material-ui/core/Box';

//Custom Component
import Loader from '../../Components/Loader';
import Alert from '../../Components/Alert'
import GitAPIs from './GitAPIs';
import apidomain from '../../Utils/ApiDomain';
import apipath from '../../Utils/Apipath';

// Material Icons
import SyncIcon from '@material-ui/icons/Sync';

// Custom Styles
import TypographyStyles from '../../assets/styles/TypographySyles';

const CommitDesign = () => {
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loader, setLoader] = useState(false)
    const [dialog, setDialog] = useState(false)
    const [formData, setFormData] = useState({})
    const [isGitConfigured, setIsGitConfigured] = useState(false)

    const typographyClasses = TypographyStyles()

    useEffect(() => {
        let apidata = {
            path: apipath['datastore'] + "/git",
            method: 'GET',
            body: {},
            headers: {
                headers: true,
                apidomain: apidomain.vmurl
            },
            ref: "GetGitCredentials"
        }
        manageAPIs(apidata)
    }, [])

    const manageAPIs = (apidata) => {
        if (apidata.ref === "GetGitCredentials") {
            GitAPIs.GetGitDetails(apidata, function (success, response) {
                if (success) {
                    if (Object.keys(response.data).length > 0) {
                        setIsGitConfigured(true)
                    }
                }
            })
        }
        else if (apidata.ref === "CommitDesign") {
            GitAPIs.CommitCode(apidata, function (success, response) {
                if (success) {
                    if (response.hasOwnProperty('message') && response.message !== "") {
                        setMessage(response.message)
                        setSeverrity('info')
                        setOpenAlert(true)
                    }
                    setDialog(false)
                    setLoader(false)
                }
                else {
                    setLoader(false)
                    setMessage(response.message)
                    setSeverrity('error')
                    setOpenAlert(true)
                }
            })
        }
    }

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const handleFormDataChage = (field, event) => {
        let formFields = formData;
        formFields[field] = event.target.value;
        setFormData(formFields);
    }

    const SubmitHandler = (key) => {
        if (!key) {
            setDialog(false)
        }
        else {
            setLoader(true)
            let canSubmit = true, requireAttrs = ['commitmessage', 'tagname']
            for (let i = 0; i < requireAttrs.length; i++) {
                if (formData[requireAttrs[i]] === undefined || formData[requireAttrs[i]] === "" || formData[requireAttrs[i]] === []) {
                    canSubmit = false
                    setLoader(false)
                    setMessage("Please fill the required details.")
                    setSeverrity('error')
                    setOpenAlert(true)
                }
            }
            if (canSubmit) {
                let apidata = {
                    method: "POST",
                    path: apipath['datastore'] + "/git/designpush",
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    },
                    body: formData,
                    ref: "CommitDesign"
                }
                manageAPIs(apidata)
            }
        }
    }

    return (
        <div>
            <Loader type="page" open={loader} />
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Box>
                {
                    isGitConfigured === false ?
                        <Box display="flex" alignItems="center">
                            <Box mr={2}>
                                <Button
                                    className="trigger-btn"
                                    color="primary"
                                    variant="contained"
                                    onClick={() => {
                                        window.location.href = window.location.origin + "/#/app/gitsetup"
                                    }}
                                >
                                    Add Code Repo
                                </Button>
                            </Box>
                        </Box>
                        :
                        null
                }
                {
                    isGitConfigured === true ?
                        <Box display="flex" alignItems="center">
                            <Box mr={2}>

                                <Button
                                    className="trigger-btn"
                                    color="primary"
                                    variant="contained"
                                    onClick={() => setDialog(true)}
                                >
                                    <SyncIcon className={typographyClasses.text} />&nbsp;&nbsp;Sync to Git
                                </Button>
                            </Box>
                            <Box mr={2}>
                                <Button
                                    color="primary"
                                    variant="text"
                                    onClick={() => {
                                        sessionStorage.setItem('showCred', 'yes')
                                        window.location.href = window.location.origin + "/#/app/gitsetup"
                                    }}
                                >
                                    Git Credentials
                                </Button>
                            </Box>
                        </Box>
                        :
                        null
                }
            </Box>
            <Dialog open={dialog} onClose={() => SubmitHandler(false)} aria-labelledby="form-dialog-title" maxWidth="sm" fullWidth>
                <DialogTitle id="form-dialog-title">Commit</DialogTitle>
                <DialogContent>
                    <Box>
                        <form style={{}} autoComplete="off">
                            <Box mb={1}>
                                <FormControl fullWidth>
                                    <label htmlFor="Commit" required>Commit Message</label>
                                    <TextField
                                        id="Commit"
                                        required
                                        fullWidth
                                        placeholder="Enter Commit Message"
                                        onChange={handleFormDataChage.bind(this, "commitmessage")}
                                        variant="outlined"
                                        multiline
                                        rows={2}
                                    >
                                    </TextField>
                                </FormControl>
                            </Box>
                            <Box>
                                <FormControl fullWidth>
                                    <label htmlFor="tagname" required>Tag Name</label>
                                    <TextField
                                        id="tagname"
                                        required
                                        fullWidth
                                        placeholder="Enter Tag Name"
                                        onChange={handleFormDataChage.bind(this, "tagname")}
                                        variant="outlined"
                                    >
                                    </TextField>
                                </FormControl>
                            </Box>
                        </form>
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => SubmitHandler(false)} color="secondary" variant="outlined">
                        Cancel
                    </Button>
                    <Button onClick={() => SubmitHandler(true)} color="primary" className="api-btn" variant="contained">
                        Sync
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default CommitDesign;