import React, { useEffect, useState } from 'react'

// Material UI
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import Dialogactions from '@material-ui/core/Dialogactions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import FormControl from '@material-ui/core/FormControl';
import withStyles from '@material-ui/core/styles/withStyles';
import MuiAccordion from '@material-ui/core/Accordion';
import MuiAccordionSummary from '@material-ui/core/AccordionSummary';
import MuiAccordionDetails from '@material-ui/core/AccordionDetails';
import makeStyles from '@material-ui/core/styles/makeStyles';
import Chip from '@material-ui/core/Chip';

// Material Icons
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import SaveIcon from '@material-ui/icons/Save';
// import SettingsIcon from '@material-ui/icons/Settings';
// import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import DeleteIcon from '@material-ui/icons/Delete';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Clear from '@material-ui/icons/Clear';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';

// Custom Components
import Loader from '../../Components/Loader';
import Alert from '../../Components/Alert';
import Confirm from '../../Components/Confirm';
import HandleApi from '../../Utils/HandleApi';
import apipath from '../../Utils/Apipath';
import apidomain from '../../Utils/ApiDomain';

//Ace Editor
import AceEditor from 'react-ace'
import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-noconflict/theme-dreamweaver";
import "ace-builds/src-noconflict/theme-monokai";
import "ace-builds/src-noconflict/theme-clouds";
import "ace-builds/src-noconflict/theme-eclipse";
import "ace-builds/src-noconflict/ext-language_tools";
import "ace-builds/src-noconflict/snippets/python"
import "ace-builds/src-noconflict/keybinding-vim"
import "ace-builds/src-noconflict/keybinding-vscode"

// Custom Styles
import TypographyStyles from '../../assets/styles/TypographySyles';
import StatusStyles from '../../assets/styles/StatusStyles';

import { JSONToHTMLTable } from '@kevincobain2000/json-to-html-table'


const sidebarStyles = makeStyles((theme) => ({
    root: {
        cursor: 'pointer'
    },
    onhoveritem: {
        '&:hover': {
            backgroundColor: '#e6e8f3'
        }
    }
}));

const Accordion = withStyles({
    root: {
        // border: '1px solid rgba(0, 0, 0, .125)',
        boxShadow: 'none',
        '&:not(:last-child)': {
            borderBottom: 0,
        },
        '&:before': {
            display: 'none',
        },
        '&$expanded': {
            margin: 'auto',
        },
    },
    expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
    root: {
        backgroundColor: 'white',
        borderBottom: '1px solid rgba(0, 0, 0, .125)',
        marginBottom: 0,
        minHeight: 56,
        '&$expanded': {
            minHeight: 56,
            // border:'1px solid rgba(0, 0, 0, .125)'
        },
    },
    // expandIcon: {
    //     '&$expanded': {
    //         transform: 'rotate(90deg)'
    //     }
    // },
    content: {
        margin: 0,
        '&$expanded': {
            margin: '0',
        },
    },
    expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
    root: {
        padding: theme.spacing(2),
    },
}))(MuiAccordionDetails);


function expansionProps(name, index) {
    return {
        id: `${name}${index}-header`,
        'aria-controls': `${name}${index}-content`,
    };
}


const NewEditor = ({ ...props }) => {
    const { methodid, btnAction, serviceid, productid, apiid, versionid, resourceid, versionname, datasource } = props

    const [loader, setLoader] = useState(false)
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loadTimeout, setLoadTimeout] = useState(false)
    const [confirmation, setConfirmation] = useState('')
    const [confApiData, setConfApiData] = useState({})
    const [showFiles, setShowFiles] = useState(false)
    const [showSnippets, setShowSnippets] = useState(false)
    const [methodJson, setMethodJson] = useState(props.methodJson)
    const [snippetList, setSnippetList] = useState(props.methodJson.snippets.snippetorder)
    const [snippetJson, setSnippetJson] = useState(props.methodJson.snippets)
    const [preSnippets, setPreSnippets] = useState([])
    const [dialog, setDialog] = useState(false)
    const [dialogContent, setDialogContent] = useState('')
    const [formData, setFormData] = useState({})
    const [activeBlk, setActiveBlk] = useState('full_code')
    const [activeData, setActiveData] = useState({})
    const [activeReqList, setActiveReqList] = useState([])
    const [editorProps, setEditorProps] = useState({})
    const [reqList, setReqList] = useState([{ "modulename": "" }]);

    let code_list = ["import_code", "data_code", "function_code", "function_call"]
    let code_map = {
        "import_code": "Imports",
        "data_code": "Data Source",
        "function_code": "Functions",
        "function_call": "Main"
    }

    const typographyClasses = TypographyStyles()
    const sidebarClasses = sidebarStyles()

    useEffect(() => {
        let apidata = {
            method: "GET",
            path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodid + "/fullcode",
            headers: {
                headers: true,
                apidomain: apidomain.vmurl
            },
            ref: "GetFullCode",
            body: {}
        }
        manageAPIs(apidata)

        let nextapi = {
            method: "POST",
            headers: {
                headers: true,
                apidomain: apidomain.vmurl
            },
            ref: "GetSnippets",
            body: {
                servicetype: methodJson['servicetype'],
                dbtype: methodJson['dbtype'],
                keys: methodJson['keys'],
                methodtype: methodJson['methodtype']
            },
            path: apipath['designapi'] + "/api/getallsnippet"
        }
        manageAPIs(nextapi)
    }, [])

    const manageAPIs = (apidata) => {
        HandleApi.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
            .then(result => {
                if (result.hasOwnProperty('message') && result.message !== "") {
                    setMessage(result.message)
                    setSeverrity('info')
                    setOpenAlert(true)
                }
                if (result.hasOwnProperty('statusCode') && result.statusCode === 200) {
                    if (apidata.ref === "GetSnippets") {
                        setPreSnippets(result.body)
                    }
                    else if (apidata.ref === "NewSnippet") {
                        setDialogContent('')
                        setFormData({})
                        setDialog(false)
                        setLoader(false)
                        setActiveData({})
                        setMethodJson(result.body)
                        setSnippetJson(result.body.snippets)
                        setActiveData(result.body.snippets[result.snippetid])
                        setActiveBlk('snippet')
                        setShowFiles(true)
                        setSnippetList(result.body.snippets.snippetorder)
                    }
                    else if (apidata.ref === "UpdateCode") {
                        setLoader(false)
                        setActiveData({})
                        setActiveData(result.body)
                        let snpjson = snippetJson
                        snpjson[result.body.snippetid] = result.body
                        setSnippetJson(snpjson)
                        setActiveReqList(result.body.requirements)
                        let snlist = [...snippetList]
                        setSnippetList([])
                        setSnippetList(snlist)
                        if (dialogContent === "Add Module") {
                            setReqList([{ "modulename": "" }])
                            setDialogContent('')
                            setDialog(false)
                        }
                    }
                    else if (apidata.ref === "GetFullCode") {
                        setActiveData({ "code": result.body })
                        setLoadTimeout(true)
                    }
                    else if (apidata.ref === "DeleteSnippet") {
                        setLoader(false)
                        setMethodJson(result.body)
                        setSnippetJson(result.body.snippets)
                        setSnippetList(result.body.snippets.snippetorder)
                        if (activeBlk === "full_code") {
                            setLoadTimeout(false)
                            setActiveData({})
                            let apidata = {
                                method: "GET",
                                path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodid + "/fullcode",
                                headers: {
                                    headers: true,
                                    apidomain: apidomain.vmurl
                                },
                                ref: "GetFullCode",
                                body: {}
                            }
                            manageAPIs(apidata)
                        }
                    }
                    else if (apidata.ref === "GetSpecificTable") {
                        let apidata1 = {
                            path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodJson.methodid + "/addsnippet",
                            ref: "NewSnippet",
                            method: "POST",
                            body: {
                                versionname: versionname,
                                table: result.body
                            },
                            headers: {
                                headers: true,
                                apidomain: apidomain.vmurl
                            },
                        }
                        if (apidata['key'] === "custom") {
                            apidata1['body'] = {
                                "snippetname": apidata['ipdata']['snippetname'],
                                "versionname": versionname,
                                "functionname": apidata['ipdata']['functionname'],
                                "table": result.body
                            }
                        }
                        else if (apidata['key'] === "predefined") {
                            apidata1['body']['snippet'] = apidata['snippet']
                        }
                        manageAPIs(apidata1)
                    }
                }
                else {
                    setMessage(result.errorMessage)
                    setSeverrity('error')
                    setOpenAlert(true)
                    setLoader(false)
                }
            })
            .catch(err => {
                setMessage(err)
                setSeverrity('error')
                setOpenAlert(true)
                setLoader(false)
            })
    }

    const handleConfirmation = (key) => {
        setConfirmation('')
        if (key) {
            handleActions(confApiData)
            // console.log(confApiData)
            setConfApiData({})
        }
        else {
            setConfApiData({})
        }
    }

    const handleActions = (apidata) => {
        if (apidata.Action === 'redirect') {
            window.open(apidata.url, '_blank')
        }
        else if (apidata.Action === 'refresh') {

        }
        else if (apidata.Action === "confirmation") {
            setConfApiData(apidata['apidata'])
            setConfirmation(apidata.confirmText)
        }
        else {
            // setLoader(true)
            if (apidata.method === "DELETE") {
                setLoader(true)
            }
            manageAPIs(apidata)
        }
    }

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const handleAddSnippet = (key, snip) => {
        if (key === "custom") {
            setDialogContent('Add Snippet')
            setDialog(true)
        }
        else if (key === "predefined") {
            let apidata = {
                path: apipath['designapi'] + "/table/" + serviceid + "/" + datasource.db.dbid + "/" + datasource.db.tableid,
                ref: "GetSpecificTable",
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                body: {},
                method: "GET",
                snippet: snip,
                key: "predefined"
            }
            manageAPIs(apidata)
        }
    }

    const ProcessSideBar = ({ order, data }) => {
        return (
            <Box>
                {
                    order.map((ord, index) => {
                        return (
                            <Box key={index}>
                                {
                                    data[ord.snippetid].hasOwnProperty('order') === true ?
                                        <Box>
                                            <Box display="flex" p={1}>
                                                <Typography className={`${typographyClasses.bold} ${typographyClasses.text}`}>{ord}</Typography>
                                            </Box>
                                            <Box ml={2}>
                                                <ProcessSideBar order={data[ord.snippetid]['order']} data={data[ord.snippetid]} />
                                            </Box>
                                        </Box>
                                        :
                                        null
                                }
                                {
                                    data[ord.snippetid].hasOwnProperty('order') === false ?
                                        <Box className={`${sidebarClasses.root} ${sidebarClasses.onhoveritem}`} display="flex" alignItems="center" justifyContent="space-between" p={1} style={{ cursor: 'pointer' }} bgcolor={activeBlk === "snippet" ? activeData['snippetid'] === ord.snippetid ? "#e6e8f3" : "" : ""} onClick={() => {
                                            setActiveBlk('snippet')
                                            setActiveData({})
                                            handleFuncClick("snippet", data[ord.snippetid])
                                        }}>
                                            <Box display="flex" mr={2}>
                                                <Typography className={typographyClasses.smallText} align="left" color={activeBlk === "snippet" ? activeData['snippetid'] === ord.snippetid ? "primary" : "inherit" : "inherit"}>{ord['name']}</Typography>
                                            </Box>
                                            <Box display="flex"
                                                onClick={(event) => {
                                                    event.stopPropagation()
                                                    handleActions({
                                                        Action: "confirmation",
                                                        confirmText: "Are you sure you want to delete this snippet(" + ord['name'] + ")?",
                                                        apidata: {
                                                            method: "DELETE",
                                                            path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodid + "/snippet/" + ord.snippetid,
                                                            headers: {
                                                                headers: true,
                                                                apidomain: apidomain.vmurl
                                                            },
                                                            body: {},
                                                            ref: "DeleteSnippet"
                                                        }
                                                    })
                                                }}
                                            >
                                                <DeleteIcon className={typographyClasses.text} color="secondary" />
                                            </Box>
                                        </Box>
                                        :
                                        null
                                }
                            </Box>
                        )
                    })
                }
            </Box>
        )
    }

    // const ProcessEditor = ({ ...props }) => {
    //     const { order, data } = props
    //     return (
    //         <Box>
    //             <Box>
    //                 {
    //                     order.map((ord, index) => {
    //                         return (
    //                             <Box key={index}>
    //                                 {
    //                                     data[ord].hasOwnProperty('order') === true ?
    //                                         <Box>
    //                                             <ProcessEditor order={data[ord]['order']} data={data[ord]} />
    //                                         </Box>
    //                                         :
    //                                         null
    //                                 }
    //                                 {
    //                                     data[ord].hasOwnProperty('order') === false && data[ord]['defaultview'] === true ?
    //                                         <Box display="flex" style={{ width: '100%' }}>
    //                                             <AceEditor
    //                                                 style={{ height: '100%', width: '100%' }}
    //                                                 mode="python"
    //                                                 theme="monokai"
    //                                                 // onChange={onChange}
    //                                                 name="UNIQUE_ID_OF_DIV"
    //                                                 setOptions={{
    //                                                     fontSize: 14,
    //                                                     // readOnly:true
    //                                                     enableBasicAutocompletion: true,
    //                                                     enableLiveAutocompletion: true,
    //                                                     useSoftTabs: true,
    //                                                     tabSize: 4,
    //                                                     showPrintMargin: false,
    //                                                     wrap: true,
    //                                                     maxLines: Infinity,
    //                                                     dragEnabled: false
    //                                                 }}
    //                                                 editorProps={{ $blockScrolling: true }}
    //                                                 defaultValue={data[ord]['code']}
    //                                                 commands={[
    //                                                     {
    //                                                         name: 'save',
    //                                                         bindKey: { win: "Ctrl-S", "mac": "Cmd-S" },
    //                                                         exec: function (editor) {
    //                                                             console.log("saving", editor.session.getValue())
    //                                                         }
    //                                                     }
    //                                                 ]}
    //                                             />
    //                                         </Box>
    //                                         :
    //                                         null
    //                                 }
    //                             </Box>
    //                         )
    //                     })
    //                 }
    //             </Box>
    //         </Box>
    //     )
    // }

    const handleFuncClick = (key, item) => {
        if (key === "full_code") {
            setLoadTimeout(false)
            setActiveBlk('full_code')
            setActiveData({})
            let apidata = {
                method: "GET",
                path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodid + "/fullcode",
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                ref: "GetFullCode",
                body: {}
            }
            manageAPIs(apidata)
        }
        else if (key === "snippet") {
            setActiveReqList(item.requirements)
            setActiveData(item)
        }
    }

    const onChangeCode = (code_key, event) => {
        let editprops = editorProps
        editprops[code_key]['code'] = event.split("'").join('"')
        setEditorProps(editprops)
    }

    const handleEditcode = (code_key) => {
        let editprops = editorProps
        editprops[code_key]['readOnly'] = false
        setEditorProps(editprops)
        let snlist = [...snippetList]
        setSnippetList([])
        setSnippetList(snlist)
    }

    const saveCode = (code_key) => {
        let editprops = editorProps
        editprops[code_key]['readOnly'] = true
        setEditorProps(editprops)
        let ips = { "dummy": "a" }
        ips[code_key] = editprops[code_key]['code']
        ips['snippetid'] = activeData['snippetid']
        ips['version'] = activeData['version']
        delete ips['dummy']
        let apidata = {
            method: "PATCH",
            path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodJson.methodid,
            headers: {
                headers: true,
                apidomain: apidomain.vmurl
            },
            ref: "UpdateCode",
            body: ips
        }
        manageAPIs(apidata)
    }

    const EditorActions = ({ code_key }) => {
        return (
            <Box display="flex" justifyContent="space-between" color="gray">
                <Box p={1} style={{ width: '100%', height: '100%', cursor: 'pointer' }} display="flex" justifyContent="center" alignItems="flex-start" onClick={() => {
                    if (editorProps[code_key]['readOnly']) {
                        handleEditcode(code_key)
                    }
                    else {
                        saveCode(code_key)
                    }
                }}>
                    {
                        editorProps[code_key]['readOnly'] === true ?
                            <EditIcon color='primary' className={typographyClasses.h5} />
                            :
                            null
                    }
                    {
                        editorProps[code_key]['readOnly'] === false ?
                            <SaveIcon color='primary' className={typographyClasses.h5} />
                            :
                            null
                    }
                </Box>
            </Box>
        )
    }

    const handleFormDataChage = (field, event) => {
        let formFields = formData;
        formFields[field] = event.target.value;
        setFormData(formFields);
    }

    const FormDialogSubmit = () => {
        setLoader(true)
        let canSubmit = true, requireAttrs = [], path = '', ref = '', method = "", body = {}
        if (dialogContent === "Add Module") {
            let reqlist = []
            for (let i = 0; i < reqList.length; i++) {
                reqlist.push(reqList[i]['modulename'])
            }
            reqlist = activeData['requirements'].concat(reqlist)
            let apidata = {
                method: "PATCH",
                path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodJson.methodid,
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                body: {
                    "requirements": reqlist,
                    "snippetid": activeData['snippetid'],
                    "version": activeData['version']
                },
                ref: "UpdateCode"
            }
            manageAPIs(apidata)
            // let activedata = activeData
            // activedata['requirements'] = reqlist
            // setActiveData({})
            // setActiveData(activedata)
        }
        if (dialogContent !== "Add Module") {
            if (dialogContent === "Add Snippet") {
                requireAttrs.push('snippetname')
                requireAttrs.push('functionname')
                path = apipath['designapi'] + "/table/" + serviceid + "/" + datasource.db.dbid + "/" + datasource.db.tableid
                ref = "GetSpecificTable"
                method = "GET"
                body = formData
            }
            for (let i = 0; i < requireAttrs.length; i++) {
                if (formData[requireAttrs[i]] === undefined || formData[requireAttrs[i]] === "" || formData[requireAttrs[i]] === []) {
                    canSubmit = false
                    setLoader(false)
                    setMessage("Please fill the required details.")
                    setSeverrity('error')
                    setOpenAlert(true)
                }
            }
            if (canSubmit) {
                if (dialogContent === "Add Snippet") {
                    let apidata = {
                        method: method,
                        path: path,
                        headers: {
                            headers: true,
                            apidomain: apidomain.vmurl
                        },
                        "body": {},
                        ref: ref,
                        key: "custom",
                        ipdata: body
                    }
                    manageAPIs(apidata)
                }
                else {
                    let apidata = {
                        method: method,
                        path: path,
                        headers: {
                            headers: true,
                            apidomain: apidomain.vmurl
                        },
                        body: formData,
                        ref: ref
                    }
                    manageAPIs(apidata)
                }
            }
        }
    }

    const handleReqDelete = (data) => {
        let reqlist = [...activeReqList]
        let index = reqlist.indexOf(data)
        reqlist.splice(index, 1)

        handleActions({
            Action: "confirmation",
            confirmText: "Are you sure you want to delete this module?",
            apidata: {
                method: "PATCH",
                path: apipath['designapi'] + "/api/" + productid + "/" + serviceid + "/" + apiid + "/" + versionid + "/" + resourceid + "/" + methodJson.methodid,
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                },
                body: {
                    "requirements": reqlist,
                    "snippetid": activeData['snippetid'],
                    "version": activeData['version']
                },
                ref: "UpdateCode",
                key: "redelete"
            }
        })
    }

    const handleAddAttr = () => {
        setReqList([...reqList, { "modulename": "" }]);
    };

    const handleRemAttr = index => {
        let list = [...reqList];
        list.splice(index, 1);
        setReqList(list);
    };

    const handleAttrInputChange = (e, index) => {
        const { name, value } = e.target;
        let list = [...reqList];
        list[index][name] = value;
        setReqList(list);
    };

    return (
        <div>
            {
                loader === true ?
                    <Loader type="page" open={true} />
                    :
                    null
            }
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Box pl={1} pr={1}>
                <Box border={1} style={{ borderColor: 'lightgray' }}>
                    <Box display="flex" alignItems="center">
                        {
                            showFiles === false ?
                                <Box style={{ height: 'calc(100vh - 34px)', borderColor: 'lightgray' }} bgcolor="#FAFBFC" width={40} borderRight={1}>
                                    <Box display="flex" alignItems="center" style={{ width: '100%', cursor: 'pointer' }} onClick={() => setShowFiles(true)} p={1} pl={0} pr={0} justifyContent="center">
                                        <MenuIcon className={`${typographyClasses.h5}`} />
                                    </Box>
                                </Box>
                                :
                                null
                        }
                        <Grid container>
                            {
                                showFiles === true ?
                                    <Grid item xs={2}>
                                        <Box style={{ height: 'calc(100vh - 34px)', borderColor: 'lightgray' }} bgcolor="#fafafa" borderRight={1}>
                                            <Box>
                                                <Box p={1} borderBottom={1} style={{ borderColor: 'lightgray' }} display="flex" justifyContent="space-between" alignItems="center">
                                                    <Box display="flex">
                                                        <Typography className={`${typographyClasses.bold} ${typographyClasses.smallText}`}>{methodid}.py</Typography>
                                                    </Box>
                                                    <Box display='flex' style={{ cursor: 'pointer' }} onClick={(e) => {
                                                        e.stopPropagation()
                                                        setShowFiles(false)
                                                    }}>
                                                        <Clear className={`${typographyClasses.h5}`} />
                                                    </Box>
                                                </Box>
                                                <Box style={{ height: 'calc(100vh - 81px)' }} overflow="auto">
                                                    <Box style={{ height: '100%' }}>
                                                        <Box className={`${sidebarClasses.root} ${sidebarClasses.onhoveritem}`} display="flex" bgcolor={activeBlk === "full_code" ? "#e6e8f3" : ""} p={1} style={{ cursor: 'pointer' }} onClick={() => {
                                                            handleFuncClick("full_code")
                                                        }}>
                                                            <Typography className={activeBlk === "full_code" ? "primary" : "inherit"}>Full Code</Typography>
                                                        </Box>
                                                        <Box display="flex" p={1}>
                                                            <Typography className={`${typographyClasses.bold}`}>Functions</Typography>
                                                        </Box>
                                                        <Box>
                                                            {
                                                                methodJson.hasOwnProperty('snippets') === true && methodJson.snippets.hasOwnProperty('snippetorder') === true ?
                                                                    <ProcessSideBar order={methodJson['snippets']['snippetorder']} data={methodJson['snippets']} />
                                                                    :
                                                                    null
                                                            }
                                                        </Box>
                                                    </Box>
                                                </Box>
                                            </Box>
                                        </Box>
                                    </Grid>
                                    :
                                    null
                            }
                            <Grid item xs={showFiles === false ? showSnippets === false ? 12 : 10 : showSnippets === false ? 10 : 8}>
                                <Box style={{ height: 'calc(100vh - 34px)', borderColor: 'lightgray' }} borderRight={1} overflow="hidden">
                                    <Box p={1} borderBottom={1} style={{ borderColor: 'lightgray' }} display="flex" alignItems="center" justifyContent="space-between">
                                        <Box display="flex">
                                            {
                                                Object.keys(activeData).length > 0 ?
                                                    <Typography className={`${typographyClasses.bold} ${typographyClasses.h6}`}>
                                                        {activeBlk === "full_code" ? "Full Code" : activeData['name']}
                                                    </Typography>
                                                    :
                                                    null
                                            }
                                        </Box>
                                    </Box>
                                    <Box style={{ height: 'calc(100vh - 80px)' }} overflow="auto">
                                        {
                                            activeBlk === "full_code" ?
                                                activeData.hasOwnProperty('code') === true ?
                                                    <Box display="flex" style={{ width: '100%' }}>
                                                        <AceEditor
                                                            style={{ height: '100%', width: '100%' }}
                                                            mode="python"
                                                            theme="monokai"
                                                            // onChange={onChange}
                                                            name="full_code"
                                                            setOptions={{
                                                                fontSize: 14,
                                                                readOnly: true,
                                                                enableBasicAutocompletion: true,
                                                                enableLiveAutocompletion: true,
                                                                useSoftTabs: true,
                                                                tabSize: 4,
                                                                showPrintMargin: false,
                                                                wrap: true,
                                                                maxLines: Infinity,
                                                                dragEnabled: false
                                                            }}
                                                            editorProps={{ $blockScrolling: true }}
                                                            value={activeData['code']}
                                                        />
                                                    </Box>
                                                    :
                                                    <Box>
                                                        {
                                                            loadTimeout === false ?
                                                                <Loader open={true} type="block" />
                                                                :
                                                                null
                                                        }
                                                    </Box>
                                                :
                                                null
                                        }
                                        {
                                            activeBlk === "snippet" ?
                                                Object.keys(activeData).length > 0 ?
                                                    <Box>
                                                        <Box p={1} pb={0} display="flex" alignItems="flex-start">
                                                            <Box display="flex" mb={1}>
                                                                <Typography className={`${typographyClasses.bold} ${typographyClasses.text}`}>Requirements&nbsp;&nbsp;-&nbsp;&nbsp;</Typography>
                                                            </Box>
                                                            <Box display="flex" alignItems="center" flexWrap="wrap">
                                                                <Box display="flex" mr={0.5} mb={0.5} border={1} p={0.5} height={27} borderColor="#3f51b5" style={{ cursor: 'pointer' }} title="Add Requirements" onClick={() => {
                                                                    setDialogContent('Add Module')
                                                                    setDialog(true)
                                                                }}
                                                                >
                                                                    <AddIcon className={typographyClasses.text} color
                                                                        ="primary" />
                                                                </Box>
                                                                {
                                                                    activeData['requirements'].map((req, index) => {
                                                                        return (
                                                                            <Box key={index} mr={0.5} mb={0.5} bgcolor="#3f51b5">
                                                                                <Box display="flex" p={0.5} alignItems="center" color="white">
                                                                                    <Box display="flex" mr={1}>
                                                                                        <Typography className={typographyClasses.smallText}>{req}</Typography>
                                                                                    </Box>
                                                                                    <Box display="flex" style={{ cursor: 'pointer' }} onClick={() => {
                                                                                        handleReqDelete(activeData['requirements'], req)
                                                                                    }}>
                                                                                        <Clear className={typographyClasses.smallText} />
                                                                                    </Box>
                                                                                </Box>
                                                                            </Box>
                                                                        )
                                                                    })
                                                                }
                                                            </Box>
                                                        </Box>
                                                        <Box>
                                                            <Box>
                                                                {
                                                                    code_list.map((cdlist, index) => {
                                                                        if (!editorProps.hasOwnProperty(cdlist)) {
                                                                            editorProps[cdlist] = {
                                                                                "code": activeData[cdlist],
                                                                                "readOnly": true
                                                                            }
                                                                        }
                                                                        return (
                                                                            <Box style={{ width: '97%' }} key={index} margin="auto" border={1} mb={1}>
                                                                                <Box display="flex" flexDirection="column" pl={1} pr={1}>
                                                                                    <Box display="flex" alignItems="center" justifyContent="flex-start">
                                                                                        <Box display="flex" mr={2}>
                                                                                            <Typography className={`${typographyClasses.text} ${typographyClasses.bold}`}>{code_map[cdlist]}</Typography>
                                                                                        </Box>
                                                                                        <Box display="flex" alignItems="center">
                                                                                            <EditorActions code_key={cdlist} />
                                                                                        </Box>
                                                                                    </Box>
                                                                                </Box>
                                                                                <Box style={{ width: '100' }}>
                                                                                    <AceEditor
                                                                                        style={{ height: '100%', width: '100%' }}
                                                                                        mode="python"
                                                                                        theme="monokai"
                                                                                        onChange={(event) => onChangeCode(cdlist, event)}
                                                                                        name={activeData.snippetid + cdlist}
                                                                                        setOptions={{
                                                                                            fontSize: 14,
                                                                                            readOnly: editorProps[cdlist]['readOnly'],
                                                                                            enableBasicAutocompletion: true,
                                                                                            enableLiveAutocompletion: true,
                                                                                            useSoftTabs: true,
                                                                                            tabSize: 4,
                                                                                            showPrintMargin: false,
                                                                                            wrap: true,
                                                                                            maxLines: 20,
                                                                                            dragEnabled: false,
                                                                                            minLines: 10
                                                                                        }}
                                                                                        editorProps={{ $blockScrolling: true }}
                                                                                        value={activeData[cdlist]}
                                                                                    />
                                                                                </Box>
                                                                            </Box>
                                                                        )
                                                                    })
                                                                }
                                                            </Box>
                                                        </Box>
                                                    </Box>
                                                    :
                                                    null
                                                :
                                                null
                                        }
                                    </Box>
                                </Box>
                            </Grid>
                            {
                                showSnippets === true ?
                                    <Grid item xs={2}>
                                        <Box style={{ height: 'calc(100vh - 34px)' }} bgcolor="#fafafa">
                                            <Box>
                                                <Box p={1} borderBottom={1} style={{ borderColor: 'lightgray' }} display="flex" justifyContent="space-between" alignItems="center">
                                                    <Box display="flex">
                                                        <Typography className={`${typographyClasses.bold} ${typographyClasses.h6}`}>Snippets</Typography>
                                                    </Box>
                                                    <Box display='flex' style={{ cursor: 'pointer' }} onClick={(e) => {
                                                        e.stopPropagation()
                                                        setShowSnippets(false)
                                                    }}>
                                                        <Clear className={`${typographyClasses.h5}`} />
                                                    </Box>
                                                </Box>
                                                <Box p={1} style={{ height: 'calc(100vh - 113px)' }} overflow="auto">
                                                    <Box p={1} display="flex" flexDirection="column" border={1} mb={0.5} style={{ borderColor: 'lightgray', width: '100%', cursor: 'pointer' }} alignItems="flex-start" bgcolor="#3F51B5" color="white" onClick={() => {
                                                        if (!btnAction) {
                                                            handleAddSnippet("custom")
                                                        }
                                                    }}>
                                                        <Box style={{ width: '100%' }} display="flex" justifyContent="space-between" alignItems="center">
                                                            <Box>
                                                                <Typography className={`${typographyClasses.smallText}`}>Custom</Typography>
                                                            </Box>
                                                            {
                                                                btnAction === false ?
                                                                    <Box>
                                                                        <AddIcon className={`${typographyClasses.smallText}`} />
                                                                    </Box>
                                                                    :
                                                                    null
                                                            }
                                                        </Box>
                                                    </Box>
                                                    {
                                                        preSnippets.length > 0 ?
                                                            preSnippets.map((snip, index) => {
                                                                return (
                                                                    <Box p={1} display="flex" flexDirection="column" border={1} mb={0.5} style={{ borderColor: 'lightgray', width: '100%', cursor: 'pointer' }} alignItems="flex-start" bgcolor="#3F51B5" color="white" key={index} onClick={() => {
                                                                        if (!btnAction) {
                                                                            handleAddSnippet('predefined', snip)
                                                                        }
                                                                    }}>
                                                                        <Box style={{ width: '100%' }} display="flex" justifyContent="space-between" alignItems="center">
                                                                            <Box>
                                                                                <Typography className={`${typographyClasses.smallText}`}>{snip.name}</Typography>
                                                                            </Box>
                                                                            {
                                                                                btnAction === false ?
                                                                                    <Box>
                                                                                        <AddIcon className={`${typographyClasses.smallText}`} />
                                                                                    </Box>
                                                                                    :
                                                                                    null
                                                                            }
                                                                        </Box>
                                                                    </Box>
                                                                )
                                                            })
                                                            :
                                                            <Box>
                                                                {
                                                                    btnAction === false ?
                                                                        <Typography className={typographyClasses.smallText}>No Predefined Snippets</Typography>
                                                                        :
                                                                        null
                                                                }
                                                            </Box>
                                                    }
                                                </Box>
                                            </Box>
                                        </Box>
                                    </Grid>
                                    :
                                    null
                            }
                        </Grid>
                        {
                            showSnippets === false ?
                                <Box style={{ height: 'calc(100vh - 34px)' }} bgcolor="#FAFBFC" width={40}>
                                    <Box display="flex" alignItems="center" style={{ width: '100%', cursor: 'pointer' }} onClick={() => setShowSnippets(true)} p={1} pl={0} pr={0} justifyContent="center">
                                        <MenuIcon className={`${typographyClasses.h5}`} />
                                    </Box>
                                </Box>
                                :
                                null
                        }
                    </Box>
                </Box>
            </Box>
            <Dialog
                open={dialog}
                onClose={() => setDialog(false)}
                aria-labelledby="acc-dialog-title"
                fullWidth={true}
                maxWidth={dialogContent === "Add Snippet" || dialogContent === "Add Module" ? "sm" : "lg"}
            >
                <DialogTitle id="acc-dialog-title" style={{ borderBottom: '1px solid lightgrey' }}>{dialogContent}</DialogTitle>
                <DialogContent>
                    {
                        dialogContent === "Add Snippet" ?
                            <form autoComplete="off">
                                <Box mb={1}>
                                    <FormControl fullWidth>
                                        <label htmlFor="SnippetName" required>Snippet Name</label>
                                        <TextField
                                            id="SnippetName"
                                            required
                                            fullWidth
                                            placeholder="Enter Snippet Name"
                                            onChange={handleFormDataChage.bind(this, "snippetname")}
                                            variant="outlined"
                                        >
                                        </TextField>
                                    </FormControl>
                                </Box>
                                <Box mb={1}>
                                    <FormControl fullWidth>
                                        <label htmlFor="FunctionName" required>Function Name</label>
                                        <TextField
                                            id="FunctionName"
                                            required
                                            fullWidth
                                            placeholder="Enter Function Name"
                                            onChange={handleFormDataChage.bind(this, "functionname")}
                                            variant="outlined"
                                        >
                                        </TextField>
                                    </FormControl>
                                </Box>
                            </form>
                            :
                            null
                    }
                    {
                        dialogContent === "Add Module" ?
                            <form autoComplete="off">
                                <Box>
                                    <label required>Module Name</label>
                                    {
                                        reqList.map((item, index) => {
                                            return (
                                                <Box key={index} display="flex" alignItems="center" p={1} pl={0} justifyContent="space-between" style={{ width: '100%' }}>
                                                    <Grid container alignItems="center">
                                                        <Grid item xs={10} style={{ padding: '2px 2px 0 0' }}>
                                                            <FormControl fullWidth>
                                                                <TextField
                                                                    name="modulename"
                                                                    required
                                                                    fullWidth
                                                                    placeholder="Enter Module Name"
                                                                    value={item.modulename}
                                                                    onChange={e => handleAttrInputChange(e, index)}
                                                                    variant="outlined"
                                                                >
                                                                </TextField>
                                                            </FormControl>
                                                        </Grid>
                                                        <Grid item xs={2} style={{ padding: '2px 2px 0 0' }}>
                                                            <Box display="flex">
                                                                {
                                                                    reqList.length !== 1 ?
                                                                        <Button
                                                                            color="secondary"
                                                                            style={{ margin: '0px 10px 0 0' }}
                                                                            onClick={() => handleRemAttr(index)}
                                                                        >
                                                                            <Clear className={typographyClasses.text} />
                                                                        </Button>
                                                                        :
                                                                        <Button
                                                                            color="secondary"
                                                                            disabled={true}
                                                                            style={{ margin: '0px 10px 0 0' }}
                                                                        >
                                                                            <Clear className={typographyClasses.text} />
                                                                        </Button>
                                                                }
                                                                {
                                                                    reqList.length - 1 === index ?
                                                                        <Button
                                                                            color="primary"
                                                                            onClick={handleAddAttr}
                                                                            style={{ margin: '0px 10px 0 0' }}
                                                                        >
                                                                            <AddIcon className={typographyClasses.text} />
                                                                        </Button>
                                                                        :
                                                                        null
                                                                }
                                                            </Box>
                                                        </Grid>
                                                    </Grid>
                                                </Box>
                                            );
                                        })
                                    }
                                </Box>
                            </form>
                            :
                            null
                    }
                </DialogContent>
                <Dialogactions>
                    <Button onClick={() => setDialog(false)} color="secondary" variant="contained">
                        Close
                    </Button>
                    {
                        dialogContent === "Add Snippet" || dialogContent === "Add Module" ?
                            <Button
                                variant="contained"
                                color='primary'
                                className="api-btn"
                                onClick={() => FormDialogSubmit()}
                            >
                                Add
                            </Button>
                            :
                            null
                    }
                </Dialogactions>
            </Dialog>
            {
                confirmation !== '' ?
                    <Confirm confirmText={confirmation} clickHandler={handleConfirmation} />
                    :
                    null
            }
        </div>
    )
}

export default NewEditor