import React, { useState } from 'react'

// Material UI
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Grid from '@material-ui/core/Grid';
import FormHelperText from '@material-ui/core/FormHelperText';
import Chip from '@material-ui/core/Chip';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

//Material Icons
import ClearIcon from '@material-ui/icons/Clear';
import EditIcon from '@material-ui/icons/Edit';

// Custom Components
import MinMax from '../../Components/MinMax';


const TemplateIp = ({ clickHandler, schema,formname }) => {

    const [abc, setAbc] = useState(schema)
    const [curFormObj, setCurFormObj] = useState({})
    const [curPath, setCurPath] = useState("")
    const [formDialog, setFormDialog] = useState(false)
    const [order, setOrder] = useState(abc['order'])
    const [formValues, setFormValues] = useState({ "dummy": "dummmy" })

    const handleDelete = () => {

    }

    const Processobject = ({ Obj, parent, path }) => {
        return (
            <Box>
                <Grid container>
                    {
                        Object.keys(Obj['fields']).map((objkey, objindex) => {
                            if (eval(path)[objkey].type === "string" || eval(path)[objkey].type === "number") {
                                if (eval(path)[objkey].hasOwnProperty('value')) {
                                    eval(path)[objkey]['value'] = eval(path)[objkey].value
                                }
                                else if (eval(path)[objkey].hasOwnProperty('default')) {
                                    eval(path)[objkey]['value'] = eval(path)[objkey]['default']
                                }
                                else if (eval(path)[objkey].hasOwnProperty('allowedvalues') && eval(path)[objkey].allowedvalues.length > 0) {
                                    if (eval(path)[objkey].hasOwnProperty('mandatory') && eval(path)[objkey].mandatory) {
                                        eval(path)[objkey]['value'] = eval(path)[objkey].allowedvalues[0]
                                    }
                                    else {
                                        eval(path)[objkey]['value'] = ''
                                    }
                                }
                                else {
                                    if (eval(path)[objkey].type === "number") {
                                        if (eval(path)[objkey].hasOwnProperty('minvalue')) {
                                            eval(path)[objkey]['value'] = eval(path)[objkey].minvalue
                                        }
                                        else if (abc['fields'][objkey].hasOwnProperty('maxvalue')) {
                                            eval(path)[objkey]['value'] = eval(path)[objkey].maxvalue
                                        }
                                        else {
                                            eval(path)[objkey]['value'] = 0
                                        }
                                    }
                                    else {
                                        eval(path)[objkey]['value'] = ''
                                    }
                                }
                                if(eval(path)[objkey].hasOwnProperty('autopopulate') && eval(path)[objkey].autopopulate){
                                    let val = ''
                                    for(let i = 0;i<eval(path)[objkey]['populatevalues'].length;i++){
                                        if(abc['fields'][eval(path)[objkey]['populatevalues'][i]].hasOwnProperty('value') && abc['fields'][eval(path)[objkey]['populatevalues'][i]]['value'] !==""){
                                            val = val+abc['fields'][eval(path)[objkey]['populatevalues'][i]]['value']+'-'
                                        }
                                    }
                                    eval(path)[objkey]['value'] = val+"index"
                                }
                                let fmv = path.split('.fields').join('')
                                fmv = fmv.split('abc.').join('')
                                eval('formValues.' + fmv)[objkey] = eval(path)[objkey]['value']
                            }
                            else if (eval(path)[objkey].type === "arrayofjson") {
                                if (eval(path)[objkey].hasOwnProperty('value')) {
                                    // abc['fields'][objkey]['fields'] = abc['fields'][objkey].fields
                                }
                                else if (eval(path)[objkey].hasOwnProperty('fields')) {
                                    eval(path)[objkey]['value'] = [{ type: "object", "fields": eval(path)[objkey]['fields'] }]
                                }
                            }
                            else if (eval(path)[objkey].type === "Array") {
                                if (eval(path)[objkey].hasOwnProperty('value')) {
                                    // abc['fields'][objkey]['fields'] = abc['fields'][objkey].fields
                                }
                                else if (eval(path)[objkey].hasOwnProperty('default')) {
                                    eval(path)[objkey]['value'] = [eval(path)[objkey]['default']]
                                }
                            }
                            else if (eval(path)[objkey].type === "boolean") {
                                if (eval(path)[objkey].hasOwnProperty('value')) {
                                    eval(path)[objkey]['value'] = eval(path)[objkey].value
                                }
                                else if (eval(path)[objkey].hasOwnProperty('default')) {
                                    eval(path)[objkey]['value'] = eval(path)[objkey]['default']
                                }
                                let fmv = path.split('.fields').join('')
                                fmv = fmv.split('abc.').join('')
                                formValues[fmv][objkey] = eval(path)[objkey]['value']
                            }
                            else if (eval(path)[objkey].type === "object") {
                                let fmv = path.split('.fields').join('')
                                fmv = fmv.split('abc.').join('')
                                formValues[fmv][objkey] = {}
                            }
                            return (
                                <Grid item xs={12} md={Obj['fields'][objkey].type === "string" ? 6 : (Obj['fields'][objkey].type === "number" ? 6 : 12)} style={{ padding: Obj['fields'][objkey].type === "string" ? '0 5px 0 0' : (Obj['fields'][objkey].type === "number" ? '0 5px 0 0' : '0'), marginBottom: 8 }} key={objindex}>
                                    {
                                        Obj['fields'][objkey].type === "string" || Obj['fields'][objkey].type === "number" || Obj['fields'][objkey].type === "boolean" ?
                                            Obj['fields'][objkey].hasOwnProperty('dependson') === true ?
                                                Obj['fields'][Obj['fields'][objkey]['dependson'].split('/')[0]]['value'] === ("true" === Obj['fields'][objkey]['dependson'].split('/')[1]) ?
                                                    < Box style={{ borderColor: 'lightgray' }} key={objindex}>
                                                        <ProcessNormalInput Field={Obj['fields'][objkey]} parent={objkey} path={path + "." + objkey} />
                                                    </Box>
                                                    :
                                                    null
                                                :
                                                < Box style={{ borderColor: 'lightgray' }} key={objindex}>
                                                    <ProcessNormalInput Field={Obj['fields'][objkey]} parent={objkey} path={path + "." + objkey} />
                                                </Box>
                                            :
                                            null
                                    }
                                    {
                                        Obj['fields'][objkey].type === "object" ?
                                            Obj['fields'][objkey].hasOwnProperty('dependson') === true ?
                                                Obj['fields'][Obj['fields'][objkey]['dependson'].split('/')[0]]['value'] === ("true" === Obj['fields'][objkey]['dependson'].split('/')[1]) ?
                                                    <Box p={1} border={1} style={{ borderColor: 'lightgray' }} key={objindex} pb={0}>
                                                        <Box mb={1} display="flex" alignItems="center">
                                                            <Box mr={4}>
                                                                <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;{Obj['fields'][objkey].hasOwnProperty('description') === true ? Obj['fields'][objkey]['description'] : ''}</Typography>
                                                            </Box>
                                                        </Box>
                                                        <Box>
                                                            <Processobject Obj={Obj['fields'][objkey]} parent={objkey} path={path + "." + objkey + ".fields"} />
                                                        </Box>
                                                    </Box>
                                                    :
                                                    null
                                                :
                                                <Box p={1} border={1} style={{ borderColor: 'lightgray' }} key={objindex} pb={0}>
                                                    <Box mb={1} display="flex" alignItems="center">
                                                        <Box mr={4}>
                                                            <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;{Obj['fields'][objkey].hasOwnProperty('description') === true ? Obj['fields'][objkey]['description'] : ''}</Typography>
                                                        </Box>
                                                    </Box>
                                                    <Box>
                                                        <Processobject Obj={Obj['fields'][objkey]} parent={objkey} path={path + "." + objkey + ".fields"} />
                                                    </Box>
                                                </Box>
                                            :
                                            null
                                    }
                                    {
                                        Obj['fields'][objkey].type === "arrayofjson" ?
                                            <Box key={objindex} p={1} pb={0} border={1} style={{ borderColor: 'lightgray' }}>
                                                <Box mb={1} display="flex" alignItems="center">
                                                    <Box mr={4}>
                                                        <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;({Obj['fields'][objkey].hasOwnProperty('description') === true ? Obj['fields'][objkey]['description'] : ''})</Typography>
                                                    </Box>
                                                    <Box>
                                                        <Button
                                                            variant="outlined"
                                                            color="primary"
                                                            onClick={() => handleAddJSOn(path + "." + objkey)}
                                                        >
                                                            Add {objkey}
                                                        </Button>
                                                    </Box>
                                                </Box>
                                                {/* {
                                                    Obj['fields'][objkey]['fields'].length === undefined ?
                                                        <Box border={1} style={{ borderColor: 'lightgrey' }} p={1}>
                                                            <Box display="flex" justifyContent="flex-end">
                                                                <Button
                                                                    variant="text"
                                                                    color="primary"
                                                                    startIcon={<EditIcon />}
                                                                    onClick={() => handleEditForm(Obj['fields'][objkey]['fields'], path + "." + objkey + ".fields")}
                                                                >
                                                                    Edit
                                                                </Button>
                                                            </Box>
                                                            <Processobject Obj={Obj['fields'][objkey]} parent={objkey} path={path + "." + objkey + ".fields"} />
                                                        </Box>

                                                        :
                                                        null
                                                }
                                                {
                                                    Obj['fields'][objkey]['fields'].length !== undefined ? */}
                                                <Processarrayofjson List={Obj['fields'][objkey]['fields']} parent={objkey} path={path + "." + objkey + ".fields"} />
                                                {/* : */}
                                                {/* null */}
                                                {/* } */}
                                            </Box>
                                            :
                                            null
                                    }
                                    {
                                        Obj['fields'][objkey].type === "array" ?
                                            <Box key={objindex} p={1} style={{ borderColor: 'lightgray' }}>
                                                <Box mb={1} display="flex" alignItems="center">
                                                    <Box mr={4}>
                                                        <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;({Obj['fields'][objkey].hasOwnProperty('description') === true ? Obj['fields'][objkey]['description'] : ''})</Typography>
                                                    </Box>
                                                    <Box>
                                                        <Button
                                                            variant="outlined"
                                                            color="primary"
                                                        >
                                                            Add {objkey}
                                                        </Button>
                                                    </Box>
                                                </Box>
                                                <Box display="flex" flexWrap="wrap" alignItems="center">
                                                    {
                                                        Obj['fields'][objkey].fields.map((arritem, arrindex) => {
                                                            return (
                                                                <Chip label={arritem} onDelete={handleDelete} color="primary" key={arrindex} />
                                                            )
                                                        })
                                                    }
                                                </Box>
                                            </Box>
                                            :
                                            null
                                    }
                                </Grid>
                            )
                        })
                    }
                </Grid>
            </Box >
        )
    }

    const ProcessNormalInput = ({ Field, parent, path }) => {
        let value = ''
        if (Field.hasOwnProperty('value')) {
            value = Field.value
        }
        else if (Field.hasOwnProperty('default')) {
            value = Field['default']
        }
        else if (Field.hasOwnProperty('allowedvalues') && Field.allowedvalues.length > 0) {
            value = ""
        }
        else {
            if (Field.type === "number") {
                if (Field.hasOwnProperty('minvalue')) {
                    value = Field.minvalue
                }
                else if (Field.hasOwnProperty('maxvalue')) {
                    value = Field.maxvalue
                }
                else {
                    value = 0
                }
            }
            else {
                value = ''
            }
        }
        if(Field.hasOwnProperty('autopopulate') && Field.autopopulate){
            let val = ''
            for(let i = 0;i<Field['populatevalues'].length;i++){
                if(abc['fields'][Field['populatevalues'][i]].hasOwnProperty('value') && abc['fields'][Field['populatevalues'][i]]['value'] !==""){
                    val = val+abc['fields'][Field['populatevalues'][i]]['value']+'-'
                }
            }
            value = val+"index"
        }
        let fmv = path.split('.fields').join('')
        fmv = fmv.split('abc.').join('')
        eval['formValues.' + fmv] = eval(path)['value']
        return (
            <Box p={1} display="flex" flexDirection="column">
                {
                    Field.type === "string" ?
                        <Box>
                            {
                                Field.hasOwnProperty('allowedvalues') === true ?
                                    <FormControl fullWidth>
                                        <label htmlFor={parent} required={Field.hasOwnProperty('mandatory') === true ? (Field['mandatory'] === true ? true : false) : false} style={{ fontWeight: 'bold' }}>{parent}{
                                            Field.hasOwnProperty('ConstraintDescription') === true ?
                                                <span style={{ fontSize: '0.8rem', fontWeight: 'normal', color: 'grey' }}>&nbsp;({Field.ConstraintDescription})</span>
                                                :
                                                null
                                        }</label>
                                        <Select
                                            native
                                            disabled={Field.hasOwnProperty('readonly') === true ? Field['readonly'] : false}
                                            labelId={parent}
                                            id={parent}
                                            variant="outlined"
                                            // defaultValue=""
                                            value={value}
                                            onChange={handleFormChange.bind(this, parent, Field.type, -1, 0, path)}
                                        >
                                            <option value="">Select {parent}</option>
                                            {
                                                Field.hasOwnProperty('condition') === true ?
                                                    Field.condition.hasOwnProperty('onchange') === true ?
                                                        Field.allowedvalues.map((valItem, valIndex) => {
                                                            let pt = path.split('.')
                                                            pt.pop()
                                                            pt = pt.join('.')
                                                            let oper = Field.condition.onchange.split('/')
                                                            let val = eval(pt + '.' + oper[1] + ".value")
                                                            if (valItem !== val) {
                                                                return (
                                                                    <option fields={valItem} key={valIndex}>{valItem}</option>
                                                                )
                                                            }
                                                            // else{
                                                            //     eval(path)['value'] = ''
                                                            //     eval['formValues.' + fmv] = ''
                                                            // }
                                                        })
                                                        :
                                                        Field.allowedvalues.map((valItem, valIndex) => {
                                                            return (
                                                                <option fields={valItem} key={valIndex}>{valItem}</option>
                                                            )
                                                        })
                                                    :
                                                    Field.allowedvalues.map((valItem, valIndex) => {
                                                        return (
                                                            <option fields={valItem} key={valIndex}>{valItem}</option>
                                                        )
                                                    })
                                            }
                                        </Select>
                                        {
                                            Field.hasOwnProperty('Description') === true ?
                                                <FormHelperText>{Field.Description}</FormHelperText>
                                                :
                                                null
                                        }
                                    </FormControl>
                                    :
                                    null
                            }
                            {
                                Field.hasOwnProperty('allowedvalues') === false ?
                                    <FormControl fullWidth>
                                        <label htmlFor={parent} required={Field.hasOwnProperty('mandatory') === true ? (Field['mandatory'] === true ? true : false) : false} style={{ fontWeight: 'bold' }}>{parent}{
                                            Field.hasOwnProperty('ConstraintDescription') === true ?
                                                <span style={{ fontSize: '0.8rem', fontWeight: 'normal', color: 'grey' }}>&nbsp;({Field.ConstraintDescription})</span>
                                                :
                                                null
                                        }</label>
                                        <TextField
                                            id={parent}
                                            helperText={Field.hasOwnProperty('Description') === true ? Field.Description : ''}
                                            defaultValue={value}
                                            disabled={Field.hasOwnProperty('readonly') === true ? Field['readonly'] : false}
                                            fullWidth
                                            placeholder={"Enter " + parent}
                                            onChange={handleFormChange.bind(this, parent, Field.type, -1, 0, path)}
                                            variant="outlined"
                                        >
                                        </TextField>
                                    </FormControl>
                                    :
                                    null
                            }
                        </Box>
                        :
                        null
                }
                {
                    Field.type === "number" ?
                        <Box>
                            {
                                Field.hasOwnProperty('allowedvalues') === true ?
                                    <FormControl fullWidth>
                                        <label htmlFor={parent} required={Field.hasOwnProperty('mandatory') === true ? (Field['mandatory'] === true ? true : false) : false} style={{ fontWeight: 'bold' }}>{parent}{
                                            Field.hasOwnProperty('ConstraintDescription') === true ?
                                                <span style={{ fontSize: '0.8rem', fontWeight: 'normal', color: 'grey' }}>&nbsp;({Field.ConstraintDescription})</span>
                                                :
                                                null
                                        }</label>
                                        <Select
                                            native
                                            labelId={parent}
                                            id={parent}
                                            // defaultValue=""
                                            value={value}
                                            variant="outlined"
                                            disabled={Field.hasOwnProperty('readonly') === true ? Field['readonly'] : false}
                                            onChange={handleFormChange.bind(this, parent, Field.type, -1, 0, path)}
                                        >
                                            <option value="">Select {parent}</option>
                                            {
                                                Field.hasOwnProperty('condition') === true ?
                                                    Field.condition.hasOwnProperty('onchange') === true ?
                                                        Field.allowedvalues.map((valItem, valIndex) => {
                                                            let pt = path.split('.')
                                                            pt.pop()
                                                            pt = pt.join('.')
                                                            let oper = Field.condition.onchange.split('/')
                                                            let val = eval(pt + '.' + oper[1] + ".value")
                                                            if (valItem !== val) {
                                                                return (
                                                                    <option fields={valItem} key={valIndex}>{valItem}</option>
                                                                )
                                                            }
                                                            // else{
                                                            //     eval(path)['value'] = ''
                                                            //     eval['formValues.' + fmv] = ''
                                                            // }
                                                        })
                                                        :
                                                        Field.allowedvalues.map((valItem, valIndex) => {
                                                            return (
                                                                <option fields={valItem} key={valIndex}>{valItem}</option>
                                                            )
                                                        })
                                                    :
                                                    Field.allowedvalues.map((valItem, valIndex) => {
                                                        return (
                                                            <option fields={valItem} key={valIndex}>{valItem}</option>
                                                        )
                                                    })
                                            }
                                        </Select>
                                        {
                                            Field.hasOwnProperty('Description') === true ?
                                                <FormHelperText>{Field.Description}</FormHelperText>
                                                :
                                                null
                                        }
                                    </FormControl>
                                    :
                                    null
                            }
                            {
                                Field.hasOwnProperty('allowedvalues') === false ?
                                    <Box>
                                        <FormControl fullWidth>
                                            <label htmlFor={parent} required={Field.hasOwnProperty('mandatory') === true ? (Field['mandatory'] === true ? true : false) : false} style={{ fontWeight: 'bold' }}>{parent}{
                                                Field.hasOwnProperty('ConstraintDescription') === true ?
                                                    <span style={{ fontSize: '0.8rem', fontWeight: 'normal', color: 'grey' }}>&nbsp;({Field.ConstraintDescription})</span>
                                                    :
                                                    null
                                            }</label>
                                            <TextField
                                                id={parent}
                                                helperText={Field.hasOwnProperty('Description') === true ? Field.Description : ''}
                                                defaultValue={value}
                                                fullWidth
                                                disabled={Field.hasOwnProperty('readonly') === true ? Field['readonly'] : false}
                                                type="number"
                                                placeholder={"Enter " + parent}
                                                onChange={handleFormChange.bind(this, parent, Field.type, Field.hasOwnProperty('minvalue') ? Field.minvalue : "", Field.hasOwnProperty('maxvalue') ? Field.maxvalue : "", path)}
                                                variant="outlined"
                                            >
                                            </TextField>
                                        </FormControl>
                                    </Box>
                                    :
                                    null
                            }
                        </Box>
                        :
                        null
                }
                {
                    Field.type === "boolean" ?
                        <FormControl component="fieldset" fullWidth>
                            <label htmlFor="Region" required={Field.hasOwnProperty('mandatory') === true ? (Field['mandatory'] === true ? true : false) : false}>{parent}</label>
                            <RadioGroup aria-label="gender" value={value.toString()} defaultValue="true" onChange={handleFormChange.bind(this, parent, Field.type, -1, 0, path)} style={{ flexDirection: 'row' }}>
                                <FormControlLabel value="true" control={<Radio />} label="true" disabled={Field.hasOwnProperty('readonly') === true ? (Field.readonly === true ? true : false) : false} />
                                <FormControlLabel value="false" control={<Radio />} label="false" disabled={Field.hasOwnProperty('readonly') === true ? (Field.readonly === true ? true : false) : false} />
                            </RadioGroup>
                        </FormControl>
                        :
                        null
                }
            </Box>
        )
    }

    const handleAddJSOn = (path) => {
        // if (eval(path)['fields'].length === undefined) {
        //     let obj = abc, tmp = { "type": "object", "fields": eval(path)['fields'] }
        //     path = path.replace('abc', 'obj')
        //     // eval
        //     eval(path)['fields'] = [tmp, tmp]
        //     // console.log(eval(path))
        //     let tmporder = [...order]
        //     // setAbc({})
        //     setOrder([])
        //     setAbc(obj)
        //     setOrder(tmporder)
        // }
        // else {
        let obj = abc
        path = path.replace('abc', 'obj')
        eval(path)['fields'].push(eval(path)['fields'][0])
        let tmporder = [...order]
        // setAbc({})
        setOrder([])
        setAbc(obj)
        setOrder(tmporder)
        // }
    }

    const handleRemJson = (path, index) => {
        let obj = abc
        path = path.replace('abc', 'obj')
        let temparr = eval(path)
        temparr = temparr.splice(index, 1)
        // eval(path) = temparr
        let tmppath = path.split('.')
        tmppath.pop()
        tmppath = tmppath.join('.')
        eval(tmppath)['fields'] = temparr
        let tmporder = [...order]
        // setAbc({})
        setOrder([])
        setAbc(obj)
        setOrder(tmporder)
        // console.log(eval(path), index)
    }

    const Processarrayofjson = ({ List, path, parent }) => {
        return (
            <Box>
                {
                    List.map((item, index) => {
                        return (
                            <Box key={index} border={1} mb={1} style={{ borderColor: 'lightgray' }} p={1}>
                                <Box display="flex" alignItems="center" justifyContent="flex-end">
                                    <Box mr={2}>
                                        <Button
                                            variant="text"
                                            color="primary"
                                            startIcon={<EditIcon />}
                                            onClick={() => handleEditForm(item['fields'], path + "[" + index + "]")}
                                        >
                                            Edit
                                        </Button>
                                    </Box>
                                    <Box>
                                        <Button
                                            variant="text"
                                            color="secondary"
                                            onClick={() => handleRemJson(path, index)}
                                        >
                                            <ClearIcon />
                                        </Button>
                                    </Box>
                                </Box>
                                <Processobject Obj={item} parent={parent} path={path + "[" + index + "].fields"} />
                            </Box>
                        )
                    })
                }
            </Box>
        )
    }

    const IterateJSON = () => {
        return (
            <Box p={1} className="template-design" style={{ width: '100%', borderColor: 'lightgray' }} mt={2} mb={2} pb={0} pr={0.5} border={1}>
                {/* <Box style={{ width: '100%' }} >
                    <Box style={{ width: '100%' }} display="flex" justifyContent="flex-end" >
                        <Button
                            variant="text"
                            color="primary"
                            startIcon={<EditIcon />}
                            onClick={() => handleEditForm(abc['fields'], "abc")}
                        >
                            Edit
                        </Button>
                    </Box>
                </Box> */}
                <Grid container>
                    {
                        order.map((objkey, objindex) => {
                            if (abc['fields'][objkey].type === "string" || abc['fields'][objkey].type === "number") {
                                if (abc['fields'][objkey].hasOwnProperty('value')) {
                                    abc['fields'][objkey]['value'] = abc['fields'][objkey].value
                                }
                                else if (abc['fields'][objkey].hasOwnProperty('default')) {
                                    abc['fields'][objkey]['value'] = abc['fields'][objkey]['default']
                                }
                                else if (abc['fields'][objkey].hasOwnProperty('allowedvalues') && abc['fields'][objkey].allowedvalues.length > 0) {
                                    if (abc['fields'][objkey].hasOwnProperty('mandatory') && abc['fields'][objkey]['mandatory']) {
                                        abc['fields'][objkey]['value'] = abc['fields'][objkey].allowedvalues[0]
                                    }
                                    else {
                                        abc['fields'][objkey]['value'] = ''
                                    }
                                }
                                else {
                                    if (abc['fields'][objkey].type === "number") {
                                        if (abc['fields'][objkey].hasOwnProperty('minvalue')) {
                                            abc['fields'][objkey]['value'] = abc['fields'][objkey].minvalue
                                        }
                                        else if (abc['fields'][objkey].hasOwnProperty('maxvalue')) {
                                            abc['fields'][objkey]['value'] = abc['fields'][objkey].maxvalue
                                        }
                                        else {
                                            abc['fields'][objkey]['value'] = 0
                                        }
                                    }
                                    else {
                                        abc['fields'][objkey]['value'] = ''
                                    }
                                }
                                if(abc['fields'][objkey].hasOwnProperty('autopopulate') && abc['fields'][objkey].autopopulate){
                                    let val = ''
                                    for(let i = 0;i<abc['fields'][objkey]['populatevalues'].length;i++){
                                        if(abc['fields'][abc['fields'][objkey]['populatevalues'][i]].hasOwnProperty('value') && abc['fields'][abc['fields'][objkey]['populatevalues'][i]]['value'] !==""){
                                            val = val+abc['fields'][abc['fields'][objkey]['populatevalues'][i]]['value']+'-'
                                        }
                                    }
                                    abc['fields'][objkey]['value'] = val+"index"
                                }
                                formValues[objkey] = abc['fields'][objkey]['value']
                            }
                            else if (abc['fields'][objkey].type === "arrayofjson") {
                                if (abc['fields'][objkey].hasOwnProperty('value')) {
                                    // abc['fields'][objkey]['fields'] = abc['fields'][objkey].fields
                                }
                                else if (abc['fields'][objkey].hasOwnProperty('fields')) {
                                    abc['fields'][objkey]['value'] = [{ type: "object", "fields": abc['fields'][objkey]['fields'] }]
                                }
                            }
                            else if (abc['fields'][objkey].type === "Array") {
                                if (abc['fields'][objkey].hasOwnProperty('fields')) {
                                    // abc['fields'][objkey]['fields'] = abc['fields'][objkey].fields
                                }
                                else if (abc['fields'][objkey].hasOwnProperty('Default')) {
                                    abc['fields'][objkey]['fields'] = [abc['fields'][objkey]['Default']]
                                }
                            }
                            else if (abc['fields'][objkey].type === "boolean") {
                                if (abc['fields'][objkey].hasOwnProperty('value')) {
                                    abc['fields'][objkey]['value'] = abc['fields'][objkey].value
                                }
                                else if (abc['fields'][objkey].hasOwnProperty('default')) {
                                    abc['fields'][objkey]['value'] = [abc['fields'][objkey]['default']]
                                }
                            }
                            else if (abc['fields'][objkey].type === "object") {
                                formValues[objkey] = {}
                            }
                            return (
                                <Grid item xs={12} md={abc['fields'][objkey].type === "string" ? 6 : (abc['fields'][objkey].type === "number" ? 6 : 12)} style={{ padding: '0px 5px 0 0', marginBottom: 8 }} key={objindex}>
                                    {
                                        abc['fields'][objkey].type === "string" || abc['fields'][objkey].type === "number" || abc['fields'][objkey].type === "boolean" ?
                                            abc['fields'][objkey].hasOwnProperty('dependson') === true ?
                                                abc['fields'][abc['fields'][objkey]['dependson'].split('/')[0]]['value'] === ("true" === abc['fields'][objkey]['dependson'].split('/')[1]) ?
                                                    <Box key={objindex} style={{ borderColor: 'lightgray' }}>
                                                        <ProcessNormalInput Field={abc['fields'][objkey]} parent={objkey} path={"abc.fields." + objkey} />
                                                    </Box>
                                                    :
                                                    null
                                                :
                                                <Box key={objindex} style={{ borderColor: 'lightgray' }}>
                                                    <ProcessNormalInput Field={abc['fields'][objkey]} parent={objkey} path={"abc.fields." + objkey} />
                                                </Box>
                                            :
                                            null
                                    }
                                    {
                                        abc['fields'][objkey].type === "object" ?
                                            abc['fields'][objkey].hasOwnProperty('dependson') === true ?
                                                abc['fields'][abc['fields'][objkey]['dependson'].split('/')[0]]['value'] === ("true" === abc['fields'][objkey]['dependson'].split('/')[1]) ?
                                                    <Box key={objindex} p={1} border={1} style={{ borderColor: 'lightgray' }} pb={0}>
                                                        <Box mb={1} display="flex" alignItems="center">
                                                            <Box mr={4}>
                                                                <Typography align="left" style={{ fontWeight: 'bold' }}>{abc['fields'][objkey].title}&nbsp;{abc['fields'][objkey].hasOwnProperty('Description') === true ? abc['fields'][objkey]['Description'] : ''}</Typography>
                                                            </Box>
                                                        </Box>
                                                        <Box>
                                                            <Processobject Obj={abc['fields'][objkey]} parent={objkey} path={"abc.fields." + objkey + ".fields"} />
                                                        </Box>
                                                    </Box>
                                                    :
                                                    null
                                                :
                                                <Box key={objindex} p={1} border={1} style={{ borderColor: 'lightgray' }} pb={0}>
                                                    <Box mb={1} display="flex" alignItems="center">
                                                        <Box mr={4}>
                                                            <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;{abc['fields'][objkey].hasOwnProperty('Description') === true ? abc['fields'][objkey]['Description'] : ''}</Typography>
                                                        </Box>
                                                    </Box>
                                                    <Box>
                                                        <Processobject Obj={abc['fields'][objkey]} parent={objkey} path={"abc.fields." + objkey + ".fields"} />
                                                    </Box>
                                                </Box>
                                            :
                                            null
                                    }
                                    {
                                        abc['fields'][objkey].type === "Array" ?
                                            <Box key={objindex} p={1} style={{ borderColor: 'lightgray' }}>
                                                <Box mb={1} display="flex" alignItems="center">
                                                    <Box mr={4}>
                                                        <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;({abc['fields'][objkey].hasOwnProperty('Description') === true ? abc['fields'][objkey]['Description'] : ''})</Typography>
                                                    </Box>
                                                    <Box>
                                                        <Button
                                                            variant="outlined"
                                                            color="primary"
                                                        >
                                                            Add {objkey}
                                                        </Button>
                                                    </Box>
                                                </Box>
                                                <Box display="flex" flexWrap="wrap" alignItems="center">
                                                    {
                                                        abc['fields'][objkey].fields.map((arritem, arrindex) => {
                                                            return (
                                                                <Chip key={arrindex} label={arritem} onDelete={handleDelete} color="primary" />
                                                            )
                                                        })
                                                    }
                                                </Box>
                                            </Box>
                                            :
                                            null
                                    }
                                    {
                                        abc['fields'][objkey].type === "arrayofjson" ?
                                            <Box key={objindex} p={1} border={1} style={{ borderColor: 'lightgray' }}>
                                                <Box mb={1} display="flex" alignItems="center">
                                                    <Box mr={4}>
                                                        <Typography align="left" style={{ fontWeight: 'bold' }}>{objkey}&nbsp;({abc['fields'][objkey].hasOwnProperty('Description') === true ? abc['fields'][objkey]['Description'] : ''})</Typography>
                                                    </Box>
                                                    <Box>
                                                        <Button
                                                            variant="outlined"
                                                            color="primary"
                                                            // onClick={() => handleAddJSOn("abc.fields." + objkey)}
                                                            onClick={() => handleAddJSOn("abc.fields." + objkey)}
                                                        >
                                                            Add {objkey}
                                                        </Button>
                                                    </Box>
                                                </Box>
                                                <Box p={1} border={1} style={{ borderColor: 'lightgray' }} pb={0}>
                                                    {/* {
                                                        abc['fields'][objkey]['fields'].length === undefined ?
                                                            <Box>
                                                                <Box>
                                                                    <Button
                                                                        variant="text"
                                                                        color="primary"
                                                                        startIcon={<EditIcon />}
                                                                        onClick={() => handleEditForm(abc['fields'][objkey]['fields'], "abc.fields." + objkey + ".fields")}
                                                                    >
                                                                        Edit
                                                                    </Button>
                                                                </Box>
                                                                <Processobject Obj={abc['fields'][objkey]} parent={objkey} path={"abc.fields." + objkey + ".fields"} />
                                                            </Box>
                                                            :
                                                            null
                                                    }
                                                    {
                                                        abc['fields'][objkey]['fields'].length !== undefined ? */}
                                                    <Processarrayofjson List={abc['fields'][objkey]['fields']} parent={objkey} path={"abc.fields." + objkey + ".fields"} />
                                                    {/* : */}
                                                    {/* null */}
                                                    {/* } */}
                                                </Box>
                                            </Box>
                                            :
                                            null
                                    }
                                </Grid>
                            )
                        })
                    }
                </Grid>
            </Box>
        )
    }

    const handleEditForm = (Obj, path) => {
        setCurFormObj(Obj)
        setCurPath(path)
        setFormDialog(true)
    }

    const handleFormChange = (field, type, minvalue, maxvalue, path, event) => {
        console.log(formValues)
        console.log(abc)
        if (type === 'number') {
            if (maxvalue !== "") {
                if (parseInt(event.target.value) > maxvalue) {
                    // setMessage('Maximum limit reached')
                    // setSeverrity('error')
                    // setOpenAlert(true)
                    eval(path)['value'] = maxvalue
                    event.target.value = maxvalue
                    // setDisableBtn(true)
                }
                else {
                    eval(path)['value'] = parseInt(event.target.value)
                    event.target.value = parseInt(event.target.value)
                }
            }
            else {
                // setDisableBtn(false)
                eval(path)['value'] = parseInt(event.target.value)
            }
            if (minvalue !== "") {
                if (parseInt(event.target.value) < minvalue) {
                    // setOpenAlert(true)
                    // setMessage('Minimum limit reached')
                    // setSeverrity('error')
                    eval(path)['value'] = minvalue
                    event.target.value = minvalue
                    // setDisableBtn(true)
                }
                else {
                    eval(path)['value'] = parseInt(event.target.value)
                    event.target.value = parseInt(event.target.value)
                }
            }
            else {
                if (parseInt(event.target.value) > 0) {
                    // setDisableBtn(false)
                    eval(path)['value'] = parseInt(event.target.value)
                }
                else {
                    eval(path)['value'] = 0
                    event.target.value = 0
                }
            }
        }
        else if (type === 'yaml') {
            eval(path)['value'] = event
        }
        else if (type === "boolean") {
            eval(path)['value'] = ("true" === event.target.value)
        }
        else {
            // setDisableBtn(false)

            eval(path)['value'] = event.target.value;
        }
        let ords = [...order]
        setOrder([])
        setOrder(ords)
        let fmv = path.split('.fields').join('')
        fmv = fmv.split('obj.').join('')
        eval['formValues.' + fmv] = eval(path)['value']
    }

    const handleFormSubmit = (boolean) => {
        delete formValues['dummy']
        if (boolean) {
            clickHandler(boolean, formValues)
        }
        else {
            clickHandler(boolean)
        }
    }

    return (
        <div>
            <Dialog
                open={true}
                onClose={() => handleFormSubmit(false)}
                aria-labelledby="acc-dialog-title"
                fullWidth={true}
                maxWidth="md"
            >
                <DialogTitle id="acc-dialog-title" style={{ borderBottom: '1px solid lightgrey' }}>{formname}</DialogTitle>
                <DialogContent>
                    <Box>
                        <IterateJSON Obj={abc} />
                    </Box>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => handleFormSubmit(false)} color="primary">
                        Cancel
                    </Button>
                    <Button
                        color="primary"
                        variant="contained"
                        className='api-btn'
                        onClick={handleFormSubmit}
                    >
                        Submit
                	</Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default TemplateIp;