import React, { useState, useEffect } from 'react';

// Custom Components
import Loader from '../../Components/Loader';
import TopNav from '../../Components/AppNav';
import Alert from '../../Components/Alert';
import Footer from '../../Components/Footer/index';
import Cognitofunctions from '../Cognito/CognitoFunctions';
import AddService from './AddService'
import DataList from '../../Utils/DataList';
import apidomain from '../../Utils/ApiDomain';
import apipath from '../../Utils/Apipath';
import HandleApi from '../../Utils/HandleApi';

// Styles
import ContainerStyles from '../../assets/styles/ContainerStyles'
import TypographyStyles from '../../assets/styles/TypographySyles'

// Material UI
import Container from '@material-ui/core/Container';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

// Material Icons
import AddIcon from '@material-ui/icons/Add';
import RefreshIcon from '@material-ui/icons/Refresh';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';

// React Helmet
import Helmet from "react-helmet/es/Helmet";


const ServicesList = ({ match }) => {
    const [serviceList, setServiceList] = useState([])
    const [loader, setLoader] = useState(false)
    const [openAlert, setOpenAlert] = useState(false)
    const [severity, setSeverrity] = useState('')
    const [message, setMessage] = useState('')
    const [loadTimeout, setLoadTimeout] = useState(false)
    const [newServ, setNewServ] = useState(false)
    const [selectTemplate, setSelectTemplate] = useState(false)

    const containerClasses = ContainerStyles()
    const typographyClasses = TypographyStyles()


    useEffect(() => {
        checkrole()
    }, [])

    const checkrole = () => {
        Cognitofunctions.CurrentUser(function (success, data) {
            if (success) {
                let apidata = {
                    path: apipath['designapi'] + "/service/templates",
                    ref: "GetTemplates",
                    method: "GET",
                    body: {},
                    headers: {
                        headers: true,
                        apidomain: apidomain.vmurl
                    }
                }
                manageAPIs(apidata)
            }
            else {
                setMessage('You are not authorized to access this page.')
                setSeverrity('error')
                setOpenAlert(true)
                setLoadTimeout(false)
                setTimeout(function () {
                    window.location.href = window.location.origin + "/#/app/login"
                    window.location.reload()
                }, 1000)
            }
        })
    }

    const manageAPIs = (apidata) => {
        HandleApi.handleAPI(apidata.path, apidata.method, JSON.stringify(apidata.body), apidata.headers)
            .then(result => {
                if (result.hasOwnProperty('message') && result.message !== "") {
                    setMessage(result.message)
                    setSeverrity('info')
                    setOpenAlert(true)
                }
                if ((result.hasOwnProperty('statusCode') && result.statusCode === 200) || (result.hasOwnProperty('status') && (result.status === 200 || result.status === "200"))) {
                    if (apidata.ref === "GetTemplates") {
                        DataList.serviceTemplates = result.body
                        let temps = []
                        for (let i = 0; i < result.body.length; i++) {
                            let zz = result.body[i]
                            if (!zz.hasOwnProperty('name')) {
                                zz['name'] = 'AWS-PYTHON-APIGW-FLASK-EKS-DYNAMODB-COGNITO'
                            }
                            zz['is_coming_soon'] = false
                            temps.push(zz)
                        }
                        setServiceList(temps)
                        setLoader(false)
                    }
                }
                else {
                    if (result.hasOwnProperty('error_message')) {
                        setMessage(result.error_message)
                    }
                    else if (result.hasOwnProperty('errorMessage')) {
                        setMessage(result.errorMessage)
                    }
                    else {
                        setMessage('Some internal error, Please try after sometime')
                    }
                    if (apidata.ref === "GetLiveResOP" || apidata.ref === "GetLiveResLogs") {
                        if (apidata.hasOwnProperty('show')) {
                            if (apidata.show) {
                                setSeverrity('error')
                                setOpenAlert(true)
                            }
                            setLoader(false)
                        }
                    }
                    else {
                        setSeverrity('error')
                        setOpenAlert(true)
                        setLoader(false)
                    }
                }
            })
            .catch(err => {
                setMessage("Some internal error,Please try after some time.")
                setSeverrity('error')
                setOpenAlert(true)
                setLoader(false)
            })
    }

    const handleActions = (apidata) => {
        if (apidata.Action === 'redirect') {
            window.open(apidata.url, '_blank')
        }
        else if (apidata.Action === 'refresh') {
            setServiceList([])
            setLoader(true)
            let apidata = {
                path: apipath['designapi'] + "/service/templates",
                ref: "GetTemplates",
                method: "GET",
                body: {},
                headers: {
                    headers: true,
                    apidomain: apidomain.vmurl
                }
            }
            manageAPIs(apidata)
        }
    }

    const handleAlert = () => {
        setOpenAlert(false)
    }

    const CancelAddService = () => {
        setNewServ(false)
    }

    const ProcessServiceTemplateList = () => {
        return (
            <Grid container>
                {
                    serviceList.map((serv, index) => {
                        return (
                            <Grid xs={12} item style={{ padding: '0 0 10px 0' }} key={index}>
                                <Box p={1} pl={2} pr={2} borderRadius={5} border={3} style={{ cursor: 'pointer', borderColor: selectTemplate === true && serv.is_coming_soon === false ? "#3f51b5" : 'lightgray', opacity: serv.is_coming_soon === false ? "1" : "0.5" }} bgcolor={selectTemplate === true && serv.is_coming_soon === false ? "#3f51b5" : "#f5f5f5"} onClick={() => serv.is_coming_soon === false ? handleSelectTemplate(serv) : ""} color={selectTemplate === true && serv.is_coming_soon === false ? "white" : ""}>
                                    <Grid container alignItems="center" justify="space-between">
                                        <Grid xs={1} item>
                                            <Box display="flex" alignItems="center" width={30} height={30} style={{ borderRadius: '50%', borderColor: selectTemplate === true && serv.is_coming_soon === false ? "#3f51b5" : 'lightgray' }} border={3} bgcolor="white" justifyContent="center">
                                                <Box width={18} height={18} style={{ borderRadius: '50%', borderColor: selectTemplate === true ? "#3f51b5" : 'lightgray' }} border={selectTemplate === true && serv.is_coming_soon === false ? 2 : 0} bgcolor={selectTemplate === true && serv.is_coming_soon === false ? "#3f51b5" : "white"}>

                                                </Box>
                                            </Box>
                                        </Grid>
                                        <Grid item xs={11}>
                                            <Box display="flex" flexDirection="column">
                                                <Box display="flex" mb={1} color={selectTemplate === true && serv.is_coming_soon === false ? "white" : "#555555"}>
                                                    <Typography className={`${typographyClasses.h6} ${typographyClasses.bold}`}>{serv.name}</Typography>
                                                </Box>
                                                <Box display="flex" alignItems="center" flexWrap="wrap">
                                                    {
                                                        Object.keys(serv).map((fld, flindex) => {
                                                            if (fld !== "servicetype" && fld !== "optionname" && fld !== "is_coming_soon") {
                                                                return (
                                                                    <Box display="flex" alignItems="center" mr={6} mb={1} key={flindex}>
                                                                        <Box display="flex" mr={0.5}>
                                                                            <Typography className={`${typographyClasses.text} ${typographyClasses.bold}`} color={selectTemplate === true && serv.is_coming_soon === false ? "white" : "primary"}>{fld}&nbsp;&nbsp;-&nbsp;&nbsp;</Typography>
                                                                        </Box>
                                                                        <Box display="flex">
                                                                            <Typography className={`${typographyClasses.text}`}>{serv[fld]}</Typography>
                                                                        </Box>
                                                                    </Box>
                                                                )
                                                            }
                                                        })
                                                    }
                                                </Box>
                                                {
                                                    serv.is_coming_soon === true ?
                                                        <Box display="flex" alignItems="center" mr={6} mb={1}>
                                                            <Box display="flex" mr={0.5} color="#151515">
                                                                <Typography className={`${typographyClasses.text} ${typographyClasses.bold}`}>Coming soon</Typography>
                                                            </Box>
                                                        </Box>
                                                        :
                                                        null
                                                }
                                            </Box>
                                        </Grid>
                                    </Grid>
                                </Box>
                            </Grid>
                        )
                    })
                }
            </Grid>
        )
    }

    const handleSelectTemplate = (serv) => {
        sessionStorage.setItem('selectedtemplate',window.btoa(JSON.stringify(serv)))
        setSelectTemplate(true)
        setNewServ(true)
    }

    return (
        <div>
            <Helmet>
                <title>Service Catalog</title>
            </Helmet>
            <TopNav />
            <Alert openAlert={openAlert} severity={severity} message={message} handler={handleAlert} />
            <Container className={containerClasses.root} style={{ height: 'calc(100% - 81px)', marginBottom: '30px' }}>
                <Box style={{ width: '100%' }} p={1} pl={2} pr={2}>
                    <Box display="flex" justifyContent="space-between" alignItems="center" style={{ width: '100%' }} mb={1}>
                        <Box display="flex" justifyContent="space-between" alignItems="center" >
                            <Breadcrumbs separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
                                <Box
                                    style={{ cursor: "pointer" }}
                                    onClick={() => {
                                        sessionStorage.removeItem('CurrentProduct')
                                        sessionStorage.removeItem('Services')
                                        sessionStorage.removeItem('CurrentService')
                                        DataList.ActiveService = {}
                                        DataList.ActiveProduct = {}
                                        DataList.ServiceList = []
                                        window.location.href = window.location.origin + "/#/app/products"
                                    }}
                                >
                                    <Typography className={`${typographyClasses.text} ${'text-design'} ${typographyClasses.bold}`}>Products</Typography>
                                </Box>
                                <Typography className={typographyClasses.smallText}>{match.params.productid}</Typography>
                                <Typography className={typographyClasses.smallText}>Services</Typography>
                            </Breadcrumbs>
                        </Box>
                        <Box display="flex" alignItems="center">
                            <Box mr={1} display="flex">
                                <Button
                                    variant="text"
                                    color="primary"
                                    onClick={handleActions.bind(this, { "Action": "refresh" })}
                                >
                                    <RefreshIcon className={typographyClasses.text} />
                                </Button>
                            </Box>
                            <Box display="flex">
                                <Box>
                                    <Button
                                        className="trigger-btn"
                                        variant="contained"
                                        color="primary"
                                        onClick={() => setNewServ(true)}
                                        disabled={!selectTemplate}
                                    >
                                        <AddIcon className={typographyClasses.text} />&nbsp;&nbsp;Create Service
                                    </Button>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                    <Box mb={2} display="flex">
                        <Typography className={`${typographyClasses.h6} ${typographyClasses.bold}`}>Choose a template to create service</Typography>
                    </Box>
                    <Box display="flex" flexWrap="wrap" justifyContent="flexStart"  >
                        {
                            serviceList.length > 0 ?
                                <ProcessServiceTemplateList />
                                :
                                <Box>
                                    {
                                        loadTimeout === false ?
                                            <Loader type="block" />
                                            :
                                            <Typography className={`${typographyClasses.bold} ${typographyClasses.text}`} align="center">No Templates</Typography>

                                    }
                                </Box>
                        }
                    </Box>
                </Box>
            </Container>
            <Footer page="app" />
            {
                newServ === true ?
                    <AddService Handler={CancelAddService} />
                    :
                    null
            }
        </div>
    );
}

export default ServicesList;